import React, {useState} from 'react'
import styled from 'styled-components'
import ScrollToTop from '../../components/Signin/ScrollToTop';
import Footer from '../../components/Footer/Footer'
import NavbarAuth from '../../candidats/components/NavbarAuth';
import SidebarAuth from '../../components/NavbarHome/SidebarAuth';
import RegisterForm from '../components/Auth/RegisterForm';
import {withRouter} from 'react-router-dom'





const HeadSection = styled.div`
   position:fixed;  
   width:100%;
   z-index:10;
   background-color:#fff;
   box-shadow: 0 6px 20px rgba(10, 10, 10, 0.1);

`

const PageContent = styled.div`   
   padding-top: 100px;


   @media screen and (max-width: 768px){
       margin-left:30px;
    }
  
`



const RegisterR = () => {
    const [isOpen, setIsOpen] = useState(false)

    const toggle = () =>{
        setIsOpen(!isOpen)
    }
    return (
        <>
         <ScrollToTop/>
         <HeadSection>
                <SidebarAuth   isOpen={isOpen}  toggle={toggle}/>
                <NavbarAuth toggle={toggle}/>
         </HeadSection>

         <PageContent>
                <RegisterForm/>
         </PageContent>

         <Footer/>
        </>
    )
}

export default withRouter(RegisterR)
