import React,{useContext,useEffect,useState} from 'react'
import {
    SidebarContainer, 
    Icon, 
    CloseIcon,
    SidebarWrapper,
    SidebarMenu,
    SidebarLink,
    SideBtnWrap,
    SidebarRoute,
    SidebarBtn
} from './SidebarElements'
import { AuthContext } from '../../firebase/AuthContext';
import app from '../../firebase/base'
import {useHistory} from 'react-router-dom'


const SidebarAuth = ({isOpen,toggle}) => {

    const {currentUser, logout} = useContext(AuthContext);
    const [role, setRole] = useState("");
    const db = app.firestore();
    var user;
    const navigate = useHistory();

   

     
   
    const getRole = async () => {
        if(currentUser){ 
         await db.collection("utilisateurs")
          .where("id_utilisateur", "==", currentUser.uid)
          .onSnapshot((querySnapshot) => {
              querySnapshot.forEach((doc) => {
              user = doc.data().role_utilisateur;
              });
             setRole(user);  
          });        
          }else{
              console.log("Nok")
          }
      } 
  
  

        useEffect(() => { 
            getRole();
        }, [])

        const getRounting = () =>{

                if(role == "candidat"){
                    navigate.push('/candidats.accueil')
                }else{
                    navigate.push('/recruteur/dashboard')
                }
        }
    return (
        <>
            <SidebarContainer isOpen={isOpen}  onClick={toggle} >
                <Icon onClick={toggle}>
                    <CloseIcon />
                </Icon>
                <SidebarWrapper>
                    <SidebarMenu>
                        <SidebarLink to="/" onClick={toggle}>Accueil</SidebarLink>
                        <SidebarLink to="/candidats" onClick={toggle}>Accès candidats</SidebarLink>
                        <SidebarLink to="/recruteur" onClick={toggle}>Accès recruteurs</SidebarLink>
                        <SidebarLink to="/candidats/emplois"   onClick={toggle}>Offres d'emploi</SidebarLink>
                    </SidebarMenu>
                    <SideBtnWrap> 
                    {!currentUser &&
                           (   
                        <SidebarRoute to="/connexion" onClick={toggle} >Se connecter</SidebarRoute>
                           )
                    }

                    {currentUser &&
                           ( 
                        <SidebarBtn  onClick={logout} >Déconnexion</SidebarBtn>
                        )
                    }
                    {currentUser &&
                            ( 
                             <SidebarBtn  onClick={() => {getRounting()}}>Mon Compte</SidebarBtn>
                        )
                     }
                    {currentUser && role == "admin" &&
                            ( 
                             <SidebarRoute to="" onClick={toggle} >Admin</SidebarRoute>
                        )
                     }
                    </SideBtnWrap>
                </SidebarWrapper>
            </SidebarContainer>
        </>
    )
}

export default SidebarAuth
