import React, {useEffect,useState }  from 'react'
import Controls from '../controls/Controls'
import { Form, useForm } from '../useForm'
import { Grid,Typography } from '@material-ui/core'
import app from '../../../firebase/base'


const initialValues = {
    id: '',
    titre_offre: '',
    nom_candidat: '',
    prenom_candidat: '',
    email_candidat: '',
    telephone_candidat: '',
    cv_candidat: '',
    lettre_motivation:'',
    date_candidature: '',
    situation_candidat:'',
    niveau_etude:'',
    id_offre:''
}



const CandidatureForm = (props) => {

    const {recordForEdit} = props
    const db = app.firestore()
    const [offre, setOffre] = useState([])
    var tabOffre = []

   
 
    const {
        values,
        setValues,
    } =useForm(initialValues, true);

   
   const getLette = async () =>{
            var dbRef =  await db.collection("lettre_motivation")
            dbRef.where("id_offre", "==", props.idOffre) 
            dbRef.where("id_candidat", "==", props.idCandidat) 
            .onSnapshot((querySnapshot) => {
                var list = []
                querySnapshot.forEach((doc) => {
                list.push({...doc.data(), id: doc.id })
                });
                setOffre(list)           
            });
            console.log(offre)
    }



    useEffect(() => {
        getLette();
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
         
    }, [recordForEdit])

    return (
        <Form >
                <Grid container spacing={2}>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="titre_offre"
                              label="Titre Offre"
                              value={values.titre_offre}
                              
                        />
                         <Controls.Input
                              name="nom_candidat"
                              label="nom_candidat"
                              value={values.nom_candidat}
                            
                        />
                         <Controls.Input
                              name="prenom_candidat"
                              label="prenom_candidat"
                              value={values.prenom_candidat}
                            
                        />
                         <Controls.Input
                              name="situation_candidat"
                              label="situation_candidat"
                              value={values.situation_candidat}
                                
                        />

                     {offre.map(item =>  (<Typography key={item.id}>
                                             <a  href={item.lettre_motivation}
                                                 target="_blank" rel="noopener noreferrer">
                                                 Télécharger la lettre de motivation
                                            </a>
                                         </Typography> ))}

                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="email_candidat"
                              label="email_candidat"
                              type="email"
                              value={values.email_candidat}
                              
                        />
                          <Controls.Input
                              name="telephone_candidat"
                               label="telephone_candidat"
                              value={values.telephone_candidat}
                             
                        />
                         <Controls.Input
                              name="date_canditature"
                              label="date_canditature"
                              value={values.date_candidature} 
                        />
                         <Controls.Input
                              name="niveau_etude"
                              label="niveau_etude"
                              value={values.niveau_etude}
                                
                        />
                         <a href={values.cv_candidat} target="_blank" rel="noopener noreferrer">Télécharger le CV</a>
                    </Grid>
                  
                   
                   
                </Grid>
        </Form>
    )
}

export default CandidatureForm
