import React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/core/styles';
import{ Link } from 'react-router-dom';





const useStyles = makeStyles((theme) => ({
    
  buttonStyle:{
          
          color: "#fff",

          "&:hover":{
              color: "#fbf000",
          }
      },
     linkStyle:{
          
        color: "#000",
        textDecoration:"none"
    }
}));



export default function SimpleMenu() {

  const [anchorEl, setAnchorEl] = React.useState(null);
  const classes = useStyles();

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div>
      <Button className={classes.buttonStyle} aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
        CV
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <Link to="/candidats/telecharger-cv" className={classes.linkStyle}> 
            <MenuItem onClick={handleClose}>Técharger un cv</MenuItem>
        </Link>
        <Link to="/candidats/experiences" className={classes.linkStyle}> 
            <MenuItem onClick={handleClose}>Expériences</MenuItem>
        </Link>
        <Link to="/candidats/formations" className={classes.linkStyle}> 
            <MenuItem onClick={handleClose}>Formations</MenuItem>
        </Link>
        <Link to="/candidats/langues" className={classes.linkStyle}> 
           <MenuItem onClick={handleClose}>Langues</MenuItem>
        </Link>
        <Link to="/candidats/langages-informatiques" className={classes.linkStyle}> 
           <MenuItem onClick={handleClose}>Langages informatiques</MenuItem>
        </Link>
        <Link to="/candidats/bibliotheque-framwork" className={classes.linkStyle}> 
          <MenuItem onClick={handleClose}>Bibiothèques Framwork</MenuItem>
        </Link>
        <Link to="/candidats/autres-competences" className={classes.linkStyle}> 
           <MenuItem onClick={handleClose}>Autres compétences</MenuItem>
        </Link>
        <Link to="/candidats/interets" className={classes.linkStyle} > 
           <MenuItem onClick={handleClose}>Intérets</MenuItem>
        </Link>
        <Link to="/candidats/reseaux-sociaux" className={classes.linkStyle} > 
            <MenuItem onClick={handleClose}>Réseaux sociaux</MenuItem>
        </Link> 
      </Menu>
    </div>
  );
}
