import React from 'react'
import { 
    MobileIcon,
        Nav,
        NavBtn,
        NavBtnLink,
        NavContainer,
        NavLogo,
        NavMenu,
        LogoImg
 } from './Elements'
 import {FaBars} from 'react-icons/fa'
import SearcHeader from '../SearchBar/SearcHeader'
import {animateScroll as scroll} from 'react-scroll'
import Img from '../../../assets/logo/new-logo-jobsgabon-1.png'


const NavbarSearch = (props) => {
    const {fetchJobsCustom,toggle,handleChange} = props
    const toggleHome = () => {
        scroll.scrollToTop(); 
    }
  
    return (
        <>
            <Nav>
                <NavContainer>
                    <NavLogo  to="/"  onClick={toggleHome}>
                           <LogoImg src={Img} alt="logo JOBSGABON" />
                    </NavLogo>
                    <MobileIcon onClick={toggle}>
                        <FaBars />
                    </MobileIcon>
                    <NavMenu>
                       <SearcHeader  handleChange={handleChange} fetchJobsCustom={fetchJobsCustom} />
                   </NavMenu>
                </NavContainer>
                <NavBtn>
                    <NavBtnLink to="/recruteur">
                        Accès recruteur
                    </NavBtnLink>
                </NavBtn>
            </Nav>
        </>
    ) 
}

export default NavbarSearch
