import React,{useContext,useEffect,useState}  from 'react'
import {
    SidebarContainer, 
    Icon, 
    CloseIcon,
    SidebarWrapper,
    SidebarMenu,
    SidebarLink,
    SideBtnWrap,
    SidebarRoute,
    SidebarLogout
} from './SidebarElements'
import { AuthContext } from '../../../firebase/AuthContext';
import app from '../../../firebase/base'

const SidebarHome = ({isOpen,toggle}) => {

    const {currentUser, logout} = useContext(AuthContext);
    const [role, setRole] = useState("");
    const db = app.firestore();
    var user;

    const getRole = async () => {
      if(currentUser){ 
       await db.collection("utilisateurs")
        .where("id_utilisateur", "==", currentUser.uid)
        .onSnapshot((querySnapshot) => {
            querySnapshot.forEach((doc) => {
            user = doc.data().role_utilisateur;
            });
           setRole(user);  
        });        
        }else{
            console.log("Aucun utilisateur connecté")
        }
    } 

        useEffect(() => { 
            
                getRole();
            
            
        }, [])

    return (
        <>
            <SidebarContainer isOpen={isOpen}  onClick={toggle} >
                <Icon onClick={toggle}>
                    <CloseIcon />
                </Icon>
                <SidebarWrapper>
                    <SidebarMenu>
                        <SidebarLink to="/" onClick={toggle}>Accueil</SidebarLink>
                        <SidebarLink to="/candidats" onClick={toggle}>Accès candidats</SidebarLink>
                        <SidebarLink to="/candidats/conseils" onClick={toggle}>Conseils et coaching</SidebarLink>
                        <SidebarLink to="/candidats/emplois"   onClick={toggle}>Offres d'emploi</SidebarLink>
                        <SidebarLink to="/candidats/actualites" onClick={toggle}>Actualités</SidebarLink>
                        <SidebarLink to="/recruteur" onClick={toggle}>Accès recruteurs</SidebarLink>
                    { !currentUser &&
                         (
                        <SidebarLink to="/candidats/connexion"  onClick={toggle}>Se connecter</SidebarLink>
                    )}
                    </SidebarMenu>
                    <SideBtnWrap> 
                    {currentUser && role == "candidat" &&
                         (
                           <SidebarRoute to="/candidats/accueil">Mon compte</SidebarRoute>
                         )
                     }
                         <SidebarRoute to="/candidats/signup">Créer un compte</SidebarRoute>

                    { currentUser &&
                     (
                          <SidebarLogout onClick={logout}  >Déconnexion</SidebarLogout>
                     )
                    }
                    </SideBtnWrap>
                </SidebarWrapper>
            </SidebarContainer>
        </>
    )
} 

export default SidebarHome
