import React, {useState,useEffect} from 'react'
import styled from 'styled-components'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import Footer from '../../components/Footer/Footer'
import PubLaterale from '../components/Publicite'
import Sidebar from '../components/Publicite/Sidebar'
import { Container, Grid,makeStyles, Paper,Box,Typography} from '@material-ui/core'
import PostulerForm from '../components/Job/PostulerForm'
import app from '../../firebase/base';
import BreadCrumbs from '../components/Breadcrumbs'

   

const HeadSection = styled.div`
   position:fixed;
   width:100%;
   z-index:10;
   background-color:#fff;
   box-shadow: 0 6px 20px rgba(10, 10, 10, 0.1);

`

const PageContent = styled.div`   
   padding-top: 185px;
   margin-left:70px;
   margin-bottom: 20px;

   @media screen and (max-width: 768px){
       margin-left:30px;
    }
  
`

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 2,
     
    },
    rootInfo: {
        backgroundColor: theme.palette.background.dark,
        
        paddingBottom: theme.spacing(2),
        paddingTop: theme.spacing(1)
      },
  }));


 

const Postuler = (props) => {

    const classes = useStyles();
    const [isOpen, setIsOpen] = useState(false)
    const db = app.firestore();
    const [recruteur, setRecruteur] = useState([])
    const toggle = () =>{
        setIsOpen(!isOpen)
    }

    const getRecruteur = async () => {
        db.collection("recruteurs")
          .where("id_utilisateur", "==", props.location.state.id_recruteur)
          .onSnapshot((querySnapshot) => {
              var list = [];
              querySnapshot.forEach((doc) => {
                  list.push({ ...doc.data(), id: doc.id })
              });
              setRecruteur(list); 
            
          });
     } 

    

    useEffect(()=> {
        console.log(props.location.state)
        getRecruteur();
        
      }, [])
    return (
        <>
            <ScrollToTop/>
            <HeadSection>
                <Sidebar  isOpen={isOpen}  toggle={toggle}/>
                <PubLaterale   toggle={toggle}/>
                <BreadCrumbs/>
            </HeadSection>
 
            <PageContent className={classes.root}>
                    <Container>
                         <Grid container spacing="3" >
                               
                                <Grid item  md={6} xs={12}>
                                        <Paper className={classes.rootInfo}>
                                            <Box
                                                display="flex"
                                                flexDirection="column"
                                                height="100%"
                                                justifyContent="center"
                                            >
                                                 <Container maxWidth="sm">
                                                 <Box mb={3}>
                                                    <Typography
                                                        color="textPrimary"
                                                        variant="h4"
                                                    >
                                                    Informations de l'offre
                                                    </Typography>
                                                        <Grid container ={1}>
                                                           <Grid item xs={12}>
                                                             {recruteur.map(item =>  <Typography variant="body2" key={item.id}>Entreprise: {item.nom_recruteur}</Typography>  )}
                                                               <Typography variant="body2">Titre: {props.location.state.titre_offre}</Typography>
                                                               <Typography variant="body2">Secteur d'activité : {props.location.state.secteur_activite}</Typography>
                                                               <Typography variant="body2">Type de contrat: {props.location.state.type_contrat}</Typography>
                                                               <Typography variant="body2">Niveau d'étude: {props.location.state.niveau_etude}</Typography>
                                                               <Typography variant="body2">Année d'expérience: {props.location.state.niveau_requis}</Typography>
                                                               <Typography variant="body2">Localisation du poste: {props.location.state.localisation}</Typography>
                                                           </Grid> 
                                                           <Grid item xs={12}>
                                                              <Typography align="justify" >{props.location.state.description_offre}</Typography>
                                                           </Grid> 
                                                        </Grid>
                                                    </Box>
                                                 </Container>
                                            </Box>
                                        </Paper>
                                </Grid>
                                <Grid item  md={6} xs={12}>
                                     <PostulerForm
                                        idOffre={props.location.state.id_offre}
                                        idRecruteur={props.location.state.id_recruteur}
                                        titreOffre={props.location.state.titre_offre}
                                        typeContrat={props.location.state.type_contrat}
                                        datePub={props.location.state.date_pub}
                                     />
                                </Grid>

                         </Grid>
                          
                    </Container>
            </PageContent>
            <Footer/>  
        </>
    )
}
 
export default Postuler
