import React,{useEffect,useContext} from 'react'
import { 
  
        Grid, 
 } from '@material-ui/core'
import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import { AuthContext } from '../../../firebase/AuthContext';
import styled from 'styled-components'
import { format } from 'date-fns'




const BlockCache = styled.div`
 display:none;
`;
 

const FormExp = (props) => {
   
    const {addOrEdit, recordForEdit} = props
    const {currentUser} = useContext(AuthContext);

    const initialValues = {
        id:'',
        titre_experience: '',
        entreprise: '',
        id_utilisateur:currentUser.uid,
        poste: '',
        secteur_activite: '',
        region:'',
        description_experience: '',
        date_debut: format(new Date(),"dd/MM/yyyy"),
        date_fin:  format(new Date(),"dd/MM/yyyy") 
       
    }

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('nom' in fieldValues)
            temp.nom = fieldValues.nom ? "" : "Champ requis."
        if ('id_utilisateur' in fieldValues)
           temp.id_utilisateur = fieldValues.id_utilisateur ? "" : "Champ requis."
        if ('entreprise' in fieldValues)
           temp.entreprise = fieldValues.entreprise ? "" : "Champ requis."
        if ('poste' in fieldValues)
            temp.poste = fieldValues.poste ? "" : "Champ requis."
        if ('secteur_activite' in fieldValues)
            temp.secteur_activite = fieldValues.secteur_activite ? "" : "Champ requis."
        if ('region' in fieldValues)
            temp.region = fieldValues.region ? "" : "Champ requis."
        if ('description_experience' in fieldValues)
            temp.description_experience = fieldValues.description_experience ? "" : "Champ requis."
        
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    }= useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit,setValues])


    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >
           
                    <Grid
                        container  
                    >
                       <Grid item md={6} xs={12}>
                              <Controls.Input
                                    name="nom"
                                    label="nom_expérience"
                                    value={values.nom}
                                    onChange={handleInputChange}
                                    error={errors.nom}
                                />
                                <Controls.Input
                                    name="entreprise"
                                    label="Structure d'experience"
                                    value={values.entreprise}
                                    onChange={handleInputChange} 
                                    error={errors.entreprise}
                                />
                                <Controls.Input
                                    name="poste"
                                    label="Poste que vous avez occupé"
                                    value={values.poste}
                                    onChange={handleInputChange}
                                    error={errors.poste}
                                />
                                 <BlockCache>
                                 <Controls.Input
                                        name="id_utilisateur"
                                        label="Identifiant"
                                        value={values.id_utilisateur}
                                        onChange={handleInputChange} 
                                        error={errors.id_utilisateur}
                                        
                                /> 
                                </BlockCache>
                                 <Controls.Input
                                    name="secteur_activite"
                                    label="Secteur d'activité"
                                    value={values.secteur_activite}
                                    onChange={handleInputChange}
                                    error={errors.secteur_activite}
                                />
                        </Grid>
    
                        <Grid item md={6} xs={12}>
                                 <Controls.Input
                                    name="region"
                                    label="Ville"
                                    value={values.region}
                                    onChange={handleInputChange}
                                    error={errors.region}
                                />
                            <Controls.DatePicker2
                                    name="date_debut"
                                    label="Date de début"
                                    value={values.date_debut}
                                    onChange={handleInputChange}
                            />
                            <Controls.DatePicker2
                                name="date_fin"
                                label="Date de Fin"
                                value={values.date_fin}
                                onChange={handleInputChange}
                           />
                           <Controls.Input
                                    name="description_experience"
                                    label="Description de votre expérience"
                                    rows="5"
                                    value={values.description_experience}
                                    onChange={handleInputChange}
                                    error={errors.description_experience}
                                />
                        </Grid>
                       
                            <div>
                                    <Controls.Button
                                        type="submit"
                                        text="Valider"
                                     />
                                    <Controls.Button
                                        text="Effacer"
                                        color="default"
                                        onClick={resetForm}
                                     />
                            </div>
                    </Grid>
                
        </Form>
    )
}

export default FormExp
