import React,{useState,useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';
import { Avatar, Box, Button } from '@material-ui/core';
import app from '../../../firebase/base';
import {
        differenceInMinutes,
        differenceInHours,
        differenceInWeeks,
        differenceInSeconds,
        differenceInYears
} from 'date-fns'
import differenceInDays from 'date-fns/esm/differenceInDays/index';
import differenceInCalendarMonths from 'date-fns/esm/differenceInCalendarMonths/index.js';
import { useHistory} from 'react-router-dom';


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    margin: 'auto',
    maxWidth: '100%',
    border: "1px solid #e6e4e4",
        cursor: "pointer",
        transition: ".3s",
        boxShadow: "0px 5px 25px rgba(0, 0, 0, 0.1)",

        "&:hover":{
            borderLeft:"4px solid #1a1359",
            boxShadow: "0px 5px 25px rgba(255, 255, 255, 0.6)",
            backgroundColor: '#e5e6e6',
        }
  },
  image: {
    width: 80,
    height: 80,
    backgroundColor: '#008647',
    borderRadius: "50px"
  },
  textPrimary:{
    color: '#008647',
    margin: theme.spacing(0.3),
    padding: theme.spacing(0.40),
    width:'320px'
  },
  textSecondary:{
    fontWeight: 'bold',
    margin: theme.spacing(0.3),
    padding: theme.spacing(0.40),
  },
  textCompany:{
    margin: theme.spacing(0.3),
    padding: theme.spacing(0.40),
  },

  avatar: {
    backgroundColor: '#008647',
    fontSize: '12px'
  },
  skillGreen:{
    margin: theme.spacing(0.3),
    padding: theme.spacing(0.40),
    fontSize: "10.5px",
    fontWeight: 600,
    justifyContent:"center",
    textAlign: "center",
    backgroundColor: "#008647",
    color: "#fff",
    width: "100px",
    borderRadius: "20px",
},
  skillChip:{
    margin: theme.spacing(0.3),
    padding: theme.spacing(0.40),
    fontSize: "10.5px",
    fontWeight: 600,
    justifyContent:"center",
    textAlign: "center",
    backgroundColor: "#fbf000",
    borderRadius: "20px",
    color: "#1a1359",
    width: "100px",

},
buttonStyle:{
        backgroundColor: "#1a1359",
        color: "#fff",
        fontSize: "10.5px",
        fontWeight: 600,
        borderRadius:'Opx',

        "&:hover":{
            backgroundColor: "#1a1359",
        }
    }
}));

export default function Job(props) {


  const classes = useStyles();
  const db = app.firestore()
  const [recruteur, setRecruteur] = useState([])
  const navigate = useHistory();





  const convertDate = () =>{
    const annees = differenceInYears(Date.now(), props.postedOn);
    const mois = differenceInCalendarMonths(Date.now(), props.postedOn);
    const semaines = differenceInWeeks(Date.now(), props.postedOn);
    const jours = differenceInDays(Date.now(), props.postedOn);
    const heures = differenceInHours(Date.now(), props.postedOn);
    const minutes = differenceInMinutes(Date.now(), props.postedOn);
    const secondes = differenceInSeconds(Date.now(), props.postedOn);
     if(secondes < 60) {
       return (   <Typography className={classes.textSecondary} variant="body2" color="textSecondary">
                        il y'a {secondes} s |
                 </Typography>)
     }
     if(minutes < 60){
          return (   <Typography className={classes.textSecondary} variant="body2" color="textSecondary">
                         il y'a {minutes} min |
          </Typography>)
     }

     if(heures < 24){
        return (   <Typography className={classes.textSecondary} variant="body2" color="textSecondary">
                      il y'a {heures} h |
        </Typography>)
      }

      if(jours < 7){
        return (   <Typography className={classes.textSecondary} variant="body2" color="textSecondary">
                       il y'a {jours} jour(s) |
        </Typography>)
      }

      if(semaines < 4){
        return (   <Typography className={classes.textSecondary} variant="body2" color="textSecondary">
                       il y'a {semaines} semaine(s) |
        </Typography>)
      }

      if(mois < 12){
        return (   <Typography className={classes.textSecondary} variant="body2" color="textSecondary">
                       il y'a {mois} mois |
        </Typography>)
      }

      if(annees != 0){
        return (   <Typography className={classes.textSecondary} variant="body2" color="textSecondary">
                       il y'a {annees} an(s) |
        </Typography>)
      }

  }


  const getRecruteur = async () => {

    await db.collection("recruteurs")
       .where("id_utilisateur", "==", props.id_utilisateur)
       .onSnapshot((querySnapshot) => {
           var list = []
           querySnapshot.forEach((doc) => {
            list.push({...doc.data(), id: doc.id })

           });
           setRecruteur(list)
       });

    }


  useEffect(()=> {
    getRecruteur();
}, [])

    const goToOffre = () => {

      navigate.push({pathname:"/candidats/offre/", state:{
                id_offre:props.id,
                id_recruteur:props.id_utilisateur,
                date_pub: props.date_debut,
                titre_offre:props.titre_offre,
                type_contrat: props.type_contrat,
                localisation: props.ville_offre,
                niveau_requis:props.experience,
                niveau_etude: props.niveau_etude,
                secteur_activite: props.secteur_activite,
                description_offre: props.description_offre,
                responsabilite_offre: props.responsabilite_offre
      } })
    }

  return (
    <div className={classes.root}>
      <Paper className={classes.paper}>
        <Grid container spacing={1}  mb="10">
          <Grid item>
            <ButtonBase className={classes.image}>
              {
              recruteur.map((item,index) =>
                <Avatar key={index} src={item.image_url} aria-label="recipe" className={classes.avatar}> </Avatar>
                )
              }
            </ButtonBase>
          </Grid>
          <Grid  xs={12} sm container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid item xs>

                <Grid item container xs >
                   <Typography className={classes.textPrimary} variant="body3" >
                           {props.titre_offre}
                    </Typography>
                    <Typography className={classes.skillChip} variant="body2" color="textSecondary">
                          {props.type_contrat.toUpperCase()}
                    </Typography>

                </Grid>
                <Typography className={classes.textCompany} variant="body2" gutterBottom>
                        {props.societe}
                </Typography>
                <Grid item container xs>
                     {convertDate()}
                    <Typography className={classes.textSecondary} variant="body2" color="textSecondary">
                        {props.ville_offre} |
                   </Typography>
                   <Typography className={classes.textSecondary} variant="body2" color="textSecondary">
                        {props.secteur_activite}
                   </Typography>
                </Grid>

              </Grid>

            </Grid>
            <Grid item alignItems="flex-end">
               <Box mt="2">
                   <Button  className={classes.buttonStyle} onClick={goToOffre} variant="contained" >consulter</Button>
               </Box>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
}
