import React,{useEffect} from 'react'
import { 
        Box,
        Card, 
        CardContent, 
        CardHeader, 
        Divider, 
        Grid, 
 } from '@material-ui/core'
import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import {getVilleCollection,getProvinceCollection,getYesOrNo} from '../../data/collection'




 const initialValues = {
    id: '',
    nom_recruteur: '',
    secteur_activite:'',
    telephone_recruteur: '',
    email_recruteur: '',
    referent_recruteur: '',
    raison_sociale:'',
    ville_recruteur:'',
    province_recruteur: '',
    adresse_recruteur: '',
    url_recruteur:'',
    description_recruteur: '',
    vedette: '',
    cvthequeplus: '',
    cvthequesimple:''
}

const RecruteurForm = (props) => {

    const {addOrEdit, recordForEdit} = props

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('nom_recruteur' in fieldValues)
            temp.nom_recruteur = fieldValues.nom_recruteur ? "" : "Champ requis."
        if ('secteur_activite' in fieldValues)
            temp.secteur_activite = fieldValues.secteur_activite ? "" : "Champ requis."
        if ('email_recruteur' in fieldValues)
            temp.email_recruteur = (/$^|.+@.+..+/).test(fieldValues.email_recruteur) ? "" : "Email non valide."
        if ('telephone_recruteur' in fieldValues)
            temp.telephone_recruteur = fieldValues.telephone_recruteur.length > 8 ? "" : "Minimum de 9 nombre requis."
        if ('referent_recruteur' in fieldValues)
            temp.referent_recruteur = fieldValues.referent_recruteur.length !== 0 ? "" : "Champ requis."
        if ('raison_sociale' in fieldValues)
            temp.raison_sociale = fieldValues.raison_sociale.length !== 0 ? "" : "Champ requis."
        if ('adresse_recruteur' in fieldValues)
            temp.adresse_recruteur = fieldValues.adresse_recruteur.length !== 0 ? "" : "Champ requis."
        if ('url_recruteur' in fieldValues)
            temp.url_recruteur = fieldValues.url_recruteur.length !== 0 ? "" : "Champ requis."
        if ('description_recruteur' in fieldValues)
            temp.description_recruteur = fieldValues.description_recruteur.length !== 0 ? "" : "Champ requis."
        if ('province_recruteur' in fieldValues)
            temp.province_recruteur = fieldValues.province_recruteur.length !== 0 ? "" : "Champ requis."
        if ('vedette' in fieldValues)
            temp.vedette = fieldValues.vedette.length !== 0 ? "" : "Champ requis."
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }
    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    }= useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit])

    return (
        <Form
        autoComplete="off"
        onSubmit={handleSubmit}
      >
          <Card>
              <CardHeader
                  subheader="Vous pouvez éditer vos informations"
                  title="Profil"
              />
              <Divider/>
              <CardContent>
                  <Grid
                      container
                      spacing={3}
                  >
                      <Grid item md={6} xs={12}>
                              <Controls.Input
                                  name="nom_recruteur"
                                  label="nom_recruteur"
                                  value={values.nom_recruteur}
                                  onChange={handleInputChange}
                                  error={errors.nom_recruteur}
                                  disabled
                              />
                                <Controls.Input
                                  name="secteur_activite"
                                  label="secteur_activite"
                                  value={values.secteur_activite}
                                  onChange={handleInputChange}
                                  error={errors.secteur_activite}
                                  disabled
                              />
                               <Controls.Input
                                  name="telephone_recruteur"
                                  label="telephone_recruteur"
                                  value={values.telephone_recruteur}
                                  onChange={handleInputChange}
                                  error={errors.telephone_recruteur}
                                  disabled
                              />
                              <Controls.Input
                                  name="email_recruteur"
                                  label="email_recruteur"
                                  value={values.email_recruteur}
                                  onChange={handleInputChange}
                                  error={errors.email_recruteur}
                                  disabled
                              />
                              <Controls.Input
                                  name="raison_sociale"
                                  label="raison_sociale"
                                  value={values.raison_sociale}
                                  onChange={handleInputChange}
                                  error={errors.raison_sociale}
                                  disabled
                              />
                               <Box  >
                                <h5>Activer la CVthèque simple ?</h5>
                                <Controls.Select
                                  name="cvthequesimple"
                                  label="CvThèque simple"
                                  value={values.cvthequesimple}
                                  onChange={handleInputChange}
                                  options={getYesOrNo()}
                                  error={errors.cvthequesimple}
                              />
                                </Box>
                                 <Box  >
                                <h5>Activer la CVthèque + ?</h5>
                                <Controls.Select
                                  name="cvthequeplus"
                                  label="CvThèque +"
                                  value={values.cvthequeplus}
                                  onChange={handleInputChange}
                                  options={getYesOrNo()}
                                  error={errors.cvthequeplus}
                              />
                                </Box>
                               
                      </Grid>
                      
                     
                      <Grid item md={6} xs={12}>
                            <Controls.Input
                                  name="referent_recruteur"
                                  label="referent_recruteur"
                                  value={values.referent_recruteur}
                                  onChange={handleInputChange}
                                  error={errors.referent_recruteur}
                                  disabled
                              />
                             <Controls.Input
                                  name="url_recruteur"
                                  label="url_recruteur"
                                  value={values.url_recruteur}
                                  onChange={handleInputChange}
                                  error={errors.url_recruteur}
                                  disabled
                              />
                              <Controls.Select
                                  name="ville_recruteur"
                                  label="ville_recruteur"
                                  value={values.ville_recruteur}
                                  options={getVilleCollection()}
                                  onChange={handleInputChange}
                                  error={errors.ville_recruteur}
                                  disabled
                              />
                               <Controls.Input
                                  name="telephone_recruteur"
                                  label="telephone_recruteur"
                                  value={values.telephone_recruteur}
                                  onChange={handleInputChange}
                                  error={errors.telephone_recruteur}
                                  disabled
                              />
                               <Controls.Select
                                  name="province_recruteur"
                                  label="province_recruteur"
                                  value={values.province_recruteur}
                                  onChange={handleInputChange}
                                  options={getProvinceCollection()}
                                  error={errors.province_recruteur}
                                  disabled
                              /> 
                                
                                <Box  >
                                <h5>Mettre en vedette</h5>
                                <Controls.Select
                                  name="vedette"
                                  label="mettre en vedette ?"
                                  value={values.vedette}
                                  onChange={handleInputChange}
                                  options={getYesOrNo()}
                                  error={errors.vedette}
                              />
                                </Box>
                               
                              
                      </Grid>
                    

                     
                      
                      <Grid item md={12} xs={12}>
                              <Controls.Input
                                  name="description_recruteur"
                                  label="description_recruteur"
                                  value={values.description_recruteur}
                                  onChange={handleInputChange}
                                  error={errors.description_recruteur}
                                  rows="7"
                                  multiline="multiline"
                                  disabled
                              />
                      </Grid>
                     
                  </Grid>
              </CardContent>
              <Divider />
              <Box
                  display="flex"
                  justifyContent="flex-end"
                  p={2}
              >
                 <Controls.Button
                          type="submit"
                          text="Valider" />
                      <Controls.Button
                          text="Effacer"
                          color="default"
                onClick={resetForm} />
              </Box>
          </Card>
      </Form>
    )
}

export default RecruteurForm
