import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import {Button} from '../Button';
import img from '../../assets/images/jobgabon2.jpeg';
import './Navbar.css';

function Navbarnav() {
    const [click, setClick] = useState(false);
    const [button, setButton] = useState(true)

    const handleClick = () => setClick(!click);
    const closeMobileMenu = () => setClick(false)

    const showButton = () => {
        if(window.innerWidth <= 960){
            setButton(false)
        }else{
            setButton(true)
        }
    };

    useEffect(()=>{
        showButton();
    },[]);

    window.addEventListener('resize', showButton);
    
    return (
        <>
           <nav className="navbar">
               <div className="navbar-container">
                   <Link to="/" className="navbar-logo" onClick={closeMobileMenu} >
                     { /*JOBS-GABON <i className='fab fa-typo3' />*/}
                      <img className="logo" src={img} alt="logo-jobsGabon"/>
                   </Link>
                   <div className="menu-icon" onClick={handleClick} >
                        <i className={click ? 'fas fa-times' : 'fas fa-bars'} />
                   </div>
                   <ul className={click ? 'nav-menu active' : 'nav-menu' }>
                        <li className='nav-item' >
                            <Link to='/emplois' className='nav-links' onClick={closeMobileMenu} >
                                 Offres d'emploi
                            </Link>
                        </li>
                        <li className='nav-item' >
                            <Link to='/deposercv' className='nav-links' onClick={closeMobileMenu} >
                                 Deposez un CV
                            </Link>
                        </li>
                        <li className='nav-item' >
                            <Link to='/recruteur' className='nav-links' onClick={closeMobileMenu} >
                                Espace recruteur
                            </Link>
                        </li>
                        <li className='nav-item' >
                            <Link to='/connexion' className='nav-links' onClick={closeMobileMenu} >
                                 Se connecter
                            </Link>
                        </li>
                        <li className='nav-item' >
                            <Link to='./inscription' className='nav-links-mobile' onClick={closeMobileMenu} >
                                  S'inscrire
                            </Link>
                        </li>
                   </ul>
                   {button && <Link to='./inscription'><Button buttonStyle='btn--outline' >S'inscrire</Button></Link> }
               </div>
            </nav>  
        </>
    )
}

export default Navbarnav
