import React,{ useState,useContext,useEffect } from 'react'
import { Grid, Typography, makeStyles, Divider, } from '@material-ui/core'
import { Exp,ExpSan } from './ExpElements'
import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';


const useStyles = makeStyles({
    detail: {
     marginBottom: '20px',
     marginTop: '20px'
    },
   
  });


const Formations = () => {
    const {currentUser} = useContext(AuthContext);
    const db = app.firestore();
    const [formations, setForamtion] = useState([]);
    const classes = useStyles();


    const getFormations = async () => {
        db.collection("formations")
          .where("id_utilisateur", "==", currentUser.uid)
          .onSnapshot((querySnapshot) => {
              var List= []
              querySnapshot.forEach((doc) => {
                List.push({ ...doc.data(), id: doc.id })
              });
              setForamtion(List)              
          });
     }

     useEffect(()=> {
        getFormations();
        
    }, [])

    return (
        <Exp className="experience">
            
        <Grid container spacing="2">

            { 
              formations.map((item,index) => {

                return(
                <Grid   md={12} xs={12}>
                    <Grid key={index} container direction="row"  justify="space-between" className={classes.detail}>
                        <Grid item xs={6}>
                            <Typography variant="body1"><ExpSan>Formation:</ExpSan>  {item.nom}</Typography>
                            <Typography variant="body1"><ExpSan>Etablissement: </ExpSan> {item.etablissement}</Typography>
                            <Typography variant="body1"><ExpSan>Discipline: </ExpSan> {item.discipline} </Typography> 
                            <Typography variant="body1"><ExpSan>Niveau atteint:</ExpSan>  {item.niveau_atteint}</Typography>
                        </Grid>
                        <Grid item xs={6}>
                            <Typography variant="body1"><ExpSan>Ville: </ExpSan> {item.ville_formation}</Typography>
                            <Typography variant="body1"><ExpSan>Province: </ExpSan> {item.province_formation} </Typography> 
                            <Typography variant="body1"><ExpSan>Date début:</ExpSan> {item.date_debut} </Typography>
                            <Typography variant="body1"><ExpSan>Date Fin:</ExpSan> {item.date_fin} </Typography>
                        </Grid>
                       
                    </Grid>
                    <Divider/>
                </Grid> 
                         )  
                     })
                }
        </Grid>
    
    </Exp>
    )
}

export default Formations
