import React,{useState} from 'react'
import Sidebar from '../components/Sidebar/Index'
import '../components/knowleges/knowleges.css'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import {withRouter} from 'react-router-dom'
import MobileSidebar from '../components/Sidebar/MobileSidebar'
import Connaissances from '../components/knowleges/Connaissances'
import styled from 'styled-components'


const KnowSection = styled.div`
 display:flex;
@media screen and (max-width: 850px){
    display:block;
  }

` 

const KnowContent = styled.div`

    margin-left: 10px;
    background-color: #F8F8F8;
    padding: 10px;
    position:relative;
    margin-left:260px;

    @media screen and (max-width: 1125px){
        margin-left: 0 !important;
        margin-left:0 !important;
        margin-top: 50px;
    }

` 


const Knowledges = () => {

    const [isOpen, setIsOpen] = useState(false)
    const toggle = () =>{ 
        setIsOpen(!isOpen)
    }

    return (
        <>
         <ScrollToTop/>
           <KnowSection>
            <MobileSidebar  isOpen={isOpen}  toggle={toggle}/>
            <Sidebar toggle={toggle}/>
               <KnowContent className="knowledgesContent">
                    <Connaissances/>
               </KnowContent>
           </KnowSection>
             
        </>
    )
}

export default withRouter(Knowledges);
