import React,{useState,useEffect,useContext} from 'react'
import {  Contactless } from '@material-ui/icons'
import PageHeader from '../PageHeader'
import { makeStyles, TableBody, TableRow, TableCell, Toolbar, Grid } from '@material-ui/core';
import useTable from "../useTable";
import Controls from "../controls/Controls";
import AddIcon from '@material-ui/icons/Add';
import Popup from "../Popup";
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';
import CloseIcon from '@material-ui/icons/Close';
import Notification from "../Notification";
import ConfirmDialog from "../ConfirmDialog";
import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';
import '../Candidatures/Candidatures.css'
import FormReseaux from './FormReseaux';

const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '75%'
    },
    newButton: {
        position: 'absolute',
        right: '10px'
    }
}))

const headCells = [
    { id: 'facebook', label: 'facebook' },
    { id: 'twitter', label: 'twitter' },
    { id: 'instagram', label: 'instagram' },
    { id: 'linkedin', label: 'linkedin' },
    { id: 'github', label: "github" },
    { id: 'actions', label: 'Actions', disableSorting: true }
]



const TableReseaux = () => {

  
    const classes = useStyles();
    const [openPopup, setOpenPopup] = useState(false)
    const [recordForEdit, setRecordForEdit] = useState(null)
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })
    const [records, setRecords] = useState([]);
    const {currentUser} = useContext(AuthContext);
    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const db = app.firestore();
    const listReseau = [];
   
    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records, headCells, filterFn);
   
   

      const getReseaux = async () => {
          db.collection("reseaux_sociaux")
            .where("id_utilisateur", "==", currentUser.uid)
            .onSnapshot((querySnapshot) => {
               
                querySnapshot.forEach((doc) => {
                    listReseau.push({ ...doc.data(), idDoc: doc.id })
               
                });
                setRecords(listReseau)              
            });
       }

       useEffect(()=> {
        getReseaux();
    }, [])

       const addOrEdit = async (obj,resetForm) => {
        try {

            if (obj.id == "") {
                await db.collection("reseaux_sociaux").doc().set(obj);
               }else{
                await db.collection("reseaux_sociaux").doc(obj.id).update(obj);
                
             }

            resetForm()
            setRecordForEdit(null)
            setOpenPopup(false)
            getReseaux();
            setNotify({
                isOpen: true,
                message: 'Validation avec Succes',
                type: 'success'
            })

        } catch (error) {
            console.error(error);
          }
       }

    
      const onDelete = async (id)=> {
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        })
        await db.collection("reseaux_sociaux").doc(id).delete();
        getReseaux();
        setNotify({
            isOpen: true,
            message: 'Suppression avec Success',
            type: 'error'
        })
    }

      

    
       const openInPopup = item => {
            setRecordForEdit(item)
            setOpenPopup(true)
      }

    return (
        <>
           <PageHeader   
            title="Rubrique reseaux sociaux"
            subTitle="Ajouter, modifier et supprimer un reseau social dans à votre CV"
            icon={<Contactless fontSize="large"/>}
           /> 
           <Toolbar>
                <Controls.Button
                        text="Ajouter"
                        variant="outlined"
                        startIcon={<AddIcon />}
                        className={classes.newButton}
                        onClick={() => { setOpenPopup(true); setRecordForEdit(null); }}
                />
           </Toolbar>
           <Grid className="container">
           <TblContainer>
                <TblHead />
                 <TableBody>
                        {
                            recordsAfterPagingAndSorting().map((item,index )=>
                                (<TableRow key={index}>
                                    <TableCell>{item.facebook}</TableCell>
                                    <TableCell>{item.twitter}</TableCell>
                                    <TableCell>{item.instagram}</TableCell>
                                    <TableCell>{item.linkedin}</TableCell>
                                    <TableCell>{item.github}</TableCell>
                                    <TableCell>
                                        <Controls.ActionButton
                                            color="primary"
                                            onClick={() => {openInPopup(item)}} 
                                             >
                                            <EditOutlinedIcon fontSize="small" />
                                        </Controls.ActionButton>
                                        <Controls.ActionButton
                                            color="secondary"
                                            onClick={() => {
                                                setConfirmDialog({
                                                    isOpen: true,
                                                    title: 'êtes-vous sûr de vouloir supprimer cet enrégistrement?',
                                                    subTitle: "Vous pouvez annuler cette opération",
                                                    onConfirm: () => {onDelete(item.id) }
                                                })
                                            }}
                                           >
                                            <CloseIcon fontSize="small" />
                                        </Controls.ActionButton>
                                    </TableCell>
                                </TableRow>)
                            )
                        }
                    </TableBody>
                </TblContainer>
                <TblPagination />
                </Grid>
                <Popup
                    title="Formulaire reseaux_sociaux"
                    openPopup={openPopup}
                    setOpenPopup={setOpenPopup}
                >
                    <FormReseaux  
                         recordForEdit={recordForEdit}
                         addOrEdit={addOrEdit}
                    />
                </Popup>
                <Notification
                    notify={notify}
                    setNotify={setNotify}
                />
                <ConfirmDialog
                    confirmDialog={confirmDialog}
                    setConfirmDialog={setConfirmDialog}
                />
        </>
    )
}

export default TableReseaux
