const Sidebar = () => {

  const db = app.firestore();
  const [logo, setLogo] = useState([]);

  const getProfil = async () => {
    db.collection("jobsgabon")
      .onSnapshot((querySnapshot) => {
          var list = [];
          querySnapshot.forEach((doc) => {
                list.push({ ...doc.data(), id: doc.id })

          });
          setLogo(list); 
        
      });
 } 

  useEffect(() => {
    getProfil();
  }, [])

    return (
        <>
           <Container>
            {
              logo.map((item) =>
               (
                <ProfilContainer key={item.id}>
                  <ProfileImg src={item.logo} />
                  <ProfileName>
                    JOBSGABON
                  </ProfileName>
                </ProfilContainer>

                ))
              }

              <MenuContainer>
                  {items.map((item) => (
                   <ContainerLink to={item.href}  >
                       
                       <TitleLink >
                        <SpanLink>{item.icon}</SpanLink>
                         {item.title}
                       </TitleLink>
                      
                   </ContainerLink>))}
                 
                     <Box display="flex">
                      <ContainerLink>
                            <SimpleMenu/>
                      </ContainerLink>     
                     </Box>
                  
                 
              </MenuContainer>
           </Container>
        </>
    )
}

export default Sidebar
