import React,{useContext,useEffect,useState} from 'react'
import { 
    BottomContainer,
    BottomItem,
    BottomLink, 
    BottomMenu ,
    BottomNav,
    BottomBtn,
    BottomBtnLink,
    LogOut
} from './NavBottomElements'
import { AuthContext } from '../../../firebase/AuthContext';
import app from '../../../firebase/base'

const NavbarBottom = () => {

    const {currentUser, logout} = useContext(AuthContext);
    const [role, setRole] = useState("");
    const db = app.firestore();
    var user;

    
    const getRole = async () => {
        if(currentUser){ 
         await db.collection("utilisateurs")
          .where("id_utilisateur", "==", currentUser.uid)
          .onSnapshot((querySnapshot) => {
              querySnapshot.forEach((doc) => {
              user = doc.data().role_utilisateur;
              });
             setRole(user);  
          });        
          }else{
              console.log("Aucun utilisateur connecté")
          }
      } 
  

        useEffect(() => {
            getRole();
        }, [])


    return (
        <>
        <BottomNav>
            <BottomContainer>
                <BottomMenu>
                    <BottomItem>
                        <BottomLink to="/candidats/conseils">
                            Conseils et coaching
                        </BottomLink>
                    </BottomItem>
                    <BottomItem>
                        <BottomLink to="/candidats/emplois"> 
                            Offres d'emploi
                        </BottomLink>
                    </BottomItem>
                    <BottomItem>
                        <BottomLink to="/candidats/actualites"> 
                            Actualités
                        </BottomLink>
                    </BottomItem>
                </BottomMenu>
                <BottomBtn>
                    {!currentUser &&
                           (    
                            <BottomBtnLink  primary='true' whiteYellow="true"  to="/candidats/connexion">Se connecter</BottomBtnLink>
                            )
                        }

                    {currentUser && role == "candidat" &&
                         (
                            <BottomBtnLink  primary='true' whiteYellow="true"  to="/candidats/accueil">Mon compte</BottomBtnLink>
                         )
                     }
                      {!currentUser &&
                           (  
                            <BottomBtnLink  primary='true' whiteYellow="false"  to="/candidats/signup">Créer un compte</BottomBtnLink>
                            )
                        }
                    { currentUser &&
                         (
                            <LogOut onClick={logout}>Déconnexion</LogOut>
                         )
                     }
                </BottomBtn>
            </BottomContainer>
            </BottomNav>
        </>
    )
}

export default NavbarBottom
