import React,{useContext,useEffect} from 'react'
import { 
        Grid, 
 } from '@material-ui/core'

import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import { AuthContext } from '../../../firebase/AuthContext';
import styled from 'styled-components'

const BlockCache = styled.div`
 display:none;
`;




const FormInterets = (props) => {
   
    const {addOrEdit, recordForEdit} = props
    const {currentUser} = useContext(AuthContext);

    const initialValues = {
        id:'',
        interet: '',
        id_utilisateur:currentUser.uid,
    }

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('interet' in fieldValues)
            temp.interet = fieldValues.interet ? "" : "Champ requis."
        if ('id_utilisateur' in fieldValues)
            temp.id_utilisateur = fieldValues.id_utilisateur ? "" : "Champ requis."
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    }= useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit,setValues])


    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >
          
                    <Grid
                        container
                    >
                       <Grid item md={6} xs={6}>
                                <Controls.Input
                                        name="interet"
                                        label="Intérets"
                                        value={values.interet}
                                        onChange={handleInputChange}
                                        error={errors.interet}
                                    />
                                     <BlockCache>
                                 <Controls.Input
                                        name="id_utilisateur"
                                        label="Identifiant"
                                        value={values.id_utilisateur}
                                        onChange={handleInputChange} 
                                        error={errors.id_utilisateur}
                                       
                                /> 
                            </BlockCache>
                        </Grid>
                                 <div>
                                    <Controls.Button 
                                        type="submit"
                                        text="Valider"
                                     />
                                    <Controls.Button
                                        text="Effacer"
                                        color="default"
                                        onClick={resetForm}
                                     />
                                  </div>
                    </Grid>
               
        </Form>
    )
}

export default FormInterets
