import React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/core/styles';
import{ Link } from 'react-router-dom';
import { Apps } from '@material-ui/icons';





const useStyles = makeStyles((theme) => ({
    
  buttonStyle:{
          fontSize: '12px',
          color: "#fbf000",
          left: '-20px',
          width:'150px',
          "&:hover":{
              color: "#fbf000",
          }
      },
      spanStyle:{
        marginRight:'5px',
      },

     linkStyle:{
          
        color: "#000",
        textDecoration:"none"
    }
}));



export default function SimpleMenu() {

  const [anchorEl, setAnchorEl] = React.useState(null);
  const classes = useStyles();

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div>
      <Button className={classes.buttonStyle} aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
          <span className={classes.spanStyle}><Apps/></span>  Recrutements
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <Link to="/admin/dashboard/gestion_recruteurs" className={classes.linkStyle}> 
            <MenuItem onClick={handleClose}>Recruteurs</MenuItem>
        </Link>
        <Link to="/admin/dashboard/gestion_candidats" className={classes.linkStyle}> 
            <MenuItem onClick={handleClose}>Candidats</MenuItem>
        </Link>
        <Link to="/admin/dashboard/gestion_emplois" className={classes.linkStyle}> 
            <MenuItem onClick={handleClose}>Offres d'emploi</MenuItem>
        </Link>
        <Link to="/admin/dashboard/gestion_candidatures" className={classes.linkStyle}> 
           <MenuItem onClick={handleClose}>Candidatures</MenuItem>
        </Link>
        <Link to="/admin/dashboard/gestion_cv" className={classes.linkStyle}> 
           <MenuItem onClick={handleClose}>CvThèque</MenuItem>
        </Link>
        
      </Menu>
    </div>
  );
}
