import React, { useState,useEffect } from 'react';
import { LanguesFramworks } from './LangElements';
import ProgressBarre from './ProgressBarre';
import app from '../../../firebase/base';

const AutresCompetences = (props) => {

   
    const db = app.firestore();
    const [competences, setCompetences] = useState([]);
    const {other} = props

    const getCompetences = async () => {
        db.collection("autre_competences")
          .where("id_utilisateur", "==", other.id_utilisateur)
          .onSnapshot((querySnapshot) => {
              var List = []
              querySnapshot.forEach((doc) => {
                List.push({ ...doc.data(), id: doc.id })
              });
              setCompetences(List)              
          });
     }

    

     useEffect(()=> {
        getCompetences();
    }, [])

    return (

        <LanguesFramworks className="languesFramworks">
                <ProgressBarre
                    data={competences}
                    className="languagesDisplay"  
                />
            </LanguesFramworks>
    )
}

export default AutresCompetences
