import React, { useState,useEffect , useContext}  from 'react'
import { 
     Container,
     Grid, 
     makeStyles, 
     Paper, 
     TableBody, 
     TableRow, 
     TableCell, 
     Toolbar,  
     InputAdornment 

} from '@material-ui/core'
import styled from 'styled-components'
import Sidebar from '../components/Sidebar/Index'
import useTable from "../components/useTable";
import { Search } from "@material-ui/icons";
import Controls from "../components/controls/Controls";
import '../components/Candidatures/Candidatures.css'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import {withRouter} from 'react-router-dom'
import MobileSidebar from '../components/Sidebar/MobileSidebar'
import app from '../../firebase/base'
import {AuthContext} from '../../firebase/AuthContext'







const CandidatureSection = styled.div`
    display:flex;
   
    @media screen and (max-width: 850px){
        display:block;
    } 
`

const CandidatureContent = styled.div`

    margin-left: 10px;
    padding: 5px;
    //background-color: #F8F8F8;
    position:relative;
    width: 80%;
    margin-left:260px;
    

    @media screen and (max-width: 1125px){
        margin-left: 0 !important;
        margin-left:0 !important;
        margin-top: 50px;
    }

`

const headCells = [
    { id: 'societe', label: 'Societe' },
    { id: 'titre_offre', label: "Titre" },
    { id: 'secteur_activite', label: 'Secteur' },
    { id: 'type_contrat', label: 'Type contrat' },
    { id: 'niveau_etude', label: 'Niveau' },
    { id: 'ville_offre', label: 'Ville' },
    { id: 'date_debut', label: 'Début' },
    { id: 'date_fin', label: 'Fin' },
]

const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '75%'
    },
    newButton: {
        position: 'absolute',
        right: '10px'
    }
}))

const MesOffres = () => {

    const classes = useStyles();
    const {currentUser} = useContext(AuthContext);
    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [records, setRecords] = useState([])
    const [candidatures, setCandidatures] = useState([])
    const [isOpen, setIsOpen] = useState(false)
    const db = app.firestore()
    var tabIdOffre = [];
    var tabOffre = [];
   

    const toggle = () =>{
        setIsOpen(!isOpen)
    }


    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records,headCells,filterFn);

    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.entreprise.toLowerCase().includes(target.value))
            }
        })
    }

    
    const getMesOffres = async () => {
        
        await db.collection("candidatures")
           .where("id_candidat", "==", currentUser.uid) 
           .onSnapshot((querySnapshot) => {
               var list = []
               querySnapshot.forEach((doc) => {
                list.push({...doc.data(), id: doc.id })
                tabIdOffre.push(doc.data().id_offre)
               });
               setCandidatures(list) 
               for(var i=0;i<tabIdOffre.length;i++){
                
                var docRef = db.collection("offre_emploi").doc(tabIdOffre[i]);
                docRef.get().then((doc) => {
                    tabOffre.push({...doc.data(), id: doc.id })
                    setRecords(tabOffre) 
               }).catch((error) => {
                   console.log("Error getting document:", error);
               }); 
               
            }
                     
           });   

           
       

        
        }

    useEffect(() => {
        getMesOffres();   
    }, [])

    return (
        <>
        <ScrollToTop/>
        <CandidatureSection>
            <MobileSidebar  isOpen={isOpen}  toggle={toggle}/>
            <Sidebar toggle={toggle}/>
            <CandidatureContent>
             <Container maxWidth="lg">
                    <Grid container>
                            <Grid item  md={12} xs={12}>
                            <Paper className={classes.pageContent}>
                            <Grid item  md={4} xs={12}>
                            <Toolbar>
                                <Controls.Input
                                    label="Rechercher par entreprise"
                                    className={classes.searchInput}
                                    InputProps={{
                                        startAdornment: (<InputAdornment position="start">
                                            <Search />
                                        </InputAdornment>)
                                    }}
                                    onChange={handleSearch}
                                />
                            </Toolbar>
                            </Grid>

                             <Grid className="container">
                            <TblContainer>
                                <TblHead />
                                <TableBody>
                                    {
                                        recordsAfterPagingAndSorting().map(item =>
                                            (<TableRow key={item.id}>
                                                <TableCell>{item.societe}</TableCell>
                                                <TableCell>{item.titre_offre}</TableCell>
                                                <TableCell>{item.secteur_activite}</TableCell>
                                                <TableCell>{item.type_contrat.toUpperCase()}</TableCell>
                                                <TableCell>{item.niveau_etude}</TableCell>
                                                <TableCell>{item.ville_offre}</TableCell>
                                                <TableCell>{item.date_debut}</TableCell>
                                                <TableCell>{item.date_fin}</TableCell>
                                               
                                            </TableRow>)
                                        )
                                    }
                                </TableBody>
                            </TblContainer>
                            <TblPagination />
                            </Grid>
                            </Paper>
                            </Grid>
                    </Grid>
             </Container>
            </CandidatureContent>
        </CandidatureSection>
        </>
    )
}

export default withRouter(MesOffres);
