import React,{useContext,useEffect,useState} from 'react'
import {FaMountain,FaTwitter, FaFacebookF, FaLinkedinIn, FaGithubAlt, FaUser, FaEdit} from 'react-icons/fa'
import{ AiFillContacts,AiFillHome} from 'react-icons/ai'
import {MdLocalOffer} from 'react-icons/md'
import '../../../candidat.css'
import {
    SidebarContainer,
    ImageWrapper,
    ImageContent,
    ImgWrp,
    SidebarH3,
    Navigation,
    NavMenu,
    NavItem,
    NavLink,
    SocialMedia,
    SocialMenu,
    SocialItem,
    SocialLink,
    Signature,
    SignatureP,
    Icon,
    IconS,
    LinkSpan,
    MobileIcon 
} from './SidebarElements'
import { IconContext } from 'react-icons/lib';
import {Box, IconButton,makeStyles, Tooltip } from '@material-ui/core'
import { AuthContext } from '../../../firebase/AuthContext'
import { ExitToApp } from '@material-ui/icons'
import {FaBars} from 'react-icons/fa'
import app from '../../../firebase/base'
import SimpleMenu from '../Menu/SimpleMenu'

const useStyles = makeStyles((theme) => ({
    margin: {
       color: '#fff',
       alignItems: 'center',
       justifyContent: 'center',
       padding: '0 0.2rem'
    },
    logoutText:{ 
        color: '#fff'
    }
   
  }));

const Sidebar = ({toggle}) => {

   
 const {currentUser, logout} = useContext(AuthContext);
 const classes = useStyles();
 const db = app.firestore();

 
 const [images, setImages] = useState([]);

    useEffect(() => {
             db.collection("candidats")
                .where("id_utilisateur", "==", currentUser.uid)
                .onSnapshot((querySnapshot) => {
                    var list = [];
                    querySnapshot.forEach((doc) => {
                        list.push({ ...doc.data(), id: doc.id })
                    });
                    setImages(list) 
                   
                });

                
    }, []);

    return (

       <IconContext.Provider value={{  size: 17 }}>
        <SidebarContainer>
       
        {images.map((item) => (
             <>
            <ImageWrapper>
                <ImageContent key={item.id_utilisateur}>
                    <ImgWrp src={item.image_url} alt="utilisateur"/>
                    <SidebarH3>{item.prenom_candidat} </SidebarH3>
                </ImageContent>
            </ImageWrapper>
         
            <MobileIcon  onClick={toggle} >
                     <FaBars />
            </MobileIcon>

            <Navigation>
                <NavMenu>
                    <NavItem>
                        <NavLink
                         whiteblack={true}
                         to="/candidats"
                        >
                           
                            <LinkSpan> Accueil candidats</LinkSpan>
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink
                         whiteblack={true}
                         to="/candidats/accueil"
                        >
                           <Icon><AiFillHome/></Icon>
                            <LinkSpan> Accueil</LinkSpan>
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink
                         whiteblack={true}
                         to="/candidats/moncompte" 
                        >
                           <Icon><FaUser/></Icon>    
                             <LinkSpan>Mon profil</LinkSpan>
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink
                            to="/candidats/competences"
                            whiteblack={true}
                        >
                          <Icon> <FaMountain/></Icon> 
                             <LinkSpan>Mes Compétences</LinkSpan>
                        </NavLink>
                    </NavItem>
                   {/* <NavItem>
                        <NavLink
                         whiteblack={true}
                         to="/candidats/portefolio"
                        >
                            <Icon><AiFillProject/></Icon>   
                            <LinkSpan>Mes projets </LinkSpan>
                        </NavLink>
                    </NavItem>*/}
                    <NavItem>
                        <NavLink
                         whiteblack={true}
                         to="/candidats/mescandidatures"
                        >
                           <Icon><MdLocalOffer/></Icon>    
                             <LinkSpan>Mes candidatures</LinkSpan>
                        </NavLink>
                    </NavItem>
                    
                    <NavItem>
                        <NavLink
                            whiteblack={true}
                            to="/candidats/contact" 
                        >
                            <Icon><AiFillContacts/></Icon>   
                           <LinkSpan>Mon Contact </LinkSpan>
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <Box ml={1} mt={-2} display="flex" alignItems="center" >
                            <FaEdit  color="white" />
                            <SimpleMenu/> 
                        </Box>
                    </NavItem>
                    <NavItem>
                            { currentUser && 
                              (
                                <Tooltip title="Se déconnecter" placement="right-start">
                                    <IconButton className={classes.margin} onClick={logout}>
                                                <ExitToApp/>
                                            
                                    </IconButton>
                                </Tooltip>
                                
                              )
                            }
                           
                    </NavItem>
                  
                  
                    
                </NavMenu>
            </Navigation>

            <SocialMedia>
                <SocialMenu>
                    <SocialItem>
                        <SocialLink
                            whiteblack={true}
                            href={item.facebook}
                            target="_blank" rel="noopener noreferrer"
                        >
                             <IconS> <FaFacebookF/></IconS> 
                        </SocialLink>
                    </SocialItem>
                    <SocialItem>
                        <SocialLink
                         whiteblack={true}
                         href={item.linkedin}
                         target="_blank" rel="noopener noreferrer"
                        >
                             <IconS><FaLinkedinIn/></IconS>  
                        </SocialLink>
                    </SocialItem>
                    <SocialItem>
                        <SocialLink
                         whiteblack={true}
                         href={item.twitter}
                         target="_blank" rel="noopener noreferrer"
                        >
                           <IconS><FaTwitter/></IconS>    
                        </SocialLink>
                    </SocialItem>
                    <SocialItem>
                         <SocialLink
                            whiteblack={true}
                            href={item.github}
                            target="_blank" rel="noopener noreferrer"
                        >
                           <IconS><FaGithubAlt/></IconS>   
                        </SocialLink>
                    </SocialItem>
                </SocialMenu>
                <Signature>
                    <SignatureP>
                        JobsGabon - 2021
                    </SignatureP>
              </Signature>
            </SocialMedia>
            </>
           
            ))}
        </SidebarContainer>
        </IconContext.Provider>
       
    )
}

export default Sidebar
