import React, {useState,useEffect} from 'react'
import styled from 'styled-components'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import Footer from '../../components/Footer/Footer'
import BreadCrumbs from '../components/Breadcrumbs'
import NavbarBottom from '../components/NavbarBottom'
import PubLaterale from '../components/Publicite'
import Sidebar from '../components/Publicite/Sidebar'
import { Container, Grid,makeStyles } from '@material-ui/core'
import {dataConseil} from '../components/data/dataConseil' 
import SearchBar from '../components/SearchBar/SearchBar'
import CardFull from '../components/Cards/CardFull'
import app from '../../firebase/base';





const HeadSection = styled.div`
   position:fixed;
   width:100%;
   z-index:10;
   background-color:#fff;
   box-shadow: 0 6px 20px rgba(10, 10, 10, 0.1);

`

const PageContent = styled.div`   
   padding-top: 185px;
   margin-left:70px; 
   margin-bottom: 20px;

   @media screen and (max-width: 768px){
       margin-left:30px;
    }
  
`

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 2,
     
    }
  }));


const Conseils = () => {

    const classes = useStyles();
 
    const [isOpen, setIsOpen] = useState(false)
    const [conseils, setConseils] = useState([])
    const db = app.firestore();

    const toggle = () =>{
        setIsOpen(!isOpen)
    }

        const getConseil = async () => {
        
            await db.collection("postes")
               .where("categorie", "==", "conseil") 
               .where("visibilite", "==", "on")
               .onSnapshot((querySnapshot) => {
                   var list = []
                   querySnapshot.forEach((doc) => {
                    list.push({...doc.data(), id: doc.id })
                   });
                   setConseils(list)              
               });
            
            }
    
    
      useEffect(()=> {
       
        getConseil()
    }, [])
    return (
        <>
            <ScrollToTop/>
            <HeadSection>
                <Sidebar  isOpen={isOpen}  toggle={toggle}/>
                <PubLaterale   toggle={toggle}/>
                <NavbarBottom/>
                <BreadCrumbs/>
            </HeadSection>

            <PageContent className={classes.root}>
                    <Container>

                           
                            <Grid container spacing={3} lg={12} md={12}  xs={12}>
                                <Grid xs={12}>
                                    <SearchBar/>
                                </Grid>
                               {conseils.map((item,index) => 
                                 <Grid item lg={4} md={6}  xs={12}  key={index}>
                                      <CardFull lien="/candidats/single-conseil"   {...item} categorie="conseil"  />
                                 </Grid>
                              )}
                            </Grid>

                    </Container>
            </PageContent>
            <Footer/>  
        </>
    )
}

export default Conseils
