import React,{useEffect,useContext} from 'react'
import {Grid} from '@material-ui/core'
import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import * as collection from '../data/collection'
import { AuthContext } from '../../../firebase/AuthContext';
import styled from 'styled-components'



const BlockCache = styled.div`
 display:none;
`;


const FormLangues = (props) => {
   
    const {addOrEdit, recordForEdit} = props
    const {currentUser} = useContext(AuthContext);

    const initialValues = {
        id:'',
        nom: '',
        id_utilisateur:currentUser.uid,
        note:'',
        niveau_experience: '',
        annee_experience: ''
    }
    

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('nom' in fieldValues)
            temp.nom = fieldValues.nom ? "" : "Champ requis."
        if ('id_utilisateur' in fieldValues)
            temp.id_utilisateur = fieldValues.id_utilisateur ? "" : "Champ requis."
        if ('note' in fieldValues)
             temp.note = fieldValues.note.length < 2 ? "" : "Maximum  de 1 chiffre requis."
        if ('niveau_experience' in fieldValues)
            temp.niveau_experience = fieldValues.niveau_experience.length !== 0 ? "" : "Champ requis."
        if ('annee_experience' in fieldValues)
            temp.annee_experience = fieldValues.annee_experience.length < 3 ? "" : "Maximum  de 2 chiffre requis."
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    }= useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit,setValues])

    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >
           
                <Grid container >
                       <Grid item md={6} xs={6}>

                                <Controls.Input
                                    name="annee_experience"
                                    label="nombre annee_experience"
                                    value={values.annee_experience}
                                    onChange={handleInputChange}
                                    error={errors.annee_experience}
                                />
                              
                             <BlockCache>
                                 <Controls.Input
                                        name="id_utilisateur"
                                        label="Identifiant"
                                        value={values.id_utilisateur}
                                        onChange={handleInputChange} 
                                        error={errors.id_utilisateur}
                                       
                                /> 
                            </BlockCache>
                            <Controls.Select 
                                    name="niveau_experience"
                                    label="Niveau"
                                    value={values.niveau_experience}
                                    onChange={handleInputChange}
                                    options={collection.getNiveauCollection()}
                                    error={errors.niveau_experience}
                                />
                          
                            </Grid>
                            <Grid item md={6} xs={6}>
                              
                                 

                                <Controls.Input
                                    name="note"
                                    label="Votre note sur une echelle de 5"
                                    value={values.note}
                                    onChange={handleInputChange}
                                    error={errors.note}
                                />
                                <Controls.Input
                                    name="nom"
                                    label="Langue"
                                    value={values.nom}
                                    onChange={handleInputChange}
                                    error={errors.nom}
                                />
                             
                                
                        </Grid>
                                 <div>
                                    <Controls.Button 
                                        type="submit"
                                        text="Valider"
                                     />
                                    <Controls.Button
                                        text="Effacer"
                                        color="default"
                                        onClick={resetForm}
                                     />
                                  </div>
                    </Grid>
               
        </Form>
    )
}

export default FormLangues
