import React, {useCallback} from 'react'
import { Link as RouterLink, withRouter} from 'react-router-dom';
import { 
    Paper,
    makeStyles,
    Container,
    Box,
    Grid,
    Link,
    TextField,
    Typography,
    Button
} from '@material-ui/core'
import { Formik } from 'formik';
import * as Yup from 'yup';
import app from '../../firebase/base';


const useStyles = makeStyles((theme) => ({
    root: {
      backgroundColor: theme.palette.background.dark,
      height: '100%',
      paddingBottom: theme.spacing(2),
      paddingTop: theme.spacing(2),
     
    } ,
    textPrimary:{
        color: '#008647',
      },
    textSecondary:{
        color: '#1a1359',
      },

      buttonStyle: {
        backgroundColor: "#1a1359",
        color: "#fff",
      
        "&:hover":{
            backgroundColor: "#008649",
        }
    },
    buttonFbk: {
        backgroundColor: "#1a1359",
        color: "#fff",
      
        "&:hover":{
            backgroundColor: "#1a1359",
        }
    }
  }));

const ForgotPassword = ({history}) => {

    const classes = useStyles();


    const handleLogin = useCallback(
        async event => {
        event.preventDefault();
        const {email} = event.target.elements;
        try{

           await app.auth().sendPasswordResetEmail(email.value);
           alert("Vérifier votre boîte mail")
               
                
        } catch (error) {
            alert(error);
        }
    },
     [history]
    );

 

 /*   const submitHandler = e => {
        e.preventDefault();

        
            navigate.push('/candidats/accueil', { replace: true });
      
    }*/




    return (
        <>
            <Paper className={classes.root}>
                <Box
                    display="flex"
                    flexDirection="column"
                    height="100%"
                    justifyContent="center"
                >
                 <Container maxWidth="sm">
                        <Formik
                            initialValues={{
                                email: '',
                                
                              }}

                            validationSchema={Yup.object().shape({
                                email: Yup.string().email('Vous devez entrer un email valide').max(255).required('Email requis'),
                               
                            })}

                            onSubmit={() => {
                                console.log('Auth')
                              
                             }}
                          >

                            {({
                                errors,
                                handleBlur,
                                handleChange,
                                handleSubmit,
                                isSubmitting,
                                touched,
                                values
                                }) => (

                                <form onSubmit={handleLogin}>
                                      <Box mb={3}>
                                            <Typography
                                                color="textPrimary"
                                                variant="h4"
                                            >
                                                Mot de passe oublié
                                            </Typography>
                                            <Typography
                                                color="textSecondary"
                                                gutterBottom
                                                variant="body2"
                                            >
                                               Restaurez votre mot de passe
                                            </Typography>
                                            </Box>
                                           
                                            <Box
                                            mt={3}
                                            mb={1}
                                            >
                                          
                                            
                                            </Box>
                                            <Grid
                                               container
                                           
                                            >
                                            <TextField
                                            error={Boolean(touched.email && errors.email)}
                                            fullWidth
                                            helperText={touched.email && errors.email}
                                            label="Adresse Email"
                                            margin="normal"
                                            name="email"
                                            onBlur={handleBlur}
                                            onChange={handleChange}
                                            type="email"
                                            value={values.email}
                                            variant="outlined"
                                            required
                                            />
                                          {/*  <TextField
                                            error={Boolean(touched.password && errors.password)}
                                            fullWidth
                                            helperText={touched.password && errors.password}
                                            label="Mot de passe"
                                            margin="normal"
                                            name="password"
                                            onBlur={handleBlur}
                                            onChange={handleChange}
                                            type="password"
                                            value={values.password}
                                            variant="outlined"
                                            required
                                          />*/}
                                            </Grid>
                                            <Box my={2}>
                                           
                                                <Button
                                                        className={classes.buttonStyle}
                                                        disabled={isSubmitting}
                                                        fullWidth
                                                        size="large"
                                                        type="submit"
                                                        variant="contained"
                                                    >
                                                       Restaurer
                                                    </Button>
                                               
                                                
                                                
                                           
                                            </Box>
                                           
                                           
                                </form>
                            )}

                        </Formik>
                 </Container>

                </Box>
            </Paper>
        </>
    )
}

export default withRouter(ForgotPassword)
