import React, {useState,useEffect} from 'react'
import { Box,makeStyles, Grid, InputAdornment, Paper,TableCell,TableRow, Toolbar, TableBody } from '@material-ui/core'
import Controls from '../controls/Controls'
import {   Search, Visibility, Work } from '@material-ui/icons'
import useTable from '../useTable';
import CloseIcon from '@material-ui/icons/Close';

import Popup from '../Popup';
import Notification from '../Notification';
import ConfirmDialog from '../ConfirmDialog';
import './table.css'
import OffreForm from '../Forms/OffreForm';
import PageHeader from '../PageHeader';

import app from '../../../firebase/base';


const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '50%',
    },
    newButton: {
        
        right: '10px',
        color: '#1a1359',
        borderColor: '#1a1359',
        justifyContent:'center',
        alignItems: 'center',
        display:'flex-start'
    },
  
}))

const headCells = [
    { id: 'societe', label: 'Societe' },
    { id: 'titre_offre', label: 'Titre' },
    { id: 'description_offre', label: 'Description' },
    { id: 'responsable_recru', label: 'Responsable' },
    { id: 'fonction_offre', label: 'Fonction' },
    { id: 'type_contrat', label: 'Contrat' },
    { id: 'experience', label: 'Experience' },
    { id: 'secteur_activite', label: 'Secteur' },
    { id: 'ville_offre', label: 'Ville' },
    { id: 'province_offre', label: 'Province' },
    { id: 'vedette', label: 'Vedette' },
    { id: 'actions', label: 'Actions', disableSorting: true }
]

const TableOffreEmploi = (props) => {
    const classes = useStyles();

    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [records, setRecords] = useState([])
    const [openPopup, setOpenPopup] = useState(false)
    const [recordForEdit, setRecordForEdit] = useState(null)
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })

    const db = app.firestore();
    const listOffre = [];

 
    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records,headCells,filterFn);

    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.titre_offre.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchContrat = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.type_contrat.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchVille = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.ville_offre.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchProvince = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.province_offre.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchDate = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.date_debut.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchSociete = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.societe.toLowerCase().includes(target.value))
            }
        })
    }

    const getOffre = async () => {
        await db.collection("offre_emploi")
                .onSnapshot((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        listOffre.push({...doc.data(), id: doc.id })
                    });
                    setRecords(listOffre)              
                });
      }


    const addOrEdit = async (obj,resetForm) => {
        try{
            if (obj.id == ""){
                 await  db.collection("offre_emploi").doc().set(obj)   
            }else{
                 await db.collection("offre_emploi").doc(obj.id).update(obj)
            }
            resetForm()
            setRecordForEdit(null)
            setOpenPopup(false)
            getOffre()
            setNotify({
                    isOpen: true,
                    message: 'Validation avec Succes',
                    type: 'success'
              })
            }catch (error) {
            console.error(error);
          }
    }

    const openInPopup = item => {
        setRecordForEdit(item)
        setOpenPopup(true)
    }

    const onDelete = async (id) => {
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        })
        await db.collection("offre_emploi").doc(id).delete();
        getOffre()
        setNotify({
            isOpen: true,
            message: 'Suppression avec Success',
            type: 'error'
        })
    }

    useEffect(()=> {
        getOffre();
    }, [])

    return (
        <Box p={2}>
                <PageHeader
                title="Gestion des offres d'emploi"
                subTitle="Poster une nouvelle offre d'emploi, modifier et supprimer"
                icon={<Work fontSize="large" />}
              
             
            />
               <Paper  >
                <Toolbar>
                    <Grid  container spacing={2}  xs={12}>
                        <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par titre"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearch}  
                            />
                       </Grid>
                       <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par contrat"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchContrat}  
                            />
                       </Grid>
                       
                      <Grid item lg={4} md={6} xs={12} >
                           <Controls.Input
                                label="Rechercher par société"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchSociete}  
                            />
                        </Grid>
                        <Grid iitem lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par ville"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchVille}  
                            />  
                       </Grid>
                       <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par province"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchProvince}  
                            />  
                       </Grid>
                       <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par date_début"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchDate}  
                            />  
                       </Grid>
                    </Grid>
                </Toolbar>
                  
                  <Grid className="container">
                    <TblContainer>
                        <TblHead/>
                        <TableBody>
                        {
                                recordsAfterPagingAndSorting().map((item,index) =>
                                    (<TableRow key={index}>
                                         <TableCell>{item.societe}</TableCell>
                                        <TableCell>{item.titre_offre}</TableCell>
                                        <TableCell>{item.description_offre.substring(0,10)}...</TableCell>
                                        <TableCell>{item.responsable_recru}</TableCell>
                                        <TableCell>{item.fonction_offre}</TableCell>
                                        <TableCell>{item.type_contrat}</TableCell>
                                        <TableCell>{item.experience}</TableCell>
                                        <TableCell>{item.secteur_activite}</TableCell>
                                        <TableCell>{item.ville_offre}</TableCell>
                                        <TableCell>{item.province_offre}</TableCell>
                                        <TableCell>{item.vedette}</TableCell>
                                        <TableCell>
                                            <Controls.ActionButton
                                                color="primary"
                                                onClick={() => { openInPopup(item) }}
                                              >
                                                <Visibility fontSize="small" /> 
                                            </Controls.ActionButton>
                                            <Controls.ActionButton
                                                color="secondary"
                                                onClick={() => {
                                                    setConfirmDialog({
                                                        isOpen: true,
                                                        title: 'Vous ne pouvez pas supprimer cette information',
                                                        subTitle: "Cliquer sur non pour fermer",
                                                        //onConfirm: () => { onDelete(item.id) }
                                                    })
                                                }}
                                              >
                                                <CloseIcon fontSize="small" />
                                            </Controls.ActionButton>
                                        </TableCell>
                                    </TableRow>)
                                )
                            }
                        </TableBody>
                    </TblContainer>
                    <TblPagination />
                    </Grid>
             </Paper>
             <Popup
                  title="Formulaire d'offre d'emploi"
                  openPopup={openPopup}
                  setOpenPopup={setOpenPopup}
             >
                 <OffreForm
                   recordForEdit={recordForEdit}
                   addOrEdit={addOrEdit}
                 />
                 
             </Popup>
             <Notification
                notify={notify}
                setNotify={setNotify}
            />
             <ConfirmDialog
                confirmDialog={confirmDialog}
                setConfirmDialog={setConfirmDialog}
             />
        </Box>
    )
}

export default TableOffreEmploi
