import { Box, Grid } from '@material-ui/core'
import React, {useEffect }  from 'react'
import Controls from '../controls/Controls'
import { Form, useForm } from '../useForm'
import {getContratCollection,getFrequence,getVilleCollection,getProvinceCollection,getYesOrNo} from '../../data/collection'


const initialValues = {
    id: '',
    societe:'',
    titre_offre: '',
    description_offre: '',
    responsable_recru: '',
    fonction_offre: '',
    type_contrat: '',
    experience: '',
    secteur_activite: '',
    ville_offre: '',
    province_offre: '',
    vedette:''
   // date_debut: new Date(),
   // date_fin: new Date()
}



const OffreForm = (props) => {

    const {addOrEdit, recordForEdit} = props

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('titre_offre' in fieldValues)
            temp.titre_offre = fieldValues.titre_offre ? "" : "Champ requis."
        if ('description_offre' in fieldValues)
            temp.description_offre = fieldValues.description_offre ? "" : "Champ requis."
        if ('responsable_recru' in fieldValues)
            temp.responsable_recru = fieldValues.responsable_recru ? "" : "Champ requis."
        if ('fonction_offre' in fieldValues)
            temp.fonction_offre = fieldValues.fonction_offre ? "" : "Champ requis."
        if ('type_contrat' in fieldValues)
            temp.type_contrat = fieldValues.type_contrat.length !== 0 ? "" : "Champ requis."
        if ('type_offre' in fieldValues)
            temp.type_offre = fieldValues.type_offre.length !== 0 ? "" : "Champ requis."
        if ('salaire_offre' in fieldValues)
            temp.salaire_offre = fieldValues.salaire_offre ? "" : "Champ requis."
        if ('ville_offre' in fieldValues)
            temp.ville_offre = fieldValues.ville_offre.length !== 0 ? "" : "Champ requis."
        if ('province_offre' in fieldValues)
            temp.province_offre = fieldValues.province_offre.length !== 0 ? "" : "Champ requis."
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    } =useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit])

    return (
        <Form  onSubmit={handleSubmit}>
                <Grid container spacing={2}>
                    <Grid item xs={6}> 
                      <Controls.Input
                              name="societe"
                              label="societe"
                              value={values.societe}
                              onChange={handleInputChange}
                              error={errors.societe}
                              disabled
                        />
                        <Controls.Input
                              name="titre_offre"
                              label="titre_offre"
                              value={values.titre_offre}
                              onChange={handleInputChange}
                              error={errors.titre_offre}
                              disabled
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="description_offre"
                              label="description_offre"
                              rows="4"
                              multiline="multiline"
                              value={values.description_offre}
                              onChange={handleInputChange}
                              error={errors.description_offre}
                              disabled
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="responsable_recru"
                              label="responsable_recru"
                              value={values.responsable_recru}
                              onChange={handleInputChange}
                              error={errors.responsable_recru}
                              disabled
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="fonction_offre"
                              label="fonction_offre"
                              value={values.fonction_offre}
                              onChange={handleInputChange}
                              error={errors.fonction_offre}
                              disabled
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                            name="type_contrat"
                            label="type_contrat"
                            value={values.type_contrat}
                            onChange={handleInputChange}
                            options={getContratCollection()}
                            error={errors.type_contrat}
                            disabled
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                            name="experience"
                            label="experience"
                            value={values.experience}
                            onChange={handleInputChange}
                            options={getFrequence()}
                            error={errors.experience}
                            disabled
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                            name="secteur_activite"
                            label="secteur_activite"
                            value={values.secteur_activite}
                            onChange={handleInputChange}
                            error={errors.secteur_activite}
                            disabled
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Select
                            name="ville_offre"
                            label="ville_offre"
                            value={values.ville_offre}
                            onChange={handleInputChange}
                            options={getVilleCollection()}
                            error={errors.ville_offre}
                            disabled
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Select
                            name="province_offre"
                            label="province_offre"
                            value={values.province_offre}
                            onChange={handleInputChange}
                            options={getProvinceCollection()}
                            error={errors.province_offre}
                            disabled
                        />
                    </Grid>
                    <Grid item xs={6}> 
                    <Box>
                                <h5>Mettre en vedette ?</h5>
                                <Controls.Select
                                  name="vedette"
                                  label="mettre en vedette ?"
                                  value={values.vedette}
                                  onChange={handleInputChange}
                                  options={getYesOrNo()}
                                  error={errors.vedette}
                              />
                    </Box>
                    {/*<Controls.DatePicker
                        name="date_debut"
                        label="date_debut"
                        value={values.date_debut}
                        onChange={handleInputChange}
                    />
                     <Controls.DatePicker
                        name="date_fin"
                        label="date_fin"
                        value={values.date_fin} 
                        onChange={handleInputChange}
                    />*/}
                  </Grid>
                 
                   <div>
                        <Controls.Button
                            type="submit"
                            text="Valider" />
                        <Controls.Button
                            text="Effacer"
                            color="default"
                            onClick={resetForm} />
                  </div>
                </Grid>
        </Form>
    )
}

export default OffreForm
