const EmploiSection = (props) => {

    const [viewJob, setViewJob] = useState(false)
    const [emplois, setEmplois] = useState([]);
    const db = app.firestore();
    var listOffre = []

    const getOffreEmploi = async () => {
        await db.collection("offre_emploi")
             .orderBy("date_ajout", "desc")
             .limit(5)
            .onSnapshot((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                    listOffre.push({...doc.data(), id: doc.id, postedOn:doc.data().date_ajout.toDate() })
                    //count = querySnapshot.numChildren();
                
                });
                setEmplois(listOffre)              
           });
      }

     useEffect(()=> {
        getOffreEmploi();
      }, [])


    return (
        <>
            <InfoContainer lightBg={props.lightBg} id={props.id} >
                    <InfoWrapper>
                        <InfoRow  imgStart={props.imgStart} >   
                            <Column1>
                              <TextWrapper>
                                  <TopLine>{props.topLine}</TopLine>
                                  <Heading lightText={props.lightText}>{props.headLine}</Heading>
                                  <Subtitle  darkText={props.darkText}>{props.description}</Subtitle>
                                  <BtnWrap>
                                  
                                        <ButtonLink to={props.path} >
                                            Voir plus
                                        </ButtonLink>
                                                                               
                                        
                                  </BtnWrap>
                              </TextWrapper>
                            </Column1> 
                            <Column2>
                            <Scrollbars autoHeight autoHeightMax = {420}>
                                
                                <ImgWrap> 
                                
                                    {emplois.map((job,index) => (
                                        <Fragment  key={index} >
                                                <ViewJobModal 
                                                              
                                                              utilisateur={job.id_utilisateur}
                                                              job={viewJob}    
                                                              closeModal={()=> setViewJob({})} 
                                                            
                                                 />
                                                <Job 
                                                     utilisateur={job.id_utilisateur}  
                                                     open={()=>setViewJob(job)} 
                                                     {...job} 
                                                /> 
                                        </Fragment>)
                                        )}
                                  
                                 
                                </ImgWrap>
                                </Scrollbars>
                            </Column2>
                        </InfoRow>
                    </InfoWrapper>
            </InfoContainer>
        </>
    )
}

export default EmploiSection
