import React,{useState,useEffect} from 'react'
import SidebarAuth from '../components/NavbarHome/SidebarAuth'
import ScrollToTop from '../components/Signin/ScrollToTop'
import Footer from '../components/Footer/Footer'
import SecondNavbar from '../components/NavbarHome/SecondNavbar'
import { Box,makeStyles, Card, CardContent, CardHeader, Container, Divider, Grid, Typography } from '@material-ui/core'
import app from '../firebase/base';
import MenuParams from '../components/Menus/MenuParams'
import MDEditor from '@uiw/react-md-editor';
import CardOne from '../candidats/components/Cards/CardOne'
import CardPub from '../candidats/components/Publicite/CardPub'
import PubSlide from '../components/Slides/PubSlide'
 

const ConditionsUse = () => {

    const [isOpen, setIsOpen] = useState(false)
    const db = app.firestore();
    const [param, setParam] = useState([])

    const [etat, setEtat] = useState(null);
    const [conseils, setConseils] = useState([])

    const [etatPub, setEtatPub] = useState(null);
    const [pub, setPub] = useState([])



    const toggle = () =>{
        setIsOpen(!isOpen)
    }


    const getParam = async () => {
   
        await db.collection("parametres")
          .where("categorie", "==", "conditions-utilisation")
          .onSnapshot((querySnapshot) => {
            var list=[]
              querySnapshot.forEach((doc) => {
                list.push({...doc.data(), id: doc.id })
              });
            setParam(list);  
          });  
      } 

      const getConseil = async () => {
        await db.collection("postes")
           .where("categorie", "==", "conseil")
           .where("visibilite", "==", "on") 
           .where("type", "==", "une") 
           .onSnapshot((querySnapshot) => {
               var list = []
               querySnapshot.forEach((doc) => {
                if(doc.exists){
                    setEtat(true)
                    list.push({...doc.data(), id: doc.id })   
                }else{
                    setEtat(false)
                }
               
               });
               setConseils(list)              
           });
        
        }

        const getPub = async () => {
          await db.collection("publicites")
             .where("categorie", "==", "laterale")
             .where("visibilite", "==", "on")
             .onSnapshot((querySnapshot) => {
                 var list = []
                 querySnapshot.forEach((doc) => {
                  if(doc.exists){
                      setEtatPub(true)
                      list.push({...doc.data(), id: doc.id })
                  }else{
                      setEtatPub(false)
                  }

                 });
                 setPub(list)
             });

          }


      useEffect(()=> {
       getParam()
       getConseil()
       getPub()
    }, [])

    return (
        <>
           <ScrollToTop/>
            <SidebarAuth isOpen={isOpen}  toggle={toggle}/>
            <SecondNavbar  toggle={toggle}/>
            <Box mt={4} mb={4}>
               <Container maxWidth={false} >
                   <Grid container spacing={2} >
                       
                       <Grid  lg={8} md={6} xs={12}>
                           <Card>
                                <CardHeader 
                                   title="Conditions d'utilisation"
                                />
                                <Divider/>
                                
                                <CardContent >
                                   {param.map((item,index) =>(
                                      <Typography align="justify" >
                                           <MDEditor.Markdown key={index} source={item.contenu} />
                                       </Typography>
                                       ))}            
                                </CardContent>
                              
                            </Card>
                        </Grid>
                        <Grid item lg={4} md={6} xs={12}  >
                         <Card>
                                <CardHeader 
                                   title="Menu"
                                />
                                <Divider/>
                               <CardContent>
                                  <MenuParams/>
                               </CardContent>
                            </Card>
                           
                         {etatPub === true &&
                            <>
                              <PubSlide  dataPub={pub} />
                            </>
                          } 
                        
                         {etat === true &&
                                <>
                                {conseils.map((item,index) => (
                                 <CardOne  key={index}  {...item} />
                                ))}
                              </>
                         }
                       </Grid>
                   </Grid>
                   
              </Container>
            </Box>
            <Footer/>  
        </>
    )
}

export default ConditionsUse
