import React, {useState} from 'react'
import LoginForm from '../components/Auth/LoginForm'
import styled from 'styled-components'
import ScrollToTop from '../components/Signin/ScrollToTop';
import NavbarAuth from '../candidats/components/NavbarAuth';
import Footer from '../components/Footer/Footer';
import SidebarAuth from '../components/NavbarHome/SidebarAuth';




const HeadSection = styled.div`
   position:fixed;
   width:100%;
   z-index:10;
   background-color:#fff;
   box-shadow: 0 6px 20px rgba(10, 10, 10, 0.1);

`

const PageContent = styled.div`   
   padding-top: 170px;
  
  
`

const Signin = () => {

    const [isOpen, setIsOpen] = useState(false)

    const toggle = () =>{
        setIsOpen(!isOpen)
    }

 
    return (
        <>
            <ScrollToTop/>
            <HeadSection>
                <SidebarAuth   isOpen={isOpen}  toggle={toggle}/>
                <NavbarAuth toggle={toggle}/>
            </HeadSection>

             <PageContent>
                <LoginForm/>
            </PageContent>
            <Footer/>
        </>
    )
}

export default Signin
