import React,{useContext} from 'react'
import {
    MobileSidebarContainer, 
    Icon, 
    CloseIcon,
    MobileSidebarWrapper,
    MobileSidebarMenu,
    MobileSidebarLink,
    MobileSideBtnWrap,

} from './MobileElements'
import {IconButton,makeStyles, Tooltip } from '@material-ui/core'
import { AuthContext } from '../../../firebase/AuthContext'
import { ExitToApp } from '@material-ui/icons'

const useStyles = makeStyles((theme) => ({
    margin: {
       color: '#fff',
       alignItems: 'center',
       justifyContent: 'center',
       padding: '0 0.2rem'
    },
    logoutText:{
        color: '#fff'
    }
   
  }));

const MobileSidebar = ({isOpen,toggle}) => {

    const {currentUser, logout} = useContext(AuthContext);
    const classes = useStyles();

    return (
        <>
            <MobileSidebarContainer isOpen={isOpen}  onClick={toggle} >
                <Icon onClick={toggle}>
                    <CloseIcon />
                </Icon>
                <MobileSidebarWrapper>
                    <MobileSidebarMenu>
                        <MobileSidebarLink to="/candidats" onClick={toggle}>Accueil candidats</MobileSidebarLink>
                        <MobileSidebarLink to="/candidats/accueil" onClick={toggle}>Accueil</MobileSidebarLink>
                        <MobileSidebarLink to="/candidats/moncompte"   onClick={toggle}>Mon profile</MobileSidebarLink>
                        <MobileSidebarLink to="/candidats/competences" onClick={toggle}>Mes compétences</MobileSidebarLink>
                      {/*  <MobileSidebarLink to="/candidats/portefolio" onClick={toggle}>Mes projets</MobileSidebarLink>*/}
                        <MobileSidebarLink to="/candidats/mescandidatures"  onClick={toggle}>Mes candidatures</MobileSidebarLink>
                        <MobileSidebarLink to="/candidats/editercv"  onClick={toggle}>Editer mon CV</MobileSidebarLink>
                        <MobileSidebarLink to="/candidats/contact"  onClick={toggle}>Mon contact</MobileSidebarLink>
                    </MobileSidebarMenu>
                    <MobileSideBtnWrap>
                           { currentUser && 
                              (
                                <Tooltip title="Se déconnecter" placement="right-start">
                                    <IconButton className={classes.margin} onClick={logout}>
                                                <ExitToApp/>
                                            
                                    </IconButton>
                                </Tooltip>
                                
                              )
                            }
                    </MobileSideBtnWrap>
                </MobileSidebarWrapper>
            </MobileSidebarContainer>
        </>
    )
}

export default MobileSidebar
