import React,{useState,useEffect,useContext} from 'react'
import { WbSunny } from '@material-ui/icons'
import PageHeader from '../PageHeader'
import { makeStyles, TableBody, TableRow, TableCell, Toolbar, Grid } from '@material-ui/core';
import useTable from "../../components/useTable";
import Controls from "../controls/Controls";
import AddIcon from '@material-ui/icons/Add';
import Popup from "../Popup";
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';
import CloseIcon from '@material-ui/icons/Close';
import Notification from "../Notification";
import ConfirmDialog from "../ConfirmDialog";
import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';
import '../Candidatures/Candidatures.css'
import FormExp from './FormExp';
import { format } from 'date-fns'



const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '75%'
    },
    newButton: {
        position: 'absolute',
        right: '10px'
    }
}))

const headCells = [
    { id: 'nom', label: 'nom' }, 
    { id: 'entreprise', label: 'entreprise' },
    { id: 'poste', label: 'poste' },
    { id: 'secteur_activite', label: 'secteur_activite' },
    { id: 'region', label: 'region' },
    { id: 'description_experience', label: 'description' },
    { id: 'date_debut', label: 'date_debut' },
    { id: 'date_fin', label: "date_fin" },
    { id: 'actions', label: 'Actions', disableSorting: true }
]


const TableExperience = () => {

    const classes = useStyles();

    const [openPopup, setOpenPopup] = useState(false)
    const [recordForEdit, setRecordForEdit] = useState(null)
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })
    const [records, setRecords] = useState([]);
    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })

    const {currentUser} = useContext(AuthContext);
    const db = app.firestore();
    const listExperiences = [];
   
    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records, headCells, filterFn);
   
    const getExperiences = async () => {
       await db.collection("experiences")
          .where("id_utilisateur", "==", currentUser.uid)
          .onSnapshot((querySnapshot) => {
              querySnapshot.forEach((doc) => {
                  listExperiences.push({...doc.data(), id: doc.id
                 })
             
              });
              setRecords(listExperiences)              
          });
     }

     useEffect(()=> {
        getExperiences();
    }, [])


     const addOrEdit = async (obj,resetForm) => {
        try {

            if (obj.id == "")
                await db.collection("experiences").doc().set(obj);
            else
                await db.collection("experiences").doc(obj.id).update(obj);
            resetForm()
            setRecordForEdit(null)
            setOpenPopup(false)
            getExperiences()
            setNotify({
                isOpen: true,
                message: 'Validation avec Succes',
                type: 'success'
            })
            

        } catch (error) {
            console.error(error);
          }
       }

           
      const onDelete = async (id)=> {
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        })
        await db.collection("experiences").doc(id).delete();
        getExperiences()
        setNotify({
            isOpen: true,
            message: 'Suppression avec Success',
            type: 'error'
        })
       }

    

       const openInPopup = item => {
        setRecordForEdit(item)
        setOpenPopup(true)
        }

      
    return (
        <>
            <PageHeader   
            title="Rubrique Expériences"
            subTitle="Ajouter, modifier et supprimer des expériences dans votre CV"
            icon={<WbSunny fontSize="large"/>}
           /> 
           <Toolbar>
                <Controls.Button
                        text="Ajouter"
                        variant="outlined"
                        startIcon={<AddIcon />}
                        className={classes.newButton}
                        onClick={() => { setOpenPopup(true); setRecordForEdit(null); }}
                />
           </Toolbar>
           <Grid className="container">
           <TblContainer>
                <TblHead />
                 <TableBody>
                        {
                            recordsAfterPagingAndSorting().map((item,index )=>
                                
                                (   <TableRow key={index}>
                                    <TableCell>{item.nom}</TableCell>
                                    <TableCell>{item.entreprise}</TableCell>
                                    <TableCell>{item.poste}</TableCell>
                                    <TableCell>{item.secteur_activite}</TableCell>
                                    <TableCell>{item.region}</TableCell>
                                    <TableCell>{item.description_experience.substring(0,10)}...</TableCell>
                                    <TableCell>{item.date_debut}</TableCell>
                                    <TableCell>{item.date_fin}</TableCell>

                                    <TableCell>
                                        <Controls.ActionButton
                                            color="primary"
                                            onClick={() => {openInPopup(item)}} 
                                             >
                                            <EditOutlinedIcon fontSize="small" />
                                        </Controls.ActionButton>
                                        <Controls.ActionButton
                                            color="secondary"
                                            onClick={() => {
                                                setConfirmDialog({
                                                    isOpen: true,
                                                    title: 'êtes-vous sûr de vouloir supprimer cet enrégistrement?',
                                                    subTitle: "Vous pouvez annuler cette opération",
                                                    onConfirm: () => {onDelete(item.id) }
                                                })
                                            }}
                                           >
                                            <CloseIcon fontSize="small" />
                                        </Controls.ActionButton>
                                    </TableCell>
                                </TableRow>)
                            )
                        }
                    </TableBody>
                </TblContainer>
                <TblPagination />
                </Grid>
                <Popup
                    title="Formulaire d'expériences"
                    openPopup={openPopup}
                    setOpenPopup={setOpenPopup}
                >
                    <FormExp 
                         recordForEdit={recordForEdit}
                         addOrEdit={addOrEdit}
                    />
                </Popup>
                <Notification
                    notify={notify}
                    setNotify={setNotify}
                />
                <ConfirmDialog
                    confirmDialog={confirmDialog}
                    setConfirmDialog={setConfirmDialog}
                />
        </>
    )
}

export default TableExperience
