import { Grid } from '@material-ui/core'
import React, {useEffect }  from 'react'
import Controls from '../controls/Controls'
import { Form, useForm } from '../useForm'
import {getVilleCollection,getProvinceCollection} from '../../data/collection'


const initialValues = {
    id: 0,
    nom_candidat: '',
    prenom_candidat: '',
    email_candidat: '',
    telephone_candidat: '',
    ville_candidat: '',
    province_candidat: '',
    situation_candidat:'',
   
}



const CandidatForm = (props) => {

    const {recordForEdit} = props

   
 
    const {
        values,
        setValues,
    } =useForm(initialValues, true);

   

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit])

    return (
        <Form >
                <Grid container spacing={2}>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="nom_candidat"
                              label="nom_candidat"
                              value={values.nom_candidat}
                              
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="prenom_candidat"
                              label="prenom_candidat"
                              value={values.nom_candidat}
                            
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="email_candidat"
                              label="email_candidat"
                              type="email"
                              value={values.email_candidat}
                              
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="situation_candidat"
                              label="situation_candidat"
                              value={values.situation_candidat}
                              
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Select
                            name="ville_candidat"
                            label="ville_candidat"
                            value={values.ville_candidat}
                            options={getVilleCollection()}
                            
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Select
                            name="province_candidat"
                            label="province_candidat"
                            value={values.province_candidat}
                            options={getProvinceCollection()}
                          
                        />
                    </Grid>
                   
                </Grid>
        </Form>
    )
}

export default CandidatForm
