import React, {useState,useEffect,useContext} from 'react'
import { Box,makeStyles, Grid, InputAdornment, Paper,TableCell,TableRow, Toolbar, TableBody } from '@material-ui/core'
import Controls from '../controls/Controls'
import {  Business,  Search } from '@material-ui/icons'
import useTable from '../useTable';
import CloseIcon from '@material-ui/icons/Close';

import Popup from '../Popup';
import Notification from '../Notification';
import ConfirmDialog from '../ConfirmDialog';
import './table.css'
import PageHeader from '../PageHeader';
import RecruteurForm from '../Forms/RecruteurForm';

import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';



const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '50%',
    },
    newButton: {
        
        right: '10px',
        color: '#1a1359',
        borderColor: '#1a1359',
        justifyContent:'center',
        alignItems: 'center',
        display:'flex-start'
    },
  
}))

const headCells = [
    { id: 'nom_recruteur', label: 'nom' },
    { id: 'secteur_activite', label: 'secteur activite' },
    { id: 'telephone_recruteur', label: 'telephone' },
    { id: 'email_recruteur', label: 'email' },
    { id: 'referent_recruteur', label: 'referent' },
    { id: 'raison_sociale', label: 'raison_sociale' },
    { id: 'ville_recruteur', label: 'ville' },
    { id: 'province_recruteur', label: 'province' },
    { id: 'adresse_recruteur', label: 'adresse' },
    { id: 'description_recruteur', label: 'description' },
    { id: 'url_recruteur', label: 'site web' },
    { id: 'cvthequeplus', label: 'Cvthèque +' },
    { id: 'cvthequesimple', label: 'Cvthèque simple' },
    { id: 'vedette', label: 'en vedette' },
    { id: 'actions', label: 'Actions', disableSorting: true }
]

const TableRecruteur = (props) => {
    const classes = useStyles();

    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [records, setRecords] = useState([])
    const [openPopup, setOpenPopup] = useState(false)
    const [recordForEdit, setRecordForEdit] = useState(null)
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })
    const [role, setRole] = useState("");
    const {currentUser} = useContext(AuthContext);

    const db = app.firestore();
    const listRec = [];
    var user;

 
    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records,headCells,filterFn);


    const getRole = async () => {
        db.collection("utilisateurs")
        .where("id_utilisateur", "==", currentUser.uid)
        .onSnapshot((querySnapshot) => {
            querySnapshot.forEach((doc) => {
            user = doc.data().role_utilisateur;
            });
        setRole(user);
            
        });        
    } 

    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.nom_recruteur.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchSecteur = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.secteur_activite.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchVille = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.ville_recruteur.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchProvince = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.province_recruteur.toLowerCase().includes(target.value))
            }
        })
    }
  
    const getRecruteur = async () => {
        await db.collection("recruteurs")
                .onSnapshot((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        listRec.push({...doc.data(), id: doc.id })
                    });
                    setRecords(listRec)              
                });
      }

    const addOrEdit = async (obj,resetForm) => {
        try{
            if (obj.id == ""){
                 await  db.collection("recruteurs").doc().set(obj)   
            }else{
                 await db.collection("recruteurs").doc(obj.id).update(obj)
            }
            resetForm()
            setRecordForEdit(null)
            setOpenPopup(false)
            getRecruteur()
            setNotify({
                    isOpen: true,
                    message: 'Validation avec Succes',
                    type: 'success'
              })
            }catch (error) {
            console.error(error);
          }
    }

    const openInPopup = item => {
        setRecordForEdit(item)
        setOpenPopup(true)
    }

    const onDelete = async (id) => {
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        })
        await db.collection("recruteurs").doc(id).delete();
        getRecruteur()
        setNotify({
            isOpen: true,
            message: 'Suppression avec Success',
            type: 'error'
        })
    }

    useEffect(()=> {
        getRecruteur();
        getRole();
    }, [])

    return (
        <Box p={2}>
                <PageHeader
                title="Gestion des Recruteurs"
                subTitle="Ajouter un recruteur,modifier et supprimer"
                icon={<Business fontSize="large" />}
              
             
            />
               <Paper  >
                <Toolbar>
                    <Grid container spacing={2}  xs={12}>
                        <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par nom"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearch}  
                            />
                       </Grid>
                       <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par secteur"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchSecteur}  
                            />
                       </Grid>
                       
                     {/* <Grid item lg={4} md={6} xs={12} >
                             <Controls.Button 
                                text="Ajouter"
                               
                                variant="outlined"
                                startIcon={<AddIcon/>}
                                onClick={() => { setOpenPopup(true); setRecordForEdit(null); }}
                                className={classes.centerButton}
                                
                            />
                            </Grid>*/}
                        <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par ville"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchVille}  
                            />  
                       </Grid>
                       <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par province"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchProvince}  
                            />  
                       </Grid>
                    </Grid>
                </Toolbar>
                  
                  <Grid className="container">
                    <TblContainer>
                        <TblHead/>
                        <TableBody>
                        {
                                recordsAfterPagingAndSorting().map((item,index) =>
                                    (<TableRow key={index}>
                                        <TableCell>{item.nom_recruteur}</TableCell>
                                        <TableCell>{item.secteur_activite}</TableCell>
                                        <TableCell>{item.telephone_recruteur}</TableCell>
                                        <TableCell>{item.email_recruteur}</TableCell>
                                        <TableCell>{item.referent_recruteur}</TableCell>
                                        <TableCell>{item.raison_sociale}</TableCell>
                                        <TableCell>{item.ville_recruteur}</TableCell>
                                        <TableCell>{item.province_recruteur}</TableCell>
                                        <TableCell>{item.adresse_recruteur}</TableCell>
                                        <TableCell>{item.description_recruteur.substring(0,10)}...</TableCell>
                                        <TableCell>{item.url_recruteur}</TableCell>
                                        <TableCell>{item.cvthequeplus}</TableCell>
                                        <TableCell>{item.cvthequesimple}</TableCell>
                                        <TableCell>{item.vedette}</TableCell> 
                                        <TableCell>
                                        {role == "admin" &&(
                                            <Controls.ActionButton
                                                color="primary"
                                                onClick={() => { openInPopup(item) }}
                                              >
                                                <EditOutlinedIcon fontSize="small" /> 
                                            </Controls.ActionButton>
                                            )}
                                         {role == "admin" &&(
                                            <Controls.ActionButton
                                                color="secondary"
                                                onClick={() => {
                                                    setConfirmDialog({
                                                        isOpen: true,
                                                        title: 'êtes-vous sûr de vouloir supprimer cet enrégistrement?',
                                                        subTitle: "Vous pouvez annuler cette opération",
                                                        onConfirm: () => { onDelete(item.id) }
                                                    })
                                                }}
                                              >
                                                <CloseIcon fontSize="small" />
                                            </Controls.ActionButton>
                                            )}
                                        </TableCell>
                                    </TableRow>)
                                )
                            }
                        </TableBody>
                    </TblContainer>
                    <TblPagination />
                    </Grid>
             </Paper>
             <Popup
                  title="Formulaire des recruteurs"
                  openPopup={openPopup}
                  setOpenPopup={setOpenPopup}
             >
                 <RecruteurForm
                   recordForEdit={recordForEdit}
                   addOrEdit={addOrEdit}
                 />
                 
             </Popup>
             <Notification
                notify={notify}
                setNotify={setNotify}
            />
             <ConfirmDialog
                confirmDialog={confirmDialog}
                setConfirmDialog={setConfirmDialog}
             />
        </Box>
    )
}

export default TableRecruteur
