import React, {useState,useEffect} from 'react'
import { Box,makeStyles, Grid, InputAdornment, Paper,TableCell,TableRow, Toolbar, TableBody } from '@material-ui/core'
import Controls from '../controls/Controls'
import {  Description, FileCopy, PeopleOutlineTwoTone, Search } from '@material-ui/icons'
import useTable from '../useTable';
import CloseIcon from '@material-ui/icons/Close';
import VisibilityIcon from '@material-ui/icons/Visibility';

import dataCandidature from '../../data/dataCandidature.json'
import Popup from '../Popup';
import ConfirmDialog from '../ConfirmDialog';
import './table.css'
import CandidatureForm from '../Forms/CandidatureForm';
import PageHeader from '../PageHeader';
import app from '../../../firebase/base';
import {AuthContext} from '../../../firebase/AuthContext'

const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '50%',
    },
    newButton: {
        position: 'absolute',
        right: '10px',
        color: '#1a1359',
        borderColor: '#1a1359'
    }
}))

const headCells = [
    { id: 'titre_offre', label: 'Titre_offre' },
    { id: 'nom_candidat', label: 'Nom' },
    { id: 'prenom_candidat', label: 'Prenom' },
    { id: 'email_candidat', label: 'Email' },
    { id: 'niveau_etude', label: 'Niveau' },
    { id: 'telephone_candidat', label: 'Télephone' },
    { id: 'ville_candidat', label: 'Ville' },
    //{ id: 'cv_candidat', label: 'cv_candidat' },
    //{ id: 'lettre_motivation', label: 'lettre_motiva' },
    { id: 'date_candidature', label: 'date_candidat' },
    { id: 'actions', label: 'Actions', disableSorting: true }
]
const TableCandidature = (props) => {
    const classes = useStyles();

    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [records,setRecords] = useState(dataCandidature)
    const [openPopup, setOpenPopup] = useState(false)
    const [recordForEdit, setRecordForEdit] = useState(null)
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })
    const [offreId, setOffreId] = useState("")
    const [candidatId, setCandidatId] = useState("")
    const db = app.firestore();


    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records,headCells,filterFn); 



    const getCandidatures = async () => {

        await db.collection("candidatures")
        .onSnapshot((querySnapshot) => {
            var list = []
            querySnapshot.forEach((doc) => {
             list.push({...doc.data(), id: doc.id })
            });
            setRecords(list)           
        });

    }


    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.titre_offre.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchDate = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.date_candidature.toLowerCase().includes(target.value))
            }
        })
    }

 

    const openInPopup = item => {
        setRecordForEdit(item)
        setOpenPopup(true)
        setOffreId(item.id_offre)
        setCandidatId(item.id_candidat)
    }

    const onDelete = id => {
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        })
    }

    useEffect(() => {
        getCandidatures();   
    }, [])

    return (
        <Box p={2}>
            <PageHeader
                title="Gestion des candidatures"
                subTitle="Regarder qui à postuler à vos offres, télécharger son cv et sa lettre de motivation"
                icon={<Description fontSize="large" />}
             
            />
               <Paper  >
                <Toolbar>
                    <Grid container spacing={2}  xs={12}>
                      <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par offre"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearch}  
                            />
                         </Grid>
                        <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par date"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchDate}  
                            />
                         </Grid>
                     
                    </Grid>
                </Toolbar>
                   
                  <Grid className="container">
                    <TblContainer>
                        <TblHead/>
                        <TableBody>
                        {
                                recordsAfterPagingAndSorting().map((item,index) =>
                                    (<TableRow key={index}>
                                        <TableCell>{item.titre_offre}</TableCell>
                                        <TableCell>{item.nom_candidat}</TableCell>
                                        <TableCell>{item.prenom_candidat}</TableCell>
                                        <TableCell>{item.email_candidat}</TableCell>
                                        <TableCell>{item.niveau_etude}</TableCell>
                                        <TableCell>{item.telephone_candidat}</TableCell>
                                        <TableCell>{item.ville_candidat}</TableCell>
                                        <TableCell>{item.date_candidature}</TableCell>
                                        <TableCell>
                                            <Controls.ActionButton
                                                color="primary"
                                                onClick={() => { openInPopup(item) }}
                                              >
                                                <VisibilityIcon fontSize="small" /> 
                                            </Controls.ActionButton>
                                            <Controls.ActionButton
                                                color="secondary"
                                                onClick={() => {
                                                    setConfirmDialog({
                                                        isOpen: true,
                                                        title: 'Vous ne pouvez pas supprimer cette information',
                                                        subTitle: "Cliquer sur non",
                                                        /*onConfirm: () => { onDelete()
                                                         }*/
                                                    })
                                                }}
                                              >
                                                <CloseIcon fontSize="small" />
                                            </Controls.ActionButton>
                                        </TableCell>
                                    </TableRow>)
                                )
                            }
                        </TableBody>
                    </TblContainer>
                    <TblPagination />
                    </Grid>
             </Paper>
             <Popup
                  title="Informations de la candidature"
                  openPopup={openPopup}
                  setOpenPopup={setOpenPopup}
             >
                 
                 <CandidatureForm
                   recordForEdit={recordForEdit}
                   idOffre={offreId}
                   idCandidat={candidatId}
                 />
             </Popup>
             <ConfirmDialog
                confirmDialog={confirmDialog}
                setConfirmDialog={setConfirmDialog}
             />
        </Box>
    )
}

export default TableCandidature
