import React,{useState} from 'react'
import { HomeContainer } from '../components/HomeContent/HomeElements'
import HomeContent from '../components/HomeContent/Index'
import Sidebar from '../components/Sidebar/Index'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import {withRouter} from 'react-router-dom'
import MobileSidebar from '../components/Sidebar/MobileSidebar'


const HomeCandidat = () => {

  const [isOpen, setIsOpen] = useState(false)
    const toggle = () =>{
        setIsOpen(!isOpen)
    }  

 
    return (
        <>
         <ScrollToTop/> 
           <HomeContainer>
            <MobileSidebar  isOpen={isOpen}  toggle={toggle}/>
            <Sidebar toggle={toggle}/>
            <HomeContent />
           </HomeContainer>
          
        </>
    )
}

export default withRouter(HomeCandidat);
