import React,{useEffect,useContext} from 'react'
import { Grid} from '@material-ui/core'
import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import * as collection from '../data/collection'
import { AuthContext } from '../../../firebase/AuthContext';
import styled from 'styled-components'
import { format } from 'date-fns'




const BlockCache = styled.div`
 display:none;
`;


const FormFormations = (props) => {

    const {addOrEdit, recordForEdit} = props
    const {currentUser} = useContext(AuthContext);

    const initialValues = {
        id:'',
        nom: '',
        etablissement: '',
        id_utilisateur:currentUser.uid,
        discipline: '',
        niveau_atteint: '',
        ville_formation:'',
        province_formation: '',
        date_debut: format(new Date(),"dd/MM/yyyy"),
        date_fin:  format(new Date(),"dd/MM/yyyy")

    }

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('nom' in fieldValues)
            temp.nom = fieldValues.nom ? "" : "Champ requis."
        if ('id_utilisateur' in fieldValues)
           temp.id_utilisateur = fieldValues.id_utilisateur ? "" : "Champ requis."
        if ('etablissement' in fieldValues)
           temp.etablissement = fieldValues.etablissement ? "" : "Champ requis."
        if ('discipline' in fieldValues)
            temp.discipline = fieldValues.discipline ? "" : "Champ requis."
        if ('niveau_atteint' in fieldValues)
            temp.niveau_atteint = fieldValues.niveau_atteint ? "" : "Champ requis."
        if ('ville_formation' in fieldValues)
            temp.ville_formation = fieldValues.ville_formation.length !== 0 ? "" : "Champ requis."
        if ('province_formation' in fieldValues)
            temp.province_formation = fieldValues.province_formation.length !== 0 ? "" : "Champ requis."

        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    }= useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()

        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit,setValues])


    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >

                    <Grid
                        container
                    >
                       <Grid item md={6} xs={12}>
                              <Controls.Input
                                    name="nom"
                                    label="nom_formation"
                                    value={values.nom}
                                    onChange={handleInputChange}
                                    error={errors.nom}
                                />
                                <Controls.Input
                                    name="etablissement"
                                    label="etablissement"
                                    value={values.etablissement}
                                    onChange={handleInputChange}
                                    error={errors.etablissement}
                                />
                                <Controls.Input
                                    name="discipline"
                                    label="discipline"
                                    value={values.discipline}
                                    onChange={handleInputChange}
                                    error={errors.discipline}
                                />
                                 <BlockCache>
                                 <Controls.Input
                                        name="id_utilisateur"
                                        label="Identifiant"
                                        value={values.id_utilisateur}
                                        onChange={handleInputChange}
                                        error={errors.id_utilisateur}

                                />
                                </BlockCache>
                                 <Controls.Input
                                    name="niveau_atteint"
                                    label="niveau_atteint"
                                    value={values.niveau_atteint}
                                    onChange={handleInputChange}
                                    error={errors.niveau_atteint}
                                />
                        </Grid>

                        <Grid item md={6} xs={12}>
                                 <Controls.Select
                                    name="ville_formation"
                                    label="ville_formation"
                                    value={values.ville_formation}
                                    onChange={handleInputChange}
                                    options={collection.getVilleCollection()}
                                    error={errors.ville_formation}
                                />
                                <Controls.Select
                                    name="province_formation"
                                    label="province_formation"
                                     value={values.province_formation}
                                    onChange={handleInputChange}
                                    options={collection.getProvinceCollection()}
                                    error={errors.province_formation}
                                />
                            <Controls.DatePicker
                                    name="date_debut"
                                    label="Date de début"
                                    value={values.date_debut}
                                    onChange={handleInputChange}
                            />
                            <Controls.DatePicker
                                name="date_fin"
                                label="Date de Fin"
                                value={values.date_fin}
                                onChange={handleInputChange}
                           />

                        </Grid>

                            <div>
                                    <Controls.Button
                                        type="submit"
                                        text="Valider"
                                     />
                                    <Controls.Button
                                        text="Effacer"
                                        color="default"
                                        onClick={resetForm}
                                     />
                            </div>
                    </Grid>

        </Form>
    )
}

export default FormFormations
