import React,{ useState,useContext,useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import StarIcon from '@material-ui/icons/Star';
import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #008647'
  },
}));

export default function Interets() {

  const classes = useStyles();
  const {currentUser} = useContext(AuthContext);
  const db = app.firestore();
  const [interets, setInterets] = useState([]);


  const getInterets = async () => {
    db.collection("interets")
      .where("id_utilisateur", "==", currentUser.uid)
      .onSnapshot((querySnapshot) => {
          var List= []
          querySnapshot.forEach((doc) => {
            List.push({ ...doc.data(), id: doc.id })
          });
          setInterets(List)              
      });
 }

    useEffect(()=> {
        getInterets();
        
    }, [])


  return (
    <List component="nav" className={classes.root} aria-label="contacts">
     {
        interets.map((item,index) => {
        return(
        <ListItem  button>  
            <ListItemIcon>
            <StarIcon />
            </ListItemIcon>
            <ListItemText key={index} primary={item.interet} />
        </ListItem>)
        })
      }
    </List>
  );
}
