import React,{useState,useEffect,useContext}  from 'react'
import { MainContainer } from '../components/main/MainElements'
import MobileSidebar from '../components/MobileSidebar/Index'
import Sidebar from '../components/Sidebar/Index'
import Topnav from '../components/Topnav/Index'
import { Box, Container, Grid } from '@material-ui/core'
import {withRouter} from 'react-router-dom'
import app from '../../firebase/base';
import { AuthContext } from '../../firebase/AuthContext';
import CardCandidatures from '../../admin/components/Cards/CardCandidatures'
import CardOffre from '../../admin/components/Cards/CardOffre'






const DasboardR = () => {
    const [isOpen, setIsOpen] = useState(false)
    const {currentUser} = useContext(AuthContext);
    const db = app.firestore();

    const [offreTaille, setOffreTaille] = useState("")
    const [candidaturesTaille, setCandidaturesTaille] = useState("")

    const [offreEmplois, setOffreEmploi] = useState([])
    const [candidatures, setCandidatatures] = useState([])
 
    const toggle = () => {
        setIsOpen(!isOpen) 
    }


    const getOffre = async () => {
        await db.collection("offre_emploi")
                .where("id_utilisateur", "==", currentUser.uid)
                .onSnapshot((querySnapshot) => {
                    var listOffre = []
                    setOffreTaille(querySnapshot.size)
                    querySnapshot.forEach((doc) => {
                        listOffre.push({...doc.data(), id: doc.id })
                    });
                    setOffreEmploi(listOffre)              
                });
     }

     const getCandidats = async () => {
        await db.collection("candidatures")
                 .where("id_recruteur", "==", currentUser.uid)
                .onSnapshot((querySnapshot) => {
                    var listCand = []
                    setCandidaturesTaille(querySnapshot.size)
                    querySnapshot.forEach((doc) => {
                        listCand.push({...doc.data(), id: doc.id })
                    });
                    setCandidatatures(listCand)              
                });
     }
     useEffect(()=> {
       
       getOffre();
       getCandidats();
      
   }, [])
    return (
        <>
           <Sidebar/>
           <MainContainer>
               <MobileSidebar isOpen={isOpen}  toggle={toggle} />
               <Topnav toggle={toggle} />
              
            
               <Box
                mt={2}
                sx={{
                    backgroundColor: 'background.default',
                    minHeight: '100%',
                    py: 3
                  }}
               >
                   <Container maxWidth={false}>

                   <Grid
                        container
                        spacing={3}
                    >

                            <Grid
                                item
                                lg={3}
                                sm={6}
                                xl={3}
                                xs={12}
                            >
                                <CardCandidatures taille={candidaturesTaille} />
                            </Grid>
                            <Grid
                                item
                                lg={3}
                                sm={6}
                                xl={3}
                                xs={12}
                            >
                                <CardOffre taille={offreTaille} />
                            </Grid>
                           
                    </Grid>
                   </Container>
               </Box>
           </MainContainer>
        </>
    )
}

export default withRouter(DasboardR)
