import React, {useState,useContext, useEffect} from 'react'
import {
    Nav, 
    NavbarContainer, 
    NavLogo,
    MobileIcon,
    NavMenu,
    NavItem,
    NavBtn,
    NavBtnLink,
    NavBtnLogout,
    NavLinkR,
    LogoImg
           
} from './SecondElements'
import {FaBars} from 'react-icons/fa'
import {animateScroll as scroll} from 'react-scroll'
import { AuthContext } from '../../firebase/AuthContext';
import app from '../../firebase/base'
import Img from '../../assets/logo/new-logo-jobsgabon-1.png'

const SecondNavbar = ({toggle}) => {

    const [scrollNav, setScrollNav] = useState(false)
    const {currentUser, logout} = useContext(AuthContext);
    const [role, setRole] = useState("");
    const db = app.firestore();
    var user;

    
    const getRole = async () => {
        if(currentUser){ 
         await db.collection("utilisateurs")
          .where("id_utilisateur", "==", currentUser.uid)
          .onSnapshot((querySnapshot) => {
              querySnapshot.forEach((doc) => {
              user = doc.data().role_utilisateur;
              });
             setRole(user);  
          });        
          }else{
              console.log("Aucun utilisateur connecté")
          }
      } 
  

        

    const changeNav = () => { 
        if(window.scrollY >= 80) {
            setScrollNav(true)
        }
        else {
            setScrollNav(false)
        }
    }  

    useEffect(()=> {
        window.addEventListener('scroll', changeNav);
        getRole(); 
    }, [])

    const toggleHome = () => {
        scroll.scrollToTop();
    }

    return (
        <>
            <Nav scrollNav={scrollNav} >
                <NavbarContainer>
                        <NavLogo to="/" onClick={toggleHome} >
                               <LogoImg src={Img} alt="logo JOBSGABON" />
                        </NavLogo> 
                        <MobileIcon  onClick={toggle} >
                            <FaBars />
                        </MobileIcon>
                        <NavMenu>
                       
                          <NavItem>
                                <NavLinkR scrollNav={scrollNav}
                                to="/candidats"
                               
                                
                                >Candidats</NavLinkR>
                            </NavItem>
                            <NavItem>
                                <NavLinkR scrollNav={scrollNav}
                                to="/recruteur"
                                
                                
                                >Recruteurs</NavLinkR>
                            </NavItem>
                            <NavItem>
                                <NavLinkR scrollNav={scrollNav}
                                to="/candidats/emplois"
                                >
                                   Offres d'emploi
                                </NavLinkR>
                            </NavItem>
                           
                           
                        </NavMenu>
                        <NavBtn>
                        {!currentUser && 
                           (   
                            <NavBtnLink scrollNav={scrollNav} to="/connexion" >Se connecter</NavBtnLink>
                            )
                        }

                        {currentUser &&
                         (
                            <NavBtnLogout  onClick={logout} scrollNav={scrollNav} to="/connexion" >Déconnexion</NavBtnLogout>
                            )
                        }
                        <>
                        {(currentUser && role === "admin") &&
                            (                        
                            <NavBtnLink to="/admin/dashboard" scrollNav={scrollNav}>Admin</NavBtnLink>
                            )
                         }
                        </>
                        </NavBtn>
                </NavbarContainer>
            </Nav>
        </>
    )
}

export default SecondNavbar
