const BannerSection = () => {

 const classes = useStyles();
    const navigate = useHistory();
    const [jobSearch, setJobSearch] = useState({
        ville_offre : "",
        secteur_activite: ""
    })

    const handleChange = e => {
        e.persist();
        setJobSearch((oldState) => ({
           ...oldState,
           [e.target.name] : e.target.value,
        })) ;
       
    }

    const search = () => {
        navigate.push({pathname:"/candidats/emplois",state:{
            resultJobSearch: jobSearch
        }});
    }
   
    return ( 
        <> 
             <IconContext.Provider value={{ color:'1a1359', size: 20 }}>
            <HeroContainer >
                    <HeroBg>
                       {/* <VideoBg  autoPlay loop muted src={Video} type="video/mp4" />*/}
                        <HeroBgImg  src={Img} alt="busness woman"/>
                    </HeroBg>
                    <HeroContent>
                        <HeroH1>Trouvez des emplois et des opportunités de carrière</HeroH1>
                        <HeroP>
                        Simple, rapide et efficace!
                        </HeroP>
                    
                    <HeroBtnWrapper>

                    <DivInput>
                        <SpanSelect><ImLocation/></SpanSelect>
                     <Select
                      name="ville_offre"
                      disableUnderline
                      variant="outlined"
                      fullWidth
                      className={classes.selectStyle}
                      value={jobSearch.ville_offre}
                      onChange={handleChange}
                     >
                                {
                                    getVilleCollection().map(
                                        item => (<MenuItem key={item.id} value={item.title.toLocaleLowerCase()}>{item.title}</MenuItem>)
                                    ) 
                                }
                     </Select>
                     </DivInput>

                      <DivInput>
                        <Span><FaSearch/></Span>
                           <TextField
                                variant="outlined"
                                label="(ex:informatique)"
                                name="secteur_activite"
                                value={jobSearch.secteur_activite}
                                size="small"
                                className={classes.textField}
                                disableUnderline
                                onChange={handleChange}
                                
                             >

                             </TextField>
                      </DivInput>
                      <ButtonClick onClick={search} >
                          Rechercher
                      </ButtonClick>
                      {/*  <Button to='signup' 
                                onMouseEnter={onHover} 
                                onMouseLeave={onHover}
                                primary='true'
                                dark='true'
                                smooth={true}
                                duration={500} 
                                spy={true}
                                exact={true} 
                                offset={-80}
                                >
                                Rechercher {hover ? <ArrowForward/> : <ArrowRight/> }
                        </Button>*/}
                    </HeroBtnWrapper>
                    </HeroContent>
            </HeroContainer>
            </IconContext.Provider>
        </>
    )
}

export default BannerSection
