import { Box } from '@material-ui/core'
import React from 'react'
import styled from 'styled-components'



const H3 = styled.h3`
      font-size: 1rem;
      margin-bottom: 1rem;
      color: #1a1359;
      text-transform: uppercase;
      margin-bottom: 0.3rem;

      @media screen and (max-width: 950px) {
            font-size: 0.8rem !important;
      }
` 
const Progress = styled.div`
    height: .4rem;
    background: transparent;
    border-radius: 50px;
    margin-top: 0.1rem;
    position: relative;

    &:after {
        content: '';
        position: absolute;
        background: #008647;
        border-radius: 50px;
        height: .2rem;
        width: 0;
        animation: line 2s ease-out forwards;

        @keyframes line {
            to {
                width: 100%;
            }
        }
    }

`

const Years = styled.div`
        font-size: 10px;
        display: flex;
        justify-content: space-between;
        color: #696d6b;
`

const Span = styled.span`
      width: 36%;

      &:nth-child(2){
            text-align: center; 
            width: 20%;
          }
      &:nth-child(3){
            text-align: right;
            width: 44%;
        }

        
          
`

const ProgressBarre = (props) => {
   
    return (

         
              <div className={props.className}>
                <H3>{props.title}</H3>

               

               <Box mb={1}>
                    {
                        props.data.map((item,index) => {
                           
                            let progressBar = ((item.note * 100)/5)  + '%';
                           
                            return (
                              <>
                                <Years key={index}>
                                    <Span>Années d'expérience</Span>
                                    <Span>{item.annee_experience} an(s)</Span>
                                    <Span> {progressBar}%</Span>
                                </Years>
                                <div  className="languagesList">
                                    <li>{item.nom}</li>
                                    <Progress className="progresBar"  style={
                                        {width:progressBar}}></Progress>
                                </div>
                              </>
                            )
                        }) 
                    }
                </Box>
        </div>
    )
}

export default ProgressBarre
