import React, {useState,useEffect,useContext} from 'react'
import { Box,makeStyles, Grid, InputAdornment, Paper,TableCell,TableRow, Toolbar, TableBody } from '@material-ui/core'
import Controls from '../controls/Controls'
import {  PeopleOutlineTwoTone, Search } from '@material-ui/icons'
import AddIcon from '@material-ui/icons/Add';
import useTable from '../useTable';
import CloseIcon from '@material-ui/icons/Close';
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';
import { format } from 'date-fns'

 
import Popup from '../Popup';
import Notification from '../Notification';
import ConfirmDialog from '../ConfirmDialog';
import './table.css'
import PageHeader from '../PageHeader';
import UtilisateurForm from '../Forms/UtilisateurForm';
import { AuthContext } from '../../../firebase/AuthContext';


import app from '../../../firebase/base';
import appAdmin from '../../../firebase/base';



const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '50%',
    },
    newButton: {
        
        right: '10px',
        color: '#1a1359',
        borderColor: '#1a1359',
        justifyContent:'center',
        alignItems: 'center',
        display:'flex-start'
    },
  
}))

const headCells = [
    { id: 'nom_admin', label: 'Nom' },
    { id: 'prenom_admin', label: 'Prénom' },
    { id: 'email_admin', label: 'Email' },
    { id: 'role_admin', label: 'Rôle' },
    { id: 'date_debut', label: 'Date ajout' },
    { id: 'actions', label: 'Actions', disableSorting: true }
]


const TableUtilisateur = (props) => {
    const classes = useStyles();  

    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [records, setRecords] = useState([])
    const [openPopup, setOpenPopup] = useState(false)
    const [recordForEdit, setRecordForEdit] = useState(null)
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })
    const [role, setRole] = useState("");
    const {currentUser} = useContext(AuthContext);

    //const {currentUser} = useContext(AuthContext);
    const db = app.firestore(); 
    const listUser = [];
    var user;

 
    const {
        TblContainer, 
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records,headCells,filterFn);

    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.nom_admin.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchPrenom = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.prenom_admin.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchRole = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.role_admin.toLowerCase().includes(target.value))
            }
        })
    }
 
    const getAdmin = async () => {
        await db.collection("administration")
                .onSnapshot((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        listUser.push({...doc.data(), id: doc.id })
                    
                    });
                    setRecords(listUser)              
                });
      } 

    const addOrEdit = async (obj,resetForm) => { 
    try{ 
        if (obj.id == ""){
                await  appAdmin.auth().createUserWithEmailAndPassword(
                        obj.email_admin,
                        obj.password
                 ).then((res)=> {
                   // appAdmin.auth().signOut()
                    db.collection("administration").doc().set({
                        date_ajout : format(obj.date_ajout,"dd MMM yyyy"),
                        email_admin: obj.email_admin,
                        nom_admin: obj.nom_admin,
                        prenom_admin: obj.prenom_admin,
                        role_admin: obj.role_admin,
                        id_admin: res.user.uid
                     })

                     db.collection("utilisateurs").doc().set({
                        date_ajout :  format(obj.date_ajout,"dd MMM yyyy"),
                        email_utilisateur: obj.email_admin,
                        role_utilisateur: obj.role_admin,
                        id_utilisateur: res.user.uid
                    })
                  
                })
               
        }else{
            await db.collection("administration").doc(obj.id).update(obj)
        }
        resetForm()
        setRecordForEdit(null)
        setOpenPopup(false)
        getAdmin()
        setNotify({
                isOpen: true,
                message: 'Validation avec Succes',
                type: 'success'
          })
        }catch (error) {
        console.error(error);
      }
    }

    const openInPopup = item => {
        setRecordForEdit(item)
        setOpenPopup(true)
    }

    const onDelete = async (id) => {
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        })
        await db.collection("administration").doc(id).delete();
        getAdmin()
        setNotify({
            isOpen: true,
            message: 'Suppression avec Success',
            type: 'error'
        })
    }

        const getRole = async () => {
            db.collection("utilisateurs")
            .where("id_utilisateur", "==", currentUser.uid)
            .onSnapshot((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                user = doc.data().role_utilisateur;
                });
            setRole(user);
                
            });        
        } 


    useEffect(()=> {
        getAdmin();
        getRole();
    }, [])

    return (
        <Box p={2}>
                <PageHeader
                title="Gestion des utilisateurs"
                subTitle="Ajouter un utilisateur, modifier et supprimer"
                icon={<PeopleOutlineTwoTone fontSize="large" />}
              
             
            />
               <Paper  >
                <Toolbar>
                    <Grid container spacing={2}  xs={12} >
                        <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par nom"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearch}  
                            />
                       </Grid>
                       <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par prénom"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchPrenom}  
                            />
                       </Grid>
                       
                      <Grid item lg={4} md={6} xs={12} >
                             <Controls.Button 
                                text="Ajouter"
                               
                                variant="outlined"
                                startIcon={<AddIcon/>}
                                onClick={() => { setOpenPopup(true); setRecordForEdit(null); }}
                                className={classes.centerButton}
                                
                            />
                        </Grid>
                        <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par rôle"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchRole}  
                            />  
                       </Grid>
                    </Grid>
                </Toolbar>
                  
                  <Grid className="container">
                    <TblContainer>
                        <TblHead/>
                        <TableBody>
                        {
                                recordsAfterPagingAndSorting().map((item,index) =>
                                    (<TableRow key={index}>
                                        <TableCell>{item.nom_admin}</TableCell>
                                        <TableCell>{item.prenom_admin}</TableCell>
                                        <TableCell>{item.email_admin}</TableCell>
                                        <TableCell>{item.role_admin}</TableCell>
                                        <TableCell>{item.date_ajout}</TableCell>
                                        <TableCell>
                                           {role == "admin" &&(
                                            <Controls.ActionButton
                                                color="primary"
                                                onClick={() => { openInPopup(item) }}
                                              >
                                                <EditOutlinedIcon fontSize="small" /> 
                                            </Controls.ActionButton>
                                            )}
                                         {role == "admin" &&(
                                            <Controls.ActionButton
                                                color="secondary"
                                                onClick={() => {
                                                    setConfirmDialog({
                                                        isOpen: true,
                                                        title: 'êtes-vous sûr de vouloir supprimer cet enrégistrement?',
                                                        subTitle: "Vous pouvez annuler cette opération",
                                                        onConfirm: () => { onDelete(item.id) }
                                                    })
                                                }}
                                              >
                                                <CloseIcon fontSize="small" />
                                            </Controls.ActionButton>
                                            )}
                                        </TableCell>
                                    </TableRow>)
                                )
                            }
                        </TableBody>
                    </TblContainer>
                    <TblPagination />
                    </Grid>
             </Paper>
             <Popup
                  title="Formulaire d'utilisateurs"
                  openPopup={openPopup}
                  setOpenPopup={setOpenPopup}
             >
                 <UtilisateurForm
                   recordForEdit={recordForEdit}
                   addOrEdit={addOrEdit}
                 />
                 
             </Popup>
             <Notification
                notify={notify}
                setNotify={setNotify}
            />
             <ConfirmDialog
                confirmDialog={confirmDialog}
                setConfirmDialog={setConfirmDialog}
             />
        </Box>
    )
}

export default TableUtilisateur
