import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { Link } from 'react-router-dom';
import { useHistory} from 'react-router-dom';


const useStyles = makeStyles({
  root: {
    maxWidth: '100%',
    height: '420px'
  },
  textTitle: {
    fontSize:'15px',
    color: '#1a1359',

   ' &:hover':{
      color: '#008647',
    },

    
  },
  
  textLink: {
    color: '#1a1359',

   ' &:hover':{
      color: '#008647',
    },
    
  },
  
});


const CardOne = (props) => {

  const classes = useStyles();
  const navigate = useHistory();
  
  return (

   
      <Card className={classes.root}>
        <CardActionArea>
          <CardMedia
            component="img"
            alt={props.description_image}
            height="200"
            image={props.image_poste}
            title={props.titre_poste}
          />
          <CardContent>
            <Typography gutterBottom variant="h6" component="h2" className={classes.textTitle}  >
                {props.titre_poste}
            </Typography>
            <Typography gutterBottom variant="subtitle2" color="textSecondary" component="span">
             
            </Typography>
            <Typography variant="body2" align="justify" color="textPrimary" component="p">
              {props.contenu.substring(0, 200)}
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions>
        
            <Button onClick={()=>navigate.push(`/candidats/single-conseil/${props.titre_poste}`)} size="small" className={classes.textLink} color="primary">
              Lire plus
            </Button>
         
        </CardActions>
      </Card>
  
    
  )
}

export default CardOne
