import React, {useCallback} from 'react'
import { Link as RouterLink, withRouter} from 'react-router-dom';
import { 
    Paper,
    makeStyles,
    Container,
    Box,
    Grid,
    Link,
    TextField,
    Typography,
    Button,
    Checkbox,
    FormHelperText,
} from '@material-ui/core'
import { Formik } from 'formik';
import * as Yup from 'yup';
import app from '../../../firebase/base';

const useStyles = makeStyles((theme) => ({
    root: {
      backgroundColor: theme.palette.background.dark,
      height: '100%',
      paddingBottom: theme.spacing(3),
      paddingTop: theme.spacing(3)
    },
    textPrimary:{
        color: '#008647',
      },

      buttonStyle: {
        backgroundColor: "#1a1359",
        color: "#fff",
      
        "&:hover":{
            backgroundColor: "#008649",
        }
    }
  }));

const SignupFormR = ({history}) => {

    const classes = useStyles();
  
    const handleSignup = useCallback(async event => {
        event.preventDefault();
        const {email,password} = event.target.elements;
        try{
            await app
                .auth()
                .createUserWithEmailAndPassword(email.value, password.value);
                history.push("/recruteur/dashbord/inscription");
        } catch (error) {
            alert(error);
        }
    }, [history])

    return (
        <>
            <Paper className={classes.root}>
            <Box
                display="flex"
                flexDirection="column"
                height="100%"
                justifyContent="center"
            >
                <Container maxWidth="sm">
                <Formik
                    initialValues={{
                    email: '',
                    password: '',
                    policy: false
                    }}
                    validationSchema={
                    Yup.object().shape({
                        email: Yup.string().email('Ajouter un email valide').max(255).required('Email requis'),
                        password: Yup.string().max(255).required('Votre mot de passe est requis'),
                        policy: Yup.boolean().oneOf([true], 'Vous devez acceptez les termes et conditions')
                    })
                    }
                    onSubmit={() => {
                       console.log('Auth')
                     
                    }}
                >
                    {({
                    errors,
                    handleBlur,
                    handleChange,
                    handleSubmit,
                    isSubmitting,
                    touched,
                    values
                    }) => (
                    <form onSubmit={handleSignup}>
                        <Box mb={3}>
                        <Typography
                            color="textPrimary"
                            variant="h4"
                        >
                            Créer un nouveau compte recruteur
                        </Typography>
                        <Typography
                            color="textSecondary"
                            gutterBottom
                            variant="body2"
                        >
                            Utiliser votre email pour créer un compte
                        </Typography>
                        </Box>
                    
                    <Grid
                        container
                        spacing={3}
                    >
                    
                        <Grid container>
                            <TextField
                            error={Boolean(touched.email && errors.email)}
                            fullWidth
                            helperText={touched.email && errors.email}
                            label="Email"
                            margin="normal"
                            name="email"
                            onBlur={handleBlur}
                            onChange={handleChange}
                            type="email"
                            value={values.email}
                            variant="outlined"
                            required
                            />
                      
                                <TextField
                                error={Boolean(touched.password && errors.password)}
                                fullWidth
                                helperText={touched.password && errors.password}
                                label="Mot de passe"
                                margin="normal"
                                name="password"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                type="password"
                                value={values.password}
                                variant="outlined"
                                required
                                />
                            
                            </Grid>
                        </Grid>
                        <Box
                        alignItems="center"
                        display="flex"
                        ml={-1}
                        >
                        <Checkbox
                            checked={values.policy}
                            name="policy"
                            onChange={handleChange}
                        />
                        <Typography
                            color="textSecondary"
                            variant="body1"
                        >
                            J'ai lu les
                            {' '}
                            <Link
                            color="primary"
                            component={RouterLink}
                            to="#"
                            underline="always"
                            variant="h6"
                            className={classes.textPrimary}
                            >
                            Termes et Conditions
                            </Link>
                        </Typography>
                        </Box>
                        {Boolean(touched.policy && errors.policy) && (
                        <FormHelperText error>
                            {errors.policy}
                        </FormHelperText>
                        )}
                        <Box my={2}>
                        <Button
                            className={classes.buttonStyle}
                            disabled={isSubmitting}
                            fullWidth
                            size="large"
                            type="submit"
                            variant="contained"
                        >
                            S&apos;inscrire maintenant
                        </Button>
                        </Box>
                        <Typography
                        color="textSecondary"
                        variant="body1"
                        >
                        Vous avez un compte?
                        {' '}
                        <Link
                            component={RouterLink}
                            to="/recruteur/connexion"
                            variant="h6"
                            className={classes.textPrimary}
                        >
                            Se connecter
                        </Link>
                        </Typography>
                    </form>
                    )}
                </Formik>
                </Container>
            </Box>
           
            </Paper>
        </>
    )
}

export default withRouter(SignupFormR)
