import React,{useEffect,useContext} from 'react'
import {Grid} from '@material-ui/core'
import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import { AuthContext } from '../../../firebase/AuthContext';
import styled from 'styled-components'



const BlockCache = styled.div`
 display:none;
`;


const FormReseaux = (props) => {
   
    const {addOrEdit, recordForEdit} = props
    const {currentUser} = useContext(AuthContext);

    const initialValues = {
        id:'',
        facebook: '',
        id_utilisateur:currentUser.uid,
        twitter:'',
        instagram: '',
        linkedin: '',
        github: ''
    }
    

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('facebook' in fieldValues)
            temp.facebook = fieldValues.facebook ? "" : "Champ requis."
        if ('id_utilisateur' in fieldValues)
            temp.id_utilisateur = fieldValues.id_utilisateur ? "" : "Champ requis."
        if ('twitter' in fieldValues)
            temp.twitter = fieldValues.twitter ? "" : "Champ requis."
        if ('instagram' in fieldValues)
            temp.instagram = fieldValues.instagram ? "" : "Champ requis."
        if ('linkedin' in fieldValues)
            temp.linkedin = fieldValues.linkedin ? "" : "Champ requis."
        if ('github' in fieldValues)
            temp.github = fieldValues.github ? "" : "Champ requis."
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    }= useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit,setValues])

    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >
           
                <Grid container >
                       <Grid item md={6} xs={6}>
                                <Controls.Input
                                    name="facebook"
                                    label="facebook"
                                    value={values.facebook}
                                    onChange={handleInputChange}
                                    error={errors.facebook}
                                />  
                             
                                
                                <Controls.Input
                                    name="twitter"
                                    label="twitter"
                                    value={values.twitter}
                                    onChange={handleInputChange}
                                    error={errors.twitter}
                                />
                              
                             <BlockCache>
                                 <Controls.Input
                                        name="id_utilisateur"
                                        label="Identifiant"
                                        value={values.id_utilisateur}
                                        onChange={handleInputChange} 
                                        error={errors.id_utilisateur}
                                       
                                /> 
                            </BlockCache>
                        
                            </Grid>
                            <Grid item md={6} xs={6}>
                              
                            <Controls.Input
                                    name="instagram"
                                    label="instagram"
                                    value={values.instagram}
                                    onChange={handleInputChange}
                                    error={errors.instagram}
                                />
                          

                                <Controls.Input
                                    name="linkedin"
                                    label="linkedin"
                                    value={values.linkedin}
                                    onChange={handleInputChange}
                                    error={errors.linkedin}
                                />
                                 <Controls.Input
                                    name="github"
                                    label="github"
                                    value={values.github}
                                    onChange={handleInputChange}
                                    error={errors.github}
                                />
                              
                        </Grid>
                                 <div>
                                    <Controls.Button 
                                        type="submit"
                                        text="Valider"
                                     />
                                    <Controls.Button
                                        text="Effacer"
                                        color="default"
                                        onClick={resetForm}
                                     />
                                  </div>
                    </Grid>
               
        </Form>
    )
} 

export default FormReseaux
