import { Avatar, Grid ,  makeStyles, } from '@material-ui/core'
import React, {useState, useEffect }  from 'react'
import Controls from '../controls/Controls'
import { Form, useForm } from '../useForm'
import {getVisibility,getPubType} from '../../data/collection'




const useStyles = makeStyles((theme)=> ({
  
    avatar:{
        height:100,
        width:100

    }
}));

const initialValues = {
    id: '',
    titre: '',
    description_image: '',
    lien_site: '',
    lien_image: '',
    visibilite:'',
    categorie:'',
  
}



const PubliciteForm = (props) => {

    const {addOrEdit, recordForEdit} = props
    const classes = useStyles();
    const [file, setFile] = useState(null);
    const [error, setError] = useState('');
    const types = ['image/png', 'image/jpeg']; // image types

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('titre' in fieldValues)
            temp.titre = fieldValues.titre ? "" : "Champ requis."
        if ('description' in fieldValues)
            temp.description = fieldValues.description ? "" : "Champ requis."
        if ('visibilite' in fieldValues)
            temp.visibilite = fieldValues.visibilite.length !== 0 ? "" : "Champ requis."
        if ('categorie' in fieldValues)
            temp.categorie = fieldValues.categorie.length !== 0 ? "" : "Champ requis."
       
      
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    } =useForm(initialValues, true, validate);

    const handleInputFileChange = (e) => {
        let selectedFile = e.target.files[0];
        if (selectedFile && types.includes(selectedFile.type)) {
            setFile(selectedFile);
            setError('')
        }
        else { 
            setFile(null);
            alert('Sélectionner une image valide de type (jpg, jpeg ou png)');
        }
    }

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values,file, resetForm);
        }
    }

   

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit])

    return (
        <Form  onSubmit={handleSubmit}>
                <Grid container spacing={2}>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="titre"
                              label="titre"
                              value={values.titre}
                              onChange={handleInputChange}
                              error={errors.titre}
                        />
                         <Controls.Input
                              name="description_image"
                              label="description de l'image"
                              value={values.description_image}
                              onChange={handleInputChange}
                              error={errors.description_image}
                        />
                       
                         <Controls.Input
                              name="lien_site"
                              label="adresse du site associée à cette pub"
                              value={values.lien_site}
                              onChange={handleInputChange}
                              error={errors.lien_site}
                        />
                        
                    </Grid>
                   
                    <Grid item xs={6}> 
                       
                       
                      <Controls.InputFile
                                    name="image_poste"
                                    label="Télécharger une photo"
                                    onChange={handleInputFileChange}
                                    error={errors.image_poste}
                                    
                       />
                        <Controls.Select
                            name="categorie"
                            label="categorie"
                            value={values.categorie}
                            onChange={handleInputChange}
                            options={getPubType()}
                            error={errors.categorie}
                        />
                      
                         <Controls.Select
                            name="visibilite"
                            label="visibilité"
                            value={values.visibilite}
                            onChange={handleInputChange}
                            options={getVisibility()}
                            error={errors.visibilite}
                        />

                    </Grid>
                    
                    <Grid item xs={6}> 
                          <Avatar
                            className={classes.avatar}
                            src={values.lien_image} 
                          />
                    </Grid>
                  
                    <div>
                        <Controls.Button
                            type="submit"
                            text="Valider" />
                        <Controls.Button
                            text="Effacer"
                            color="default"
                            onClick={resetForm} />
                    </div>
                </Grid>
        </Form>
    )
}

export default PubliciteForm
