import React, {useCallback} from 'react'
import { Link as RouterLink, withRouter} from 'react-router-dom';
import { 
    Paper,
    makeStyles,
    Container,
    Box,
    Grid,
    Link,
    TextField,
    Typography,
    Button
} from '@material-ui/core'
import { Formik } from 'formik';
import * as Yup from 'yup';
import FacebookIcon from '../../../assets/icons/Facebook';
import GoogleIcon from '../../../assets/icons/Google';
import app from '../../../firebase/base';


const useStyles = makeStyles((theme) => ({
    root: {
      backgroundColor: theme.palette.background.dark,
      height: '100%',
      paddingBottom: theme.spacing(2),
      paddingTop: theme.spacing(2),
     
    } ,
    textPrimary:{
        color: '#008647',
      },

      textSecondary:{
        color: '#1a1359',
      },

      buttonStyle: {
        backgroundColor: "#1a1359",
        color: "#fff",
      
        "&:hover":{
            backgroundColor: "#008649",
        }
    },
    buttonFbk: {
        backgroundColor: "#1a1359",
        color: "#fff",
      
        "&:hover":{
            backgroundColor: "#1a1359",
        }
    }
  }));

const LoginForm = ({history}) => {

    const classes = useStyles();


    const handleLogin = useCallback(
        async event => {
        event.preventDefault();
        const {email,password} = event.target.elements;
        try{
            await app.auth().signInWithEmailAndPassword(email.value, password.value);
                history.push("/recruteur");
                 
        } catch (error) {
            alert(error);
        }
    },
     [history]
    );

 

 /*   const submitHandler = e => {
        e.preventDefault();

        
            navigate.push('/candidats/accueil', { replace: true });
      
    }*/




    return (
        <>
            <Paper className={classes.root}>
                <Box
                    display="flex"
                    flexDirection="column"
                    height="100%"
                    justifyContent="center"
                >
                 <Container maxWidth="sm">
                        <Formik
                            initialValues={{
                                email: '',
                                password: ''
                              }}

                            validationSchema={Yup.object().shape({
                                email: Yup.string().email('Vous devez entrer un email valid').max(255).required('Email requis'),
                                password: Yup.string().max(255).required('Mot de passe  requis')
                            })}

                            onSubmit={() => {
                                console.log('Auth')
                              
                             }}
                        >

                            {({
                                errors,
                                handleBlur,
                                handleChange,
                                handleSubmit,
                                isSubmitting,
                                touched,
                                values
                                }) => (

                                <form onSubmit={handleLogin}>
                                      <Box mb={3}>
                                            <Typography
                                                color="textPrimary"
                                                variant="h4"
                                            >
                                                Se connecter
                                            </Typography>
                                            <Typography
                                                color="textSecondary"
                                                gutterBottom
                                                variant="body2"
                                            >
                                                Se connecter à votre compte recruteur
                                            </Typography>
                                            </Box>
                                            <Grid
                                            container
                                            spacing={3}
                                            >
                                            <Grid
                                                item
                                                xs={12}
                                                md={6}
                                            >
                                                <Button
                                                className={classes.buttonFbk}
                                                fullWidth
                                                startIcon={<FacebookIcon />}
                                                onClick={handleSubmit}
                                                size="large"
                                                variant="contained"
                                                >
                                                Se connecter avec Facebook
                                                </Button>
                                            </Grid>
                                            <Grid
                                                item
                                                xs={12}
                                                md={6}
                                            >
                                                <Button
                                                fullWidth
                                                startIcon={<GoogleIcon />}
                                                onClick={handleSubmit}
                                                size="large"
                                                variant="contained"
                                                >
                                                Se conecter avec  Google
                                                </Button>
                                            </Grid>
                                            </Grid>
                                            <Box
                                            mt={3}
                                            mb={1}
                                            >
                                          
                                            <Typography
                                                align="center"
                                                color="textSecondary"
                                                variant="body1"
                                            >
                                                login ou adresse email
                                            </Typography>
                                            </Box>
                                            <Grid
                                               container
                                           
                                            >
                                            <TextField
                                            error={Boolean(touched.email && errors.email)}
                                            fullWidth
                                            helperText={touched.email && errors.email}
                                            label="Adresse Email"
                                            margin="normal"
                                            name="email"
                                            onBlur={handleBlur}
                                            onChange={handleChange}
                                            type="email"
                                            value={values.email}
                                            variant="outlined"
                                            required
                                            />
                                            <TextField
                                            error={Boolean(touched.password && errors.password)}
                                            fullWidth
                                            helperText={touched.password && errors.password}
                                            label="Mot de passe"
                                            margin="normal"
                                            name="password"
                                            onBlur={handleBlur}
                                            onChange={handleChange}
                                            type="password"
                                            value={values.password}
                                            variant="outlined"
                                            required
                                            />
                                            </Grid>
                                            <Box my={2}>
                                            <Grid container spacing={1}>
                                                <Grid item lg={6}>
                                                <Button
                                                        className={classes.buttonStyle}
                                                        disabled={isSubmitting}
                                                        fullWidth
                                                        size="large"
                                                        type="submit"
                                                        variant="contained"
                                                    >
                                                        Se connecter
                                                    </Button>
                                                </Grid>
                                                <Grid item lg={6}>
                                                <Typography>
                                                    <Link
                                                        component={RouterLink}
                                                        to="/mot-de-passe-oublier"
                                                        variant="p"
                                                        className={classes.textSecondary}
                                                    >
                                                        Mot de passe oublié ?
                                                    </Link>
                                                    </Typography>
                                                </Grid>
                                            </Grid>
                                           
                                            </Box>
                                            <Typography
                                            color="textSecondary"
                                            variant="body1"
                                            >
                                            &apos;Pas de compte?
                                            {' '}
                                            <Link
                                                component={RouterLink}
                                                to="/recruteur/signup"
                                                variant="p"
                                                className={classes.textPrimary}
                                            >
                                               Créer un compte recruteur
                                            </Link>
                                            </Typography>
                                </form>
                            )}

                        </Formik>
                 </Container>

                </Box>
            </Paper>
        </>
    )
}

export default withRouter(LoginForm)
