import React,{useState,useEffect,useContext} from 'react'
import { School } from '@material-ui/icons'
import PageHeader from '../PageHeader'
import { makeStyles, TableBody, TableRow, TableCell, Toolbar, Grid } from '@material-ui/core';
import useTable from "../../components/useTable";
import Controls from "../controls/Controls";
import AddIcon from '@material-ui/icons/Add';
import Popup from "../Popup";
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';
import CloseIcon from '@material-ui/icons/Close';
import Notification from "../Notification";
import ConfirmDialog from "../ConfirmDialog";
import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';
import '../Candidatures/Candidatures.css'
import FormFormations from './FormFormations';
import { format } from 'date-fns'



const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '75%'
    },
    newButton: {
        position: 'absolute',
        right: '10px'
    }
}))

const headCells = [
    { id: 'nom', label: 'nom_formation' },
    { id: 'etablissement', label: 'etablissement' },
    { id: 'discipline', label: 'discipline' },
    { id: 'niveau_atteint', label: 'niveau_atteint' },
    { id: 'ville_formation', label: 'ville' },
    { id: 'province_formation', label: 'Province' },
    { id: 'date_debut', label: 'date_debut' },
    { id: 'date_fin', label: "date_fin" },
    { id: 'actions', label: 'Actions', disableSorting: true }
]   


const TableFormations = () => {

    const classes = useStyles();
    const [openPopup, setOpenPopup] = useState(false)
    const [recordForEdit, setRecordForEdit] = useState(null)
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })
    const [records, setRecords] = useState([]);
    const {currentUser} = useContext(AuthContext);
    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const db = app.firestore();
    const listFormations = [];
   
    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records, headCells, filterFn);
   
    /**
        startOn:doc.data().date_debut.toDate(),
        endOn:doc.data().date_fin.toDate()
     */

    const getFormations = async () => {
       await db.collection("formations")
          .where("id_utilisateur", "==", currentUser.uid)
          .onSnapshot((querySnapshot) => {
              querySnapshot.forEach((doc) => {
                  listFormations.push({...doc.data(), id: doc.id })
             
              });
              setRecords(listFormations)              
          });
     }


    useEffect(()=> {
    getFormations();
     }, [])




     const addOrEdit = async (obj,resetForm) => {
         console.log(obj,"Objet")
        try {

            if (obj.id == "")
                await db.collection("formations").doc().set(obj);
            else
                await db.collection("formations").doc(obj.id).update(obj);
            resetForm()
            setRecordForEdit(null)
            setOpenPopup(false) 
            getFormations()
            setNotify({
                isOpen: true,
                message: 'Validation avec Succes',
                type: 'success'
            })
            

        } catch (error) {
            console.error(error);
          }
       }

           
      const onDelete = async (id)=> {
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        })
        await db.collection("formations").doc(id).delete();
        getFormations()
        setNotify({
            isOpen: true,
            message: 'Suppression avec Success',
            type: 'error'
        })
    }

        const openInPopup = item => {
        setRecordForEdit(item)
        setOpenPopup(true)
        }




      

    return (
        <>
            <PageHeader   
            title="Rubrique Formation"
            subTitle="Ajouter, modifier et supprimer des formations dans votre CV"
            icon={<School fontSize="large"/>}
           /> 
           <Toolbar>
                <Controls.Button
                        text="Ajouter"
                        variant="outlined"
                        startIcon={<AddIcon />}
                        className={classes.newButton}
                        onClick={() => { setOpenPopup(true); setRecordForEdit(null); }}
                />
           </Toolbar>
           <Grid className="container">
           <TblContainer>
                <TblHead />
                 <TableBody> 
                        {
                            recordsAfterPagingAndSorting().map((item,index) =>
                                (    <TableRow key={index}>
                                    <TableCell>{item.nom}</TableCell>
                                    <TableCell>{item.etablissement}</TableCell>
                                    <TableCell>{item.discipline}</TableCell>
                                    <TableCell>{item.niveau_atteint}</TableCell>
                                    <TableCell>{item.ville_formation}</TableCell>
                                    <TableCell>{item.province_formation}</TableCell>
                                    <TableCell>{item.date_debut}</TableCell>
                                    <TableCell>{item.date_fin}</TableCell>

                                   {/* <TableCell>{ format(item.startOn,"dd/MM/yyy")}</TableCell>
                                    <TableCell>{format(item.endOn,"dd/MM/yyy")}</TableCell>*/}
                                    <TableCell>
                                        <Controls.ActionButton
                                            color="primary"
                                            onClick={() => {openInPopup(item)}} 
                                             >
                                            <EditOutlinedIcon fontSize="small" />
                                        </Controls.ActionButton>
                                        <Controls.ActionButton
                                            color="secondary"
                                            onClick={() => {
                                                setConfirmDialog({
                                                    isOpen: true,
                                                    title: 'êtes-vous sûr de vouloir supprimer cet enrégistrement?',
                                                    subTitle: "Vous pouvez annuler cette opération",
                                                    onConfirm: () => {onDelete(item.id) }
                                                })
                                            }}
                                           >
                                            <CloseIcon fontSize="small" />
                                        </Controls.ActionButton>
                                    </TableCell>
                                </TableRow>)
                            )
                        }
                    </TableBody>
                </TblContainer>
                <TblPagination />
                </Grid>
                <Popup
                    title="Formulaire des formations"
                    openPopup={openPopup}
                    setOpenPopup={setOpenPopup}
                >
                    <FormFormations 
                         recordForEdit={recordForEdit}
                         addOrEdit={addOrEdit}
                    />
                </Popup>
                <Notification
                    notify={notify}
                    setNotify={setNotify}
                />
                <ConfirmDialog
                    confirmDialog={confirmDialog}
                    setConfirmDialog={setConfirmDialog}
                />
        </>
    )
}

export default TableFormations
