import React,{useState,useEffect,useContext} from 'react'
import styled from 'styled-components'
import ProfilForm from '../components/Moncompte/ProfilForm'
import Sidebar from '../components/Sidebar/Index'
import {withRouter} from 'react-router-dom'
import MobileSidebar from '../components/Sidebar/MobileSidebar'
import app from '../../firebase/base';
import { AuthContext } from '../../firebase/AuthContext';
import {
    Avatar,
    Box,
    Card,
    CardContent,
    makeStyles,
    Container, Grid
} from '@material-ui/core'
import Controls from '../components/controls/Controls';
import { EditOutlined } from '@material-ui/icons';
import { toast } from "react-toastify";
import CardProfil from '../components/Cards/CardProfil'


const useStyles = makeStyles((theme)=> ({
    root:{

        paddingBottom: theme.spacing(3),
        paddingTop: theme.spacing(3),
        textAlign:'left'

    },
    avatar:{
        height:100,
        width:100,
        marginBottom:'10px'

    },
    buttonStyle:{
        backgroundColor: "#fff",
        color: "#1a1359"
    }
}));

const CompteSection = styled.div`
 display:flex;
@media screen and (max-width: 850px){
    display:block;
  }

`

const CompteContent = styled.div`

    margin-left: 10px;
    background-color: #F8F8F8;
    padding: 10px;
    position:relative;
    margin-left:260px;

    @media screen and (max-width: 1125px){
        margin-left: 0 !important;
        margin-left:0 !important;
        margin-top: 50px;
    }

`





const MonCompte = () => {


    const classes = useStyles();
    const {currentUser} = useContext(AuthContext);
    const [candidatObjects, setCandidatObjects] = useState([])
    const [currentID, setCurrentID] = useState("")
    const db = app.firestore();
    const storage = app.storage();

    const [isOpen, setIsOpen] = useState(false)
    const toggle = () =>{
        setIsOpen(!isOpen)
    }

    const getData = async () => {
      await  db.collection("candidats")
            .where("id_utilisateur", "==", currentUser.uid)
            .onSnapshot((querySnapshot) => {
                var list = [];
                querySnapshot.forEach((doc) => {
                    list.push({ ...doc.data(), id: doc.id })
                });
                setCandidatObjects(list)
                
            });
        }

        useEffect(()=> {
            getData();
        }, [])


    const addOrEdit = async (obj,file) => {

          if(file !== null){
            const uploadTask = storage.ref(`user-image/${file.name}`).put(file);
            uploadTask.on('state_changed', snapshot => {
                const progress = (snapshot.bytesTransferred/snapshot.totalBytes) * 100;
                console.log(progress);

            }, err => {
                console.log(err.message)
            }, ()=>{
                storage.ref('user-image').child(file.name).getDownloadURL().then(url => {
                db.collection('candidats').doc(currentID).update({
                        nom_candidat: obj.nom_candidat,
                        prenom_candidat:obj.prenom_candidat,
                        email_candidat: obj.email_candidat,
                        telephone_candidat: obj.telephone_candidat,
                        ville_candidat:obj.ville_candidat,
                        province_candidat: obj.province_candidat,
                        situation_candidat: obj.situation_candidat,
                        facebook:obj.facebook,
                        linkedin:obj.linkedin,
                        twitter:obj.twitter,
                        github:obj.github,
                        image_url: url
                    })
                }).then(()=>{
                    toast("Profil mis à jour avec succès!!!", {
                        type: "info",
                    });
                    setCurrentID("");
                })
            })

            }else{
                await db.collection("candidats").doc(currentID).update(obj);
                toast("Informations ajoutées avec succès", {
                    type: "info",
                    });
                setCurrentID("");
            }

    }





    return (
        <>
        <CompteSection>
            <MobileSidebar  isOpen={isOpen}  toggle={toggle}/>
            <Sidebar toggle={toggle}/>
            <CompteContent>
                <Container  maxWidth="lg">
                        <Grid container spacing={3}>
                            <Grid item lg={4} md={6} xs={12}>
                                 <Card
                                        className={classes.root}
                                    >

                                 <CardContent >
                                    {
                                        candidatObjects.map((item,index) => (

                                            <Box
                                                alignItems="center"
                                                display="flex"
                                                flexDirection="column"

                                            >
                                            <Avatar
                                                className={classes.avatar}
                                                src={item.image_url}
                                                key={index}
                                            />
                                                   <CardProfil  item={item} />
                                                    <Box>
                                                        <Controls.ActionButton
                                                            color="primary"
                                                            onClick={() => {setCurrentID(item.id)}}
                                                        >
                                                            <EditOutlined fontSize="medium"/>Editer
                                                            </Controls.ActionButton>
                                                     </Box>
                                            </Box>
                                            ))
                                          }

                                    </CardContent>


                                    </Card>
                            </Grid>

                            <Grid item lg={8} md={6} xs={12} >
                                <ProfilForm
                                   {...{addOrEdit,currentID,candidatObjects}}
                                />
                            </Grid>
                        </Grid>
                </Container>
            </CompteContent>
        </CompteSection>
        </>
    )
}

export default withRouter(MonCompte);
