import React, { Component } from 'react';


import styled from 'styled-components'

const ProjectContent = styled.div`
    background: #f8f5f5;
    border: 3px solid #008647;
    margin: 0.5rem 0 0 0;
    text-align: center;
    width: 32%;
    max-height: 50%;
    padding: .3rem;
    box-shadow: 0 1px 3px #333;
    display: grid;
    grid-template-rows: 15% 15% 55% 15%;
    grid-template-areas: "1" "2" "3" "4";

    @media (max-width: 650px) and (min-width: 521px){
        width: 30% !important;
    }

    @media (max-width: 520px){
        grid-template-rows: 30px 40px 80px 40px !important;
        width: 98% !important;
     }
`
const ProjectIcons = styled.div`
    grid-area: 1;
    display: flex;
    justify-content: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:#008647;
   
`
const ProjectI = styled.i`
    font-size: 1.4rem;
    padding: 0 .35rem;
    color: #ffffff;
    text-shadow: 0 0px 1px #fff;

    @media (max-width: 650px) and (min-width: 521px){
        font-size: 1rem;
    }
`
const ProjectII = styled.i`
     @media (max-width: 650px) and (min-width: 521px){
        font-size: 1rem;
    }
`
const ProjectH3 = styled.h3`
      grid-area: 2;
      font-size: 1.65rem;
      justify-content: left;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #1a1359;

`
const ProjectImg = styled.img`
      grid-area: 3;
      object-fit: cover;
      margin: 0 auto;
      width: 100%;
      height: 100%;
      box-shadow: 0 2px 6px #333;
      border-radius: 4px;
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;

      @media (max-width: 520px){
        height: 100% !important;
     }

`
const ProjectSpan = styled.span`
      grid-area: 4;
      color:#008647;
      cursor: pointer;
      font-size: 1.5rem;
      display: flex;
      justify-content: center;
      align-items: center; 
      transition: .2s;

      &:hover {
        transform: scale(1.1);
      }
`
const ShowInfo = styled.div`
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: rgba(0, 0, 0, 0.507);
      animation: popup .3s ease forwards ;
      transform-origin: center;
`
const InfoContent = styled.div`
        position: relative;
        top: 25%;
        width: 600px;
        margin: 0 auto;
        color: #fff;
        background:  #008647;
        padding: 2rem;
        box-shadow: 0 10px 10px #333;
        animation: popup .6s ease forwards;
        transform: scale(0);
        transform-origin: center;

        @keyframes popup {
          to {
            transform: scale(1);
          } 
        }

    @media (max-width: 650px){
            width: 90% !important;
    }
`
const Head = styled.div`
        display: flex;
        justify-content: space-around;
        margin: 1rem 0;

`
const HeadH2 = styled.h2`
        text-align: left;
        margin-bottom: 1rem;
`
const ButtonInfo = styled.div`
        cursor: pointer;
        color: #fff;
        background: #1a1359 ;
        padding: 1rem;
        transition: .2s;
        margin: 1rem auto;
        width: 200px;

        &:hover {
          background: #fbf000;
          color:#1a1359;
        }
`
const TextInfo = styled.div` 
    text-align: justify;
    padding-bottom: 1rem;
`
const SourceCode = styled.div``

const LinkA = styled.a`
        cursor: pointer;
        color: #fff;
        background: #1a1359;
        padding: 1rem;
        transition: .2s;
        text-decoration:none;

`

class Project extends Component {
    state= {
        showInfo:false
    }

    handleInfo = () => {
        this.setState({
            showInfo:!this.state.showInfo
        })
    }
    render() {
        const {name,languagesIcons, source, info, picture}= this.props.item;
        return (
            
            <ProjectContent className="project">
                <ProjectIcons className="icons">
                    {
                        languagesIcons.map(icon =>
                            <ProjectI  key={icon} className={icon}></ProjectI>
                        )
                    }
                </ProjectIcons>
                <ProjectH3>{name}</ProjectH3>
                <ProjectImg src={picture} alt="infos"  onClick={this.handleInfo} />
                <ProjectSpan  onClick={this.handleInfo}>
                    <ProjectII className="fas fa-plus-circle"></ProjectII>
                </ProjectSpan>


                    {
                        this.state.showInfo && (
                            <ShowInfo>
                                <InfoContent>
                                    <Head>
                                        <HeadH2>{name}</HeadH2>
                                        <SourceCode>
                                            <LinkA href={source} rel="noopener noreferrer" target="_blank" >
                                                Code source
                                            </LinkA>
                                        </SourceCode>
                                    </Head>

                                    <TextInfo>{info}</TextInfo>
                                    <ButtonInfo onClick={this.handleInfo} >
                                        Retourner à la page
                                    </ButtonInfo>
                                </InfoContent>
                            </ShowInfo>
                        )
                    }


            </ProjectContent>
        );
    }
}

export default Project;
