import React,{useState,useEffect,useContext} from 'react'
import {
    Container,
    ProfilContainer,
    ProfileImg,
    ProfileName,
    MenuContainer,
    ContainerLink,
    SpanLink,
    TitleLink
} from './SidebarElements'
import {FaUsers} from 'react-icons/fa'
import{AiOutlineUser} from 'react-icons/ai'
import {MdLocalOffer} from 'react-icons/md'
import {IconButton,makeStyles, Tooltip } from '@material-ui/core'
import {  Dashboard, ExitToApp, Folder, Home } from '@material-ui/icons'
import app from '../../../firebase/base'
import { AuthContext } from '../../../firebase/AuthContext'



const useStyles = makeStyles((theme) => ({
  margin: {
     color: '#fff',
     alignItems: 'center',
     justifyContent: 'center',
     padding: '0 0.2rem'
  },
  logoutText:{ 
      color: '#fff'
  }
 
}));


const items = [
    {
      href: '/recruteur/',
      icon: <Home/>,
      title: 'Recruteur'
    },
    {
      href: '/recruteur/dashboard',
      icon: <Dashboard/>,
      title: 'Tableau de bord'
    },
  
    {
      href: '/recruteur/dashbord/offresemploi',
      icon: <MdLocalOffer/>,
      title: "Offres d'emploi"
    },
    {
      href: '/recruteur/dashbord/candidature',
      icon:  <FaUsers/>,
      title: 'Candidatures'
    },

    {
      href: '/recruteur/dashbord/profil',
      icon: <AiOutlineUser/>,
      title: 'Profil'
    }
  ];
  


const Sidebar = () => {

   const [images, setImages] = useState([]);
   const {currentUser, logout} = useContext(AuthContext);
   const [roleCvP, setRoleCvP] = useState("");
   const [roleCvS, setRoleCvS] = useState("");
   const db = app.firestore();
   var cvplus;
   var cvsimple;

   useEffect(() => {
    db.collection("recruteurs")
       .where("id_utilisateur", "==", currentUser.uid)
       .onSnapshot((querySnapshot) => {
           var list = [];
           querySnapshot.forEach((doc) => {
               list.push({ ...doc.data(), id: doc.id })
               cvplus = doc.data().cvthequeplus;
               cvsimple = doc.data().cvthequesimple;
           });
           setImages(list)  
           setRoleCvP(cvplus)
           setRoleCvS(cvsimple)
       });
    }, []);




  const classes = useStyles();

    return (
        <>
           <Container>
           {images.map((item,index) => (
            <ProfilContainer  key={index}>
              <ProfileImg src={item.image_url} />
              <ProfileName>{item.nom_recruteur}</ProfileName>
          </ProfilContainer>
         ))}
              

              <MenuContainer>
                  {items.map((item) => (

                   <ContainerLink to={item.href}>
                       <TitleLink >
                        <SpanLink>{item.icon}</SpanLink>
                         {item.title}
                       </TitleLink>
                   </ContainerLink>))}
                   {(roleCvS == "oui" && roleCvP == "non" || roleCvP == "oui" ) && (
                   <ContainerLink to="/recruteur/dashbord/cvtheque">
                     
                         <TitleLink>
                        <SpanLink> <Folder/> </SpanLink>
                         CvThèque
                       </TitleLink>
                   </ContainerLink>
                   )
                  }
                   <ContainerLink>
                         {       
                            currentUser && 
                              (
                                <Tooltip title="Se déconnecter" placement="right-start">
                                    <IconButton className={classes.margin} onClick={logout}>
                                                <ExitToApp/>
                                            
                                    </IconButton>
                                </Tooltip>
                                
                              ) 
                            }
                   </ContainerLink>
              </MenuContainer>
           </Container>
        </>
    )
}

export default Sidebar
