import React,{ useState,useContext,useEffect } from 'react'
import { Grid, Typography, makeStyles, Divider, } from '@material-ui/core'
import { Exp,ExpSan, ExpP } from './ExpElements'
import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';


const useStyles = makeStyles({
    detail: {
     marginBottom: '10px'
    },
   
  });

const Experiences = () => {

    const {currentUser} = useContext(AuthContext);
    const db = app.firestore();
    const [experience, setExperience] = useState([]);
    const classes = useStyles();


    const getExperiences = async () => {
        db.collection("experiences")
          .where("id_utilisateur", "==", currentUser.uid)
          .onSnapshot((querySnapshot) => {
              var List= []
              querySnapshot.forEach((doc) => {
                List.push({ ...doc.data(), id: doc.id })
              });
              setExperience(List)              
          });
     }


     useEffect(()=> {
        getExperiences();
    }, [])

    return (
        <Exp className="experience">
            
            <Grid container spacing="2">

                { 
                  experience.map((item,index) => {

                    return(
                    <Grid   md={12} xs={12}>
                        <Grid key={index} container   justify="space-between" className={classes.detail}>
                            <Grid item xs={6}>
                                <Typography variant="body2"><ExpSan>Secteur d'acitvité:</ExpSan>  {item.secteur_activite}</Typography>
                                <Typography variant="body2"><ExpSan>Poste occupé: </ExpSan> {item.poste}</Typography>
                                <Typography variant="body2"><ExpSan>Société: </ExpSan> {item.entreprise} </Typography>
                            </Grid>
                            <Grid item xs={6}>
                                <Typography variant="body2"><ExpSan>Ville: </ExpSan>{item.region} </Typography>
                                <Typography variant="body2"><ExpSan>Date début:</ExpSan> {item.date_debut} </Typography>
                                <Typography variant="body2"><ExpSan>Date Fin:</ExpSan> {item.date_fin} </Typography>
                            </Grid>      
                        </Grid>
                    
                       <ExpP className="p">
                            {item.description_experience}
                        </ExpP> 
                        <Divider/>
                    </Grid> 
                             )  
                         })
                    }
            </Grid>
        
        </Exp>
    )
}

export default Experiences
