import React, {useContext} from 'react'
import { Link as RouterLink,useHistory } from 'react-router-dom';
import { 
    Paper,
    makeStyles,
    Container,
    Box,
    Grid,
    Link,
    TextField,
    Typography,
    Button,
    Checkbox,
    FormHelperText,
} from '@material-ui/core'
import { Formik } from 'formik';
import * as Yup from 'yup';
import {GetProvinces, GetVilles} from '../data/administrative'
import { AuthContext } from '../../../firebase/AuthContext';
import app from '../../../firebase/base';
import { toast } from "react-toastify";
import { format } from 'date-fns'

const useStyles = makeStyles((theme) => ({
    root: {
      backgroundColor: theme.palette.background.dark,
      height: '100%',
      paddingBottom: theme.spacing(3),
      paddingTop: theme.spacing(3)
    },
    textPrimary:{
        color: '#008647',
      },

      buttonStyle: {
        backgroundColor: "#1a1359",
        color: "#fff",
      
        "&:hover":{
            backgroundColor: "#008649",
        }
    }
  }));

const RegisterForm = () => {

    const classes = useStyles();
    const navigate = useHistory();
    const {currentUser} = useContext(AuthContext);
    const role = 'recruteur';
    const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/
   
    const d = new Date();
    const mois = d.getMonth();
    const jour = d.getDate();
    const heure = d.getHours();
    const annee = d.getUTCFullYear();
    const minute = d.getUTCMinutes();
    const seconde = d.getUTCSeconds();
    const year = d.getYear();
    const tabMois = ["janvier","fevrier", "mars","avril","mai","juin",
                      "juillet","aout","septembre","octobre","novembre","decembre"
                    ]
    const handleRegister = (e) => {
        
        e.preventDefault();
        const db = app.firestore();
        const {
            
            nom_recruteur, 
            email_recruteur,
            province_recruteur,
            ville_recruteur,
            secteur_activite,
            telephone_recruteur,
            referent_recruteur,
            description_recruteur,
            url_recruteur, 
            raison_sociale,
            adresse_recruteur

        } = e.target.elements;


        db.collection('utilisateurs').add({
            id_utilisateur: currentUser.uid,
            email_utilisateur: email_recruteur.value,
            role_utilisateur: role,
            date_ajout:format( new Date(),"dd MMM yyyy")

        })
        .then(() => {
            console.log("Les informations ont été ajouté avec succès")
            
        })
        .catch((error) => {
            console.log(error.message)
        })
        /************************************************ */
        
        db.collection('recruteurs').add({

            id_utilisateur: currentUser.uid,
            nom_recruteur: nom_recruteur.value, 
            email_recruteur: email_recruteur.value,
            province_recruteur: province_recruteur.value,
            ville_recruteur: ville_recruteur.value,
            secteur_activite: secteur_activite.value,
            telephone_recruteur: telephone_recruteur.value,
            referent_recruteur:referent_recruteur.value,
            description_recruteur:description_recruteur.value,
            url_recruteur: url_recruteur.value,
            raison_sociale: raison_sociale.value,
            adresse_recruteur: adresse_recruteur.value,
            mois:tabMois[mois],
            annees:annee,
            date_inscription: d


        })
        .then(() => {
            toast("Compte  créé avec succès", {
                type: "success",
            })
            navigate.push('/recruteur/', { replace: true });
        })
        .catch((error) => {
            alert(error.message)
        });

      


    }

    return (
        <>
            <Paper className={classes.root}>
            <Box
                display="flex"
                flexDirection="column"
                height="100%"
                justifyContent="center"
            >
                <Container maxWidth="sm">
                <Formik
                    initialValues={{
                    email_recruteur: '',
                    nom_recruteur: '',
                    adresse_recruteur: '',
                    referent_recruteur:'',
                    ville_recruteur:'',
                    province_recruteur:'',
                    raison_sociale:'',
                    secteur_activite: '',
                    telephone_recruteur: '',
                    description_recruteur: '',
                    url_recruteur:'',
                    policy: false
                    }}
                    validationSchema={
                    Yup.object().shape({
                        email: Yup.string().email('Ajouter un email valide').max(50).required('Email de la société requis'),
                        nom_recruteur: Yup.string().max(50).required('Le nom de votre société est requis'),
                        adresse_recruteur: Yup.string().max(255).required('Votre adresse est requise'),
                        referent_recruteur: Yup.string().max(10).required('Votre reférent est requis'),
                        ville_recruteur:Yup.string().max(20).required('Votre ville est requise'),
                        province_recruteur:Yup.string().max(20).required('Votre province est requise'),
                        secteur_activite: Yup.string().max(20).required('Champ requis'),
                        raison_sociale: Yup.string().max(20).required('Raison sociale requise'),
                        telephone_recruteur: Yup.string().max(9).matches(phoneRegExp, "Minimum de 6 chiffres requis,Ce numéro n'est pas valide"),
                        description_recruteur: Yup.string().max(1000).required('Décrivez votre entreprise ou activité'),
                        policy: Yup.boolean().oneOf([true], 'Vous devez acceptez les termes et conditions')
                    })
                    }
                    onSubmit={() => {
                       alert("go")
                    }}
                    >
                    {({
                    errors,
                    handleBlur,
                    handleChange,
                    handleSubmit,
                    isSubmitting,
                    touched,
                    values
                    }) => (
                    <form onSubmit={handleRegister}>
                        <Box mb={3}>
                        <Typography
                            color="textPrimary"
                            variant="h4"
                        >
                            Créer un nouveau compte
                        </Typography>
                        <Typography
                            color="textSecondary"
                            gutterBottom
                            variant="body2"
                        >
                            Utiliser votre email pour créer un compte
                        </Typography>
                        </Box>
                    
                    <Grid
                        container
                        spacing={2}
                    >
                     <Grid item md={6}   xs={12}  >
                        <TextField
                        error={Boolean(touched.nom_recruteur && errors.nom_recruteur)}
                        fullWidth
                        helperText={touched.nom_recruteur && errors.nom_recruteur}
                        label="Nom de la société"
                        margin="normal"
                        required
                        name="nom_recruteur"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.nom_recruteur}
                        variant="outlined"
                        />
                    </Grid>
                    <Grid item md={6} xs={12}   >
                        <TextField
                        error={Boolean(touched.adresse_recruteur && errors.adresse_recruteur)}
                        fullWidth
                        helperText={touched.adresse_recruteur && errors.adresse_recruteur}
                        label="Adresse"
                        required
                        margin="normal"
                        name="adresse_recruteur"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.adresse_recruteur}
                        variant="outlined"
                        />
                    </Grid>
                    <Grid item md={6}  xs={12}>
                    <TextField
                        fullWidth
                        label="Ville"
                        name="ville_recruteur"
                        onChange={handleChange}
                        required
                        select
                        SelectProps={{ native: true }}
                        value={values.GetVilles}
                        variant="outlined"
                    >
                        {GetVilles.map((option) => (
                        <option
                            key={option.value}
                            value={option.value}
                        >
                            {option.label}
                        </option>
                        ))}
                    </TextField>
                    </Grid>
                    <Grid item md={6}  xs={12}   >
                    <TextField
                        fullWidth
                        label="Province"
                        name="province_recruteur"
                        onChange={handleChange}
                        required
                        select
                        SelectProps={{ native: true }}
                        value={values.GetProvinces}
                        variant="outlined"
                    >
                        {GetProvinces.map((option) => (
                        <option
                            key={option.value}
                            value={option.value}
                        >
                            {option.label}
                        </option>
                        ))}
                    </TextField>
                    </Grid>
                    <Grid item md={6}  xs={12}>
                        <TextField
                        error={Boolean(touched.email_recruteur && errors.email_recruteur)}
                        fullWidth
                        helperText={touched.email_recruteur && errors.email_recruteur}
                        label="Email"
                        margin="normal"
                        required
                        name="email_recruteur"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        type="email"
                        value={values.email_recruteur}
                        variant="outlined"
                        />
                    </Grid>
                    <Grid item md={6} xs={12}   >
                        <TextField
                        error={Boolean(touched.secteur_activite && errors.secteur_activite)}
                        fullWidth
                        helperText={touched.secteur_activite && errors.secteur_activite}
                        label="Secteur d'activité"
                        margin="normal"
                        required
                        name="secteur_activite"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.secteur_activite}
                        variant="outlined"
                        />
                    </Grid>
                    <Grid item md={6} xs={12}   >
                        <TextField
                        error={Boolean(touched.raison_sociale && errors.raison_sociale)}
                        fullWidth
                        helperText={touched.raison_sociale && errors.raison_sociale}
                        label="Raison sociale"
                        margin="normal"
                        name="raison_sociale"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.raison_sociale}
                        variant="outlined"
                        />
                    </Grid>
                    <Grid item md={6} xs={12}   >
                        <TextField
                        error={Boolean(touched.referent_recruteur && errors.referent_recruteur)}
                        fullWidth
                        helperText={touched.referent_recruteur && errors.referent_recruteur}
                        label="Reférent"
                        margin="normal"
                        name="referent_recruteur"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.referent_recruteur}
                        variant="outlined"
                        />
                    </Grid>
                   
                     <Grid item   md={6} xs={12}  >
                        <TextField
                       
                        fullWidth
                        helperText={touched.url_recruteur && errors.url_recruteur}
                        label="Votre site web"
                        margin="normal"
                        name="url_recruteur"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.url_recruteur}
                        variant="outlined"
                        />
                     </Grid>
                     <Grid item   md={6} xs={12}  >
                        <TextField
                        error={Boolean(touched.telephone_recruteur && errors.telephone_recruteur)}
                        fullWidth
                        helperText={touched.telephone_recruteur && errors.telephone_recruteur}
                        label="Numéro de téléphone"
                        required
                        margin="normal"
                        name="telephone_recruteur"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.telephone_recruteur}
                        variant="outlined"
                        />
                     </Grid>
                     <Grid item   md={12} xs={12}  >
                        <TextField
                        error={Boolean(touched.description_recruteur && errors.description_recruteur)}
                        fullWidth
                        helperText={touched.description_recruteur && errors.description_recruteur}
                        label="Décriver votre structure"
                        margin="normal"
                        name="description_recruteur"
                        required
                        multiline
                        rows={7}
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.description_recruteur}
                        variant="outlined"
                        />
                     </Grid>

                        </Grid>
                        <Box
                        alignItems="center"
                        display="flex"
                        ml={-1}
                        >
                        <Checkbox
                            checked={values.policy}
                            name="policy"
                            onChange={handleChange}
                        />
                        <Typography
                            color="textSecondary"
                            variant="body1"
                        >
                            J'ai lu les
                            {' '}
                            <Link
                            color="primary"
                            component={RouterLink}
                            to="#"
                            underline="always"
                            variant="h6"
                            className={classes.textPrimary}
                            >
                            Termes et Conditions
                            </Link>
                        </Typography>
                        </Box>
                        {Boolean(touched.policy && errors.policy) && (
                        <FormHelperText error>
                            {errors.policy}
                        </FormHelperText>
                        )}
                        <Box my={2}>
                        <Button
                            className={classes.buttonStyle}
                            disabled={isSubmitting}
                            fullWidth
                            size="large"
                            type="submit"
                            variant="contained"
                        >
                            S&apos;inscrire maintenant
                        </Button>
                        </Box>
                        <Typography
                        color="textSecondary"
                        variant="body1"
                        >
                        Vous avez un compte?
                        {' '}
                        <Link
                            component={RouterLink}
                            to="/recruteur/connexion"
                            variant="h6"
                            className={classes.textPrimary}
                        >
                            Se connecter
                        </Link>
                        </Typography>
                    </form>
                    )}
                </Formik>
                </Container>
            </Box>
          
            </Paper>
        </>
    )
}

export default RegisterForm
