import React,{useEffect,useState} from 'react'
import { 
        Box,
        Card, 
        CardContent, 
        CardHeader, 
        Divider, 
        Grid, 
 } from '@material-ui/core'

import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import app from '../../../firebase/base';




const initialValues = {
    id:'',
    nom_candidat: '',
    prenom_candidat:'',
    email_candidat: '',
    telephone_candidat: '',
    ville_candidat:'',
    province_candidat: '',
    situation_candidat: '',
    image_url:'',
    facebook:'',
    linkedin:'',
    twitter:'',
    github:'',
} 




const ProfilForm = (props) => {
   
    const {addOrEdit,currentID} = props
    const db = app.firestore();
    const [file, setFile] = useState(null);
    const [error, setError] = useState('');
    const types = ['image/png', 'image/jpeg']; // image types
 
 
     

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('nom_candidat' in fieldValues)
            temp.nom_candidat = fieldValues.nom_candidat ? "" : "Champ requis."
        if ('prenom_candidat' in fieldValues) 
            temp.prenom_candidat = fieldValues.prenom_candidat ? "" : "Champ requis."
        if ('email_candidat' in fieldValues)
            temp.email_candidat = (/$^|.+@.+..+/).test(fieldValues.email_candidat) ? "" : "Email non valide."
        if ('telephone_candidat' in fieldValues)
            temp.telephone_candidat = fieldValues.telephone_candidat.length > 8 ? "" : "Minimum de 9 nombre requis."
        if ('ville_candidat' in fieldValues)
            temp.ville_candidat = fieldValues.ville_candidat.length !== 0 ? "" : "Champ requis."
        if ('situation_candidat' in fieldValues)
            temp.situation_candidat = fieldValues.situation_candidat.length !== 0 ? "" : "Champ requis."
        if ('province_candidat' in fieldValues)
            temp.province_candidat = fieldValues.province_candidat.length !== 0 ? "" : "Champ requis."

        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange, 
        resetForm
    }= useForm(initialValues, true, validate);


    const handleInputFileChange = (e) => {
        let selectedFile = e.target.files[0];
        if (selectedFile && types.includes(selectedFile.type)) {
            setFile(selectedFile);
            setError('')
        }
        else {
            setFile(null);
            alert('Sélectionner un fichier valide de type (jpg, jpeg ,png ou pdf)');
        }
    }


    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values,file,resetForm);
        }
    }

    const getCandidatById = async (id) => {
           const doc = await db.collection("candidats").doc(id).get();
         setValues({ ...doc.data() });
        
    };

    useEffect(() => {
        if (currentID == "")
            setValues({
                ...initialValues
            })
        else
           getCandidatById(currentID)
    }, [currentID])

  


    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >
            <Card>
                <CardHeader
                    subheader="Vous pouvez éditer vos informations"
                    title="Profil"
                />
                <Divider/>
                <CardContent>
                    <Grid 
                        container
                        spacing={3}
                    >
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="nom_candidat"
                                    label="Nom"
                                    value={values.nom_candidat}
                                    onChange={handleInputChange}
                                    error={errors.nom_candidat}
                                /> 
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="prenom_candidat"
                                    label="prenom"
                                    value={values.prenom_candidat}
                                    onChange={handleInputChange}
                                    error={errors.prenom_candidat}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="province_candidat"
                                    label="Province"
                                    value={values.province_candidat}
                                    onChange={handleInputChange}
                                    error={errors.province_candidat}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="ville_candidat"
                                    label="ville"
                                    value={values.ville_candidat}
                                    onChange={handleInputChange}
                                    error={errors.ville_candidat}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                        <Controls.Input
                                    name="situation_candidat"
                                    label="Situation Professionnelle"
                                    value={values.situation_candidat}
                                    onChange={handleInputChange}
                                    error={errors.situation_candidat}
                                />
                                
                         </Grid>
                         <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="email_candidat"
                                    label="email"
                                    value={values.email_candidat}
                                    onChange={handleInputChange}
                                    error={errors.email_candidat}
                                /> 
                        </Grid>
                       
                       
                    <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="telephone_candidat"
                                    label="telephone"
                                    value={values.telephone_candidat}
                                    onChange={handleInputChange}
                                    error={errors.telephone_candidat}
                                />
                                 
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="facebook"
                                    label="Facebook"
                                    value={values.facebook}
                                    onChange={handleInputChange}
                                    error={errors.Facebook}
                                />
                        </Grid>
                       
                    
                    <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="linkedin"
                                    label="Linkedin"
                                    value={values.linkedin}
                                    onChange={handleInputChange}
                                    error={errors.linkedin}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="twitter"
                                    label="Twitter"
                                    value={values.twitter}
                                    onChange={handleInputChange}
                                    error={errors.twtter}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="github"
                                    label="Github"
                                    value={values.github}
                                    onChange={handleInputChange}
                                    error={errors.github}
                                />
                        </Grid>
                        <Grid item md={6} xs={12}> 
                              
                              <Controls.InputFile
                                name="image_url"
                                label="Télécharger une photo"
                                onChange={handleInputFileChange}
                                error={errors.image_url}
                                
                              />
                    </Grid>
                    { /*     
                    
                    
                       
                       
                       
                     <Grid item md={6} xs={12}>
                                <Controls.Input
                                    type="file"
                                    name="image"
                                    label="Image"
                                   
                                    value={values.image}
                                    onChange={handleInputChange}
                                    error={errors.image}
                                />
                        </Grid>*/}
                    </Grid>
                </CardContent>
                <Divider />
                <Box
                    display="flex"
                    justifyContent="flex-end"
                    p={2}
                >
                   <Controls.Button
                            type="submit"
                            text="Modifier" />
                        <Controls.Button
                            text="Effacer"
                            color="default"
                        onClick={resetForm} />
                </Box>
            </Card>
        </Form>
    )
}

export default ProfilForm
