import { Box, CardContent, Container, Grid } from '@material-ui/core'
import React, {useState,useEffect} from 'react'
import CardCandidats from '../components/Cards/CardCandidats'
import CardOffre from '../components/Cards/CardOffre'
import CardRecruteur from '../components/Cards/CardRecruteur'
import CardUtilisateurs from '../components/Cards/CardUtilisateurs'
import { MainContainer } from '../components/main/MainElements'
import MobileSidebar from '../components/MobileSidebar/Index'
import Sidebar from '../components/Sidebar/Index'
import Topnav from '../components/Topnav/Index'
import app from '../../firebase/base';
import ChartOffre from '../components/Cards/ChartOffre'
import CardCv from '../components/Cards/CardCv'
import ChartsGraph from '../components/Cards/ChartsGraph'




 
const Dasboard = () => {

    const [isOpen, setIsOpen] = useState(false)
    const db = app.firestore();
    const [offreEmplois, setOffreEmploi] = useState([])
    const [admin, setAdmin] = useState([])
    const [recruteurs, setRecruteurs] = useState([])
    const [candidats, setCandidats] = useState([])
    const [cv, setCV] = useState([])
    const [tabUtilisateur, setTabUtilisateur] = useState([])


    const [dataOffre, setDataOffre] = useState([])
    const [dataCandidatures, setDataCandidatures] = useState([])
    const [dataCandidats, setDataCandidats] = useState([])
    const [dataRecruteurs, setDataRecruteurs] = useState([])
    const [dataCv, setDataCv] = useState([])

    const [offreTaille, setOffreTaille] = useState("")
    const [adminTaille, setAdminTaille] = useState("")
    const [recruteursTaille, setRecruteursTaille] = useState("")
    const [candidatsTaille, setCandidatsTaille] = useState("")
    const [cvTaille, setCvTaille] = useState("")

    let tableUsers = [];

    let tableOffre = [];
    let tableCandidatures = [];
    let tableCandidats = [];
    let tableRecruteurs = [];
    let tableCV = [];

    const d = new Date();
    const mois = d.getMonth();
    const annee = d.getUTCFullYear();
    const tabMois = ["janvier","fevrier", "mars","avril","mai","juin",
                     "juillet","aout","septembre","octobre","novembre","decembre"
                    ]


   

 

    const toggle = () => {
        setIsOpen(!isOpen)
    }
   
    
    const getOffreData = async () => {
        for(var i= 0;i<tabMois.length;i++){
            await db.collection("offre_emploi")
            .where("mois", "==", tabMois[i])
            .where("annees", "==", annee.toString() )
            .onSnapshot((querySnapshot) => {
                let listOffre = []
                tableOffre.push(parseInt(querySnapshot.size))
                querySnapshot.forEach((doc) => {
                    listOffre.push({...doc.data(), id: doc.id })
                });         
            });
            
        } 
        setDataOffre(tableOffre)
     }

     const getCandidatureData = async () => {
        for(var i= 0;i<tabMois.length;i++){
            await db.collection("candidatures")
            .where("mois", "==", tabMois[i])
            .where("annees", "==", annee.toString() )
            .onSnapshot((querySnapshot) => {
                let listOffre = []
                tableCandidatures.push(parseInt(querySnapshot.size))
                querySnapshot.forEach((doc) => {
                    listOffre.push({...doc.data(), id: doc.id })
                });         
            });
        } 
        setDataCandidatures(tableCandidatures)
     }

     const getCandidatsData = async () => {
        for(var i= 0;i<tabMois.length;i++){
            await db.collection("candidats")
            .where("mois", "==", tabMois[i])
            .where("annees", "==", annee.toString() )
            .onSnapshot((querySnapshot) => {
                let listOffre = []
                tableCandidats.push(parseInt(querySnapshot.size))
                querySnapshot.forEach((doc) => {
                    listOffre.push({...doc.data(), id: doc.id })
                });         
            });
        } 
        setDataCandidats(tableCandidats)
     }


     const getRecruteursData = async () => {
        for(var i= 0;i<tabMois.length;i++){
            await db.collection("recruteurs")
            .where("mois", "==", tabMois[i])
            .where("annees", "==", annee.toString() )
            .onSnapshot((querySnapshot) => {
                let listOffre = []
                tableRecruteurs.push(parseInt(querySnapshot.size))
                querySnapshot.forEach((doc) => {
                    listOffre.push({...doc.data(), id: doc.id })
                });         
            });
        } 
        setDataRecruteurs(tableRecruteurs)
     }
     const getCvData = async () => {
        for(var i= 0;i<tabMois.length;i++){
            await db.collection("cv")
            .where("mois", "==", tabMois[i])
            .where("annees", "==", annee.toString() )
            .onSnapshot((querySnapshot) => {
                let listOffre = []
                tableCV.push(parseInt(querySnapshot.size))
                querySnapshot.forEach((doc) => {
                    listOffre.push({...doc.data(), id: doc.id })
                });         
            });
        } 
        setDataCv(tableCV)
     }


     /*******ici on recupère le nombre d'utilisateurs dans tabUsers */
    const getOffre = async () => {
        await db.collection("offre_emploi")
                .onSnapshot((querySnapshot) => {
                    var listOffre = []
                    setOffreTaille(querySnapshot.size)
                    querySnapshot.forEach((doc) => {
                        listOffre.push({...doc.data(), id: doc.id })
                    });
                    setOffreEmploi(listOffre)              
                });
     }

     const getAdmin = async () => {
        await db.collection("administration")
                .onSnapshot((querySnapshot) => {
                    var listAdmin = []
                    setAdminTaille(querySnapshot.size)
                    tableUsers.push(parseInt(querySnapshot.size))
                    querySnapshot.forEach((doc) => {
                        listAdmin.push({...doc.data(), id: doc.id })
                    });
                    setAdmin(listAdmin)              
                });
     }


     const getRecruteurs = async () => {
        await db.collection("recruteurs")
                .onSnapshot((querySnapshot) => {
                    var listRecru = []
                    tableUsers.push(parseInt(querySnapshot.size) )
                    setRecruteursTaille(querySnapshot.size)
                    querySnapshot.forEach((doc) => {
                        listRecru.push({...doc.data(), id: doc.id })
                    });
                    setRecruteurs(listRecru)              
                });
     }

     const getCandidats = async () => {
        await db.collection("candidats")
                .onSnapshot((querySnapshot) => {
                    var listCand = []
                    tableUsers.push(parseInt(querySnapshot.size) )
                    setCandidatsTaille(querySnapshot.size)
                    querySnapshot.forEach((doc) => {
                        listCand.push({...doc.data(), id: doc.id })
                    });
                    setCandidats(listCand)   
                              
                });
     }

     const getCV = async () => {
        await db.collection("cv")
                .onSnapshot((querySnapshot) => {
                    var listCV = []
                    setCvTaille(querySnapshot.size)
                    querySnapshot.forEach((doc) => {
                        listCV.push({...doc.data(), id: doc.id })
                    });
                    setCV(listCV)   
                              
                });
     }
 

     useEffect(()=> {
         
        getOffre();
        getAdmin();
        getCandidats();
        getRecruteurs();
        getCV();
        getOffreData();
        getCandidatsData();
        getCandidatureData();
        getRecruteursData();
        getCvData();
        setTabUtilisateur(tableUsers)   
    }, [])

    return (
        <>
           <Sidebar/>
           <MainContainer>
               <MobileSidebar isOpen={isOpen}  toggle={toggle} />
               <Topnav toggle={toggle} />
               <Box
                mt={2}
                sx={{
                    backgroundColor: 'background.default',
                    minHeight: '100%',
                    py: 3
                  }}
               >
                   <Container maxWidth={false}>

                   <Grid
                        container
                        spacing={3}
                    >

                           <Grid
                                item
                                lg={3}
                                sm={6}
                                xl={3}
                                xs={12}
                            >
                                <CardUtilisateurs  taille={adminTaille} />
                            </Grid>
                            <Grid
                                item
                                lg={3}
                                sm={6}
                                xl={3}
                                xs={12}
                            >
                                <CardRecruteur taille={recruteursTaille} />
                            </Grid>
                            <Grid
                                item
                                lg={3}
                                sm={6}
                                xl={3}
                                xs={12}
                            >
                                <CardCandidats taille={candidatsTaille} />
                            </Grid>
                            <Grid
                                item
                                lg={3}
                                sm={6}
                                xl={3}
                                xs={12}
                            >
                                <CardOffre taille={offreTaille} />
                            </Grid>
                            <Grid
                                item
                                lg={3}
                                sm={6}
                                xl={3}
                                xs={12}
                            >
                                <CardCv taille={cvTaille} />
                            </Grid>
                            <Grid
                                item
                                lg={8}
                                md={12}
                                xl={9}
                                xs={12} 
                            >
                                <ChartOffre tabUsers={tabUtilisateur}  />
                            </Grid>
                            <Grid
                                item
                                lg={8}
                                md={12}
                                xl={9}
                                xs={12} 
                            >
                                <ChartsGraph titre="Nombre d'offres d'emploi par mois" tableData={dataOffre}  />
                            </Grid>
                            <Grid
                                item
                                lg={8}
                                md={12}
                                xl={9}
                                xs={12} 
                            >
                                <ChartsGraph titre="Nombre de candidatures  par mois" tableData={dataCandidatures}  />
                            </Grid>
                            <Grid
                                item
                                lg={8}
                                md={12}
                                xl={9}
                                xs={12} 
                            >
                                <ChartsGraph titre="Nombre de candidat par mois" tableData={dataCandidats}  />
                            </Grid>
                            <Grid
                                item
                                lg={8}
                                md={12}
                                xl={9}
                                xs={12} 
                            >
                                <ChartsGraph titre="Nombre de recruteurs par mois" tableData={dataRecruteurs}  />
                            </Grid>
                            <Grid
                                item
                                lg={8}
                                md={12}
                                xl={9}
                                xs={12} 
                            >
                                <ChartsGraph titre="Nombre de CV par mois" tableData={dataCv}  />
                            </Grid>
                    </Grid>
                   </Container>
               </Box>
           </MainContainer>
        </>
    )
}

export default Dasboard
