import React, { useState } from "react";
import "./PubSlide.css";
import Slider from "react-slick";
import astronaut from "../../assets/icons/astronaut.png";
import celebrating from "../../assets/icons/celebrating.png";
import education from "../../assets/icons/education.png";
import taken from "../../assets/icons/taken.png";
import { FaArrowRight, FaArrowLeft } from "react-icons/fa";

const images = [astronaut, celebrating, education, taken];

const settings = {
    dots: false,
    fade: true,
    adaptiveHeight: true,
    infinite: true,
    autoplay: true,
    speed: 300,
    slidesToShow: 1,
    slidesToScroll: 1
  };



const PubSlide = (props) => {

    const [imageIndex, setImageIndex] = useState(0);
    const {dataPub} = props
    return (
        <div className="App">
            <Slider {...settings}>
                {dataPub.map((item, idx) => (
                <div key={idx} className="slide" >
                    <img src={item.lien_image} alt={item.description_image} />
                </div>
                ))}
            </Slider>
        </div>
    )
}

export default PubSlide
