import React from 'react'
import { TextField } from '@material-ui/core';

export default function InputMultiligne(props) {

    const { name,type,rows, label, value,error=null, onChange } = props;
    return (
        <TextField
            style={{backgroundColor: '#fff'}}
            variant="outlined"
            label={label} 
            fullWidth
            multiline
            rows={rows}
            type={type}
            name={name}
            value={value}
            onChange={onChange}
            {...(error && {error:true,helperText:error})}
        /> 
    )
}
