import React,{useEffect,useContext,useState} from 'react'
import { 
        Box,
        Card, 
        CardContent, 
        CardHeader, 
        Divider, 
        Grid, 
 } from '@material-ui/core'

import { Form, useForm } from '../useForm'
import Controls from '../controls/Controls'
import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';
import styled from 'styled-components'



const BlockCache = styled.div`
 display:none;
`;


const FormCv = (props) => {
   
    const {addOrEdit,currentID} = props
    const {currentUser} = useContext(AuthContext);
    const db = app.firestore();

    const [file, setFile] = useState(null);
    const [error, setError] = useState('');
    const types = ['image/png', 'image/jpeg']; // image types

   
    
   
    const initialValues = {
        id_utilisateur:currentUser.uid,
        intitule_cv: '',
        secteur_activite:'',
        fichier_cv:''
        //cv_document: '',
    }

    

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('id_utilisateur' in fieldValues)
        temp.id_utilisateur = fieldValues.id_utilisateur ? "" : "Champ requis."
        if ('intitule_cv' in fieldValues)
            temp.intitule_cv = fieldValues.intitule_cv ? "" : "Champ requis."
        if ('secteur_activite' in fieldValues)
            temp.secteur_activite = fieldValues.secteur_activite ? "" : "Champ requis."
       // if ('fichier_cv' in fieldValues)
          //  temp.fichier_cv = (/\.(jpg|jpeg|png|pdf)$/).test(fieldValues.fichier_cv) ? "" : "Sélectionner une image valide jpg|jpeg|png|pdf"
      /*  if ('cv_document' in fieldValues)
            temp.cv_document = fieldValues.cv_document.length !== 0 ? "" : "Champ requis."*/
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
            values,
            setValues,
            errors,
            setErrors,
            handleInputChange,
            resetForm
    } = useForm(initialValues, true, validate);

   

    const getCvById = async (id) => {
        const doc = await db.collection("cv").doc(id).get();
         setValues({ ...doc.data() });
         console.log(values)
    };

    const handleInputFileChange = (e) => {
        let selectedFile = e.target.files[0];
        if (selectedFile && types.includes(selectedFile.type)) {
            setFile(selectedFile);
            setError('')
        }
        else {
            setFile(null);
            alert('Sélectionner un fichier valide de type (jpg, jpeg ,png ou pdf)');
        }
    }

    useEffect(() => {
        if (currentID === "")
            setValues({
                ...initialValues
            })
        else
           getCvById(currentID);
           
    }, [currentID])

    

    const handleSubmit = e => {
        e.preventDefault()
 
            if (validate()) {
                addOrEdit(values,file,resetForm);
            }
    }


    return (
        <Form
          autoComplete="off"
          onSubmit={handleSubmit}
        >
            <Card>
                <CardHeader
                    subheader="Vous pouvez éditer vos informations"
                    title="Curriculum Vitae"
                />
                <Divider/>
                <CardContent>
                    <Grid
                        container
                        spacing={2}
                    >
                   
                        <BlockCache>
                            <Grid item md={6} xs={12}>
                                    <Controls.Input
                                        name="id_utilisateur"
                                        label="Identifiant"
                                        value={values.id_utilisateur}
                                        onChange={handleInputChange}
                                        error={errors.id_utilisateur}
                                        disabled="true"
                                    /> 
                            </Grid>
                        </BlockCache>

                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="intitule_cv"
                                    label="Titre de votre CV"
                                    value={values.intitule_cv}
                                    onChange={handleInputChange}
                                    error={errors.intitule_cv}
                                /> 
                        </Grid>

                        <Grid item md={6} xs={12}>
                                <Controls.Input
                                    name="secteur_activite"
                                    label="Secteur d'activité"
                                    value={values.secteur_activite}
                                    onChange={handleInputChange}
                                    error={errors.secteur_activite}
                                />
                        </Grid>
                       <Grid item md={6} xs={12}>
                                <Controls.InputFile
                                    name="fichier_cv"
                                    label="Choisir votre fichier cv"
                                    onChange={handleInputFileChange}
                                    type="file"
                                />
                         </Grid> 
                    </Grid>
                </CardContent>
                <Divider />
                <Box
                    display="flex"
                    justifyContent="flex-end"
                    p={2}
                >
                   <Controls.Button
                            type="submit"
                            text={currentID === "" ? "Valider" : "Modifier"} />
                        <Controls.Button
                            text="Effacer"
                            color="default"
                        onClick={resetForm} />
                </Box>
            </Card>
        </Form>
    )
}

export default FormCv
