import React, {useState,useEffect} from 'react'
import { Box,makeStyles,InputAdornment, Grid, Paper,TableCell,TableRow, Toolbar, TableBody } from '@material-ui/core'
import Controls from '../controls/Controls'
import {   Build, Edit, Search } from '@material-ui/icons'
import useTable from '../useTable';
import CloseIcon from '@material-ui/icons/Close';
import AddIcon from '@material-ui/icons/Add';


import Popup from '../Popup';
import Notification from '../Notification';
import ConfirmDialog from '../ConfirmDialog';
import './table.css'
import PageHeader from '../PageHeader';
import app from '../../../firebase/base';
import ReglagesForm from '../Forms/ReglagesForm';


const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '50%',
    },
    newButton: {
        
        right: '10px',
        color: '#1a1359',
        borderColor: '#1a1359',
        justifyContent:'center',
        alignItems: 'center',
        display:'flex-start'
    },
  
}))

const headCells = [
    { id: 'titre', label: 'tire' },
    { id: 'categorie', label: 'catégorie' },
    { id: 'contenu', label: 'contenu' },
    { id: 'actions', label: 'Actions', disableSorting: true }
]

const TableReglages = (props) => {
    const classes = useStyles();

    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [records, setRecords] = useState([])
    const [openPopup, setOpenPopup] = useState(false)
    const [recordForEdit, setRecordForEdit] = useState(null)
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })
    const [etat, setEtat] = useState(null);

    const db = app.firestore();
    const listOffre = [];

 
    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records,headCells,filterFn);

    const handleSearchTitre = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.titre.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchCat = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.categorie.toLowerCase().includes(target.value))
            }
        })
    }

    
   

    const getParams = async () => {
        await db.collection("parametres")
                .onSnapshot((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        listOffre.push({...doc.data(), id: doc.id })
                    });
                    setRecords(listOffre)              
                });
      }


             const addOrEdit = async (obj,content,resetForm) => {
         
                await  db.collection("parametres").where("categorie", "==", obj.categorie)
                .get()
                .then((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        if(doc.exists){
                            setEtat(true)
                               
                        }else{
                            setEtat(false)
                        }
                    });
                })
            


        try{
            if (obj.id == ""){
                if(etat === true){
                    alert("Vous avez déjà créé une page de cette catégorie")
                }else{
                    await  db.collection("parametres").doc().set({
                        titre : obj.titre,
                        categorie : obj.categorie,
                        contenu:content 
                 })  
                }
                  
            }else{
                 await db.collection("parametres").doc(obj.id).update({
                    titre : obj.titre,
                    categorie : obj.categorie,
                    contenu:content 
                 })
            }
            resetForm()
            setRecordForEdit(null)
            setOpenPopup(false)
            getParams()
            setNotify({
                    isOpen: true,
                    message: 'Validation avec Succes',
                    type: 'success'
              })
            }catch (error) {
            console.error(error);
          }
    }

    const openInPopup = item => {
        setRecordForEdit(item)
        setOpenPopup(true)
    }

    const onDelete = async (id) => {
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        })
        await db.collection("parametres").doc(id).delete();
        getParams()
        setNotify({
            isOpen: true,
            message: 'Suppression avec Success',
            type: 'error'
        })
    }

    useEffect(()=> {
        getParams();
    }, [])

    return (
        <Box p={2}>
                <PageHeader
                title="Gestion des Paramètres"
                subTitle="Editer les pages des paramètres (Conditions d'utilsation,Mentions légales, cookies,Données personnelles) "
                icon={<Build fontSize="large" />}
              
             
            />
               <Paper  >
                <Toolbar>
                    <Grid  container spacing={2}  xs={12}>
                        <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par titre"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchTitre}  
                            />
                       </Grid>
                       <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par catégorie"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchCat}  
                            />
                       </Grid>
                       
                    
                        
                      
                      
                       <Grid item lg={4} md={6} xs={12} >
                             <Controls.Button 
                                text="Ajouter"
                               
                                variant="outlined"
                                startIcon={<AddIcon/>}
                                onClick={() => { setOpenPopup(true); setRecordForEdit(null); }}
                                className={classes.centerButton}
                                
                            />
                        </Grid>
                      
                    </Grid>
                </Toolbar>
                  
                  <Grid className="container">
                    <TblContainer>
                        <TblHead/>
                        <TableBody>
                        {
                                recordsAfterPagingAndSorting().map((item,index) =>
                                    (<TableRow key={index}>
                                         <TableCell>{item.titre}</TableCell>
                                        <TableCell>{item.categorie}</TableCell>
                                        <TableCell>Ouvrir pour voir...</TableCell>
                                        <TableCell> 
                                            <Controls.ActionButton
                                                color="primary"
                                                onClick={() => { openInPopup(item) }}
                                              >
                                                <Edit fontSize="small" /> 
                                            </Controls.ActionButton>
                                            <Controls.ActionButton
                                                color="secondary"
                                                onClick={() => {
                                                    setConfirmDialog({
                                                        isOpen: true,
                                                        title: 'Vous ne pouvez pas supprimer cette information',
                                                        subTitle: "Cliquer sur non pour retourner",
                                                        onConfirm: () => { onDelete(item.id) }
                                                    })
                                                }}
                                              >
                                                <CloseIcon fontSize="small" />
                                            </Controls.ActionButton>
                                        </TableCell>
                                    </TableRow>)
                                )
                            }
                        </TableBody>
                    </TblContainer>
                    <TblPagination />
                    </Grid>
             </Paper>
             <Popup
                  title="Formulaire des paramètres"
                  openPopup={openPopup}
                  setOpenPopup={setOpenPopup}
             >
                 <ReglagesForm
                   recordForEdit={recordForEdit}
                   addOrEdit={addOrEdit}
                 />
                 
             </Popup>
             <Notification
                notify={notify}
                setNotify={setNotify}
            />
             <ConfirmDialog
                confirmDialog={confirmDialog}
                setConfirmDialog={setConfirmDialog}
             />
        </Box>
    )
}
  
export default TableReglages
