import React,{useState,useContext} from 'react'
import styled from 'styled-components'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import Footer from '../../components/Footer/Footer'
import PubLaterale from '../components/Publicite'
import Sidebar from '../components/Publicite/Sidebar'
import { 
    Paper,
    makeStyles,
    Container,
    Box,
    Grid,
    Typography,
    Button, 
    LinearProgress,
} from '@material-ui/core'
import { AuthContext } from '../../firebase/AuthContext';
import app from '../../firebase/base';
import BreadCrumbs from '../components/Breadcrumbs'
import { useHistory } from 'react-router-dom';
import { format } from 'date-fns'


const useStyles = makeStyles((theme) => ({
    rootG: {
      flexGrow: 2,
     
    },
    rootInfo: {
        backgroundColor: theme.palette.background.dark,
        
        paddingBottom: theme.spacing(2),
        paddingTop: theme.spacing(1)
      },
      root: {
        backgroundColor: theme.palette.background.dark,
        height: '100%',
        paddingBottom: theme.spacing(3),
        paddingTop: theme.spacing(3)
      },
      textPrimary:{
          color: '#008647',
        },
  
        buttonStyle: {
          backgroundColor: "#1a1359",
          color: "#fff",
        
          "&:hover":{
              backgroundColor: "#008649",
          }
      },
      rootInput: {
          '& > *': {
            margin: theme.spacing(1),
          },
        },
        input: {
          display: 'none',
        },
  }));

const HeadSection = styled.div`
   position:fixed;
   width:100%;
   z-index:10;
   background-color:#fff;
   box-shadow: 0 6px 20px rgba(10, 10, 10, 0.1);

`
const PageContent = styled.div`   
   padding-top: 185px;
   margin-left:70px;
   margin-bottom: 20px;

   @media screen and (max-width: 768px){
       margin-left:30px;
    }
  
`

const Postuler2 = (props) => {

    const classes = useStyles();
    const {currentUser} = useContext(AuthContext);
    const [image, setImage] = useState(null);
    const [progress, setProgress] = useState(0);
    const [error, setError] = useState("");
    const [isOpen, setIsOpen] = useState(false)

    const storage = app.storage();
    const db = app.firestore()
    const date = new Date();
    const navigate = useHistory();
  

    const toggle = () =>{
        setIsOpen(!isOpen)
    }


    const handleChange = e => {
        const file = e.target.files[0];
    
        if (file) {
          const fileType = file["type"];
          const validImageTypes = ["image/jpg", "image/jpeg", "image/png","application/pdf"];
          if (validImageTypes.includes(fileType)) {
            setError("");
            setImage(file);
          } else {
            console.log("error");
            setError("error please upload a image file");
          }
        }
      };
        
      const handleUpload = () => {
        if (image) {
          // add to image folder in firebase
          const uploadTask = storage.ref(`cv-candidat/${image.name}`).put(image);
          // Listen for state changes, errors, and completion of the upload.
          uploadTask.on(
            "state_changed",
            snapshot => {
              // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
              const progress = Math.round(
                (snapshot.bytesTransferred / snapshot.totalBytes) * 100
              );
              setProgress(progress);
            },
            error => {
              // error function ....
              console.log(error);
              setError(error);
            },
            () => {
              // complete function ....
              storage.ref("cv-candidat").child(image.name) // Upload the file and metadata
                .getDownloadURL() // get download url
                .then(url => {
                    db.collection('lettre_motivation').doc().set({
                        id_candidat: currentUser.uid,
                        id_recruteur: props.location.state.id_recruteur,
                        id_offre: props.location.state.id_offre,
                        lettre_motivation: url,
                        date_candidature : format(date, "dd/MM/yyyy"),
                    }).then(()=>{
                        alert("Vous avez postuler avec succès!!!")
                        navigate.push("/candidats/emplois")

                    })
                });
            }
          );
        } else {
          setError("Error please choose an image to upload");
        }
      };

    return (
        <>
         <ScrollToTop/>
            <HeadSection>
                <Sidebar  isOpen={isOpen}  toggle={toggle}/>
                <PubLaterale   toggle={toggle}/>
                <BreadCrumbs/>
            </HeadSection>
 
            <PageContent className={classes.rootG}>
             <Paper className={classes.root}>
                <Box
                    display="flex"
                    flexDirection="column"
                    height="100%"
                    justifyContent="center"
                >
                    <Container maxWidth="sm">
                    <Box mb={3}>
                        <Typography
                            color="textPrimary"
                            variant="h4"
                        >
                           Candidatez à votre offre d'emploi étape II
                        </Typography>

                        <Typography
                            color="textSecondary"
                            gutterBottom
                            variant="body2"
                        >
                           Téléchargez votre lettre de motivation
                        </Typography>
                        <LinearProgress color="secondary"  />
                        </Box>


                    <Grid
                        container
                        spacing={1}
                    >
                        <Grid item md={6}   xs={12}  >
                                <div  className={classes.rootInput}>
                                    <input                      
                                        className={classes.input}
                                        id="contained-button-file"
                                        type="file"
                                        onChange={handleChange} 
                                    />
                                    <label htmlFor="contained-button-file">
                                      <Button   variant="contained" color="primary" component="span">
                                        Télécharger la lettre de motivation
                                      </Button>
                                    </label>
                                </div>

                            </Grid>

                          </Grid>
                          <Box my={2}>
                        <Button
                            className={classes.buttonStyle}
                            onClick={handleUpload}
                            fullWidth
                            size="large"
                            type="submit"
                            variant="contained"
                        >
                            Envoyer
                        </Button>
                        </Box>
                    </Container>
                </Box>
            </Paper>
          </PageContent>
          <Footer/>  
        </>
    )
}

export default Postuler2
