import React,{useState,useEffect,useContext} from 'react'
import styled from 'styled-components'
import ProfilForm from './ProfilForm'
import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';
import { 
    Avatar,
    Box, 
    Card, 
    CardContent, 
    makeStyles, 
    Container, 
    Grid
} from '@material-ui/core'
import { toast } from "react-toastify";
import Controls from '../controls/Controls';
import { EditOutlined } from '@material-ui/icons';
import CardFull from '../Cards/CardFull';


const useStyles = makeStyles((theme)=> ({
    root:{
        
        paddingBottom: theme.spacing(3),
        paddingTop: theme.spacing(3)
    },
    avatar:{
        height:100,
        width:100

    }
}));

const CompteContent = styled.div`
  
    padding: 10px;
 

` 

const ProfilGroup = () => {

    const classes = useStyles()
    const {currentUser} = useContext(AuthContext)
    const [recruteur, setRecruteur] = useState([])
    const [currentID, setCurrenID] = useState("") 
    const db = app.firestore();
    const storage = app.storage();
  
    const edit = (obj,file) => { 
         
        if(file !== null ){
            const uploadTask = storage.ref(`user-image/${file.name}`).put(file);
            uploadTask.on('state_changed', snapshot => {
                const progress = (snapshot.bytesTransferred/snapshot.totalBytes) * 100;
                console.log(progress);

            }, err => {
                console.log(err.message)
            }, ()=>{
                storage.ref('user-image').child(file.name).getDownloadURL().then(url => {
                    db.collection('recruteurs').doc(currentID).update({
                        nom_recruteur: obj.nom_recruteur,
                        secteur_activite:obj.secteur_activite,
                        email_recruteur: obj.email_recruteur,
                        telephone_recruteur:obj.telephone_recruteur,
                        url_recruteur:obj.url_recruteur,
                        referent_recruteur: obj.referent_recruteur,
                        adresse_recruteur:obj.adresse_recruteur,
                        raison_sociale:obj.raison_sociale,
                        ville_recruteur:obj.ville_recruteur,
                        province_recruteur: obj.province_recruteur,
                        description_recruteur:obj.description_recruteur,
                        image_url: url
                    })
                }).then(()=>{
                    toast("Profil mis à jour avec succès!!!", {
                        type: "info",
                    });
                    setCurrenID("");
                })
            })

        }else{
            
                db.collection('recruteurs').doc(currentID).update(obj)
                toast("Profil mis à jour avec succès!!!", {
                      type: "info",
                  });
                setCurrenID("");
           
           
        }

         
    }
   

     const getRecruteur = async () => {
            db.collection("recruteurs")
                .where("id_utilisateur", "==", currentUser.uid)
                .onSnapshot((querySnapshot) => {
                    var list = [];
                    querySnapshot.forEach((doc) => {
                        list.push({ ...doc.data(), id: doc.id })
                    });
                    setRecruteur(list)
                    
                });
     }

     useEffect(()=> {
        getRecruteur();
      }, [])




    return (
        <>
         <CompteContent>
            <Container maxWidth="lg" >
                    <Grid container spacing={3}>
                            <Grid item lg={4} md={6} xs={12}>
                            <Card className={classes.root} >
                                <CardContent >
                                {recruteur.map((item,index) => (
                                        <Box
                                            alignItems="center"
                                            display="flex"
                                            flexDirection="column"
                                        >
                                        <Avatar
                                            className={classes.avatar}
                                            src={item.image_url}
                                        />
                                        
                                             <CardFull key={index}  item={item}/>
                                                     <Box>
                                                        <Controls.ActionButton
                                                            color="primary"
                                                            onClick={() => {setCurrenID(item.id)}}
                                                        >
                                                            <EditOutlined fontSize="medium"/> Editer
                                                            </Controls.ActionButton>
                                                     </Box> 

                                        </Box>
                                         ))
                                        }   
                                    </CardContent>
                                </Card>
                            </Grid>

                            <Grid item lg={8} md={6} xs={12} >
                                <ProfilForm   {...{edit,currentID,recruteur}}/>
                            </Grid>
                    </Grid>
            </Container>
          </CompteContent>
        </>
    )
}

export default ProfilGroup
