import React,{useState} from 'react'
import NavbarHome from '../components/NavbarHome'
import SidebarAuth from '../components/NavbarHome/SidebarAuth'
import ScrollToTop from '../components/Signin/ScrollToTop'
import Footer from '../components/Footer/Footer'
import ContactezNousForm from '../components/Contact/ContactezNousForm'
import SecondNavbar from '../components/NavbarHome/SecondNavbar'



const ContactezNous = () => {

    const [isOpen, setIsOpen] = useState(false)

    const toggle = () =>{
        setIsOpen(!isOpen)
    }

    return (
        <>
           <ScrollToTop/>
            <SidebarAuth isOpen={isOpen}  toggle={toggle}/>
            <SecondNavbar  toggle={toggle}/>
            <ContactezNousForm/>
            <Footer/>  
        </>
    )
}

export default ContactezNous
