import React,{useState,useContext,useEffect} from 'react'
import styled from 'styled-components'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import Footer from '../../components/Footer/Footer'
import Sidebar from '../components/Publicite/Sidebar'
import { Container,CardActions,Card,CardContent,Avatar,Box,Link,CardHeader,Typography,Grid,makeStyles } from '@material-ui/core'
import NavOffre from '../components/Publicite/NavOffre'
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { ArrowRightAlt } from '@material-ui/icons';
import { Email} from '@material-ui/icons'
import { FiHeart } from 'react-icons/fi'
import { useHistory} from 'react-router-dom';
import Button from '@material-ui/core/Button';
import app from '../../firebase/base';
import { AuthContext } from '../../firebase/AuthContext';
import {
    FacebookShareButton,
    LineShareButton,
    LinkedinShareButton,
    PinterestShareButton,
    TwitterShareButton,
    WhatsappShareButton,
  } from "react-share";

import {
    EmailIcon,
    FacebookIcon,
    LinkedinIcon,
    PinterestIcon,
    TwitterIcon,
    WhatsappIcon,
  } from "react-share";
 

const HeadSection = styled.div`
   position:fixed;
   width:100%;
   z-index:10;
   background-color:#fff;
   box-shadow: 0 6px 20px rgba(10, 10, 10, 0.1);

`
const PageContent = styled.div`   
   padding-top: 115px;
   margin-left:70px; 
   margin-bottom: 20px;

   @media screen and (max-width: 768px){
       margin-left:30px;
    }
  
`

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 2,
     
    },
    contentStyle:{
        marginBottom: "50px"
    },
    rootList: {
        width: '100%',
        maxWidth: 360,
        backgroundColor: theme.palette.background.paper,
       
      },

      bullet: {
        fontWeight:'bold'
      },
      iconStyle:{
          color:'#1a1359'
      },
      rootCard: {
        minWidth: 275,
      },
      bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
      },
      title: {
        fontSize: 14,
      },
      pos: {
        marginBottom: 12,
      },
      avatar:{
        height:100,
        width:100,
        marginBottom:'10px'
    
    },
    info:{
        '& > *' :{
            margin: '5px',
    
        },
       
    },
    link:{
          justifyContent:'center',
          alignItems:'center',
          display: 'flex',
          cursor: 'pointer',
          color:'#1a1359',
          
    },
    btnPostuer:{
        backgroundColor:'#008647',
        color:'#fff',

        '&:hover': {
            backgroundColor:"#fbf000",
            color:'#1a1359'
            
        }
    }
  }));

const Offre = (props) => {
    const classes = useStyles();

    const [isOpen, setIsOpen] = useState(false)
    const db = app.firestore()
    const {currentUser} = useContext(AuthContext);
    const [recruteur, setRecruteur] = useState([])
    const [role, setRole] = useState("");
    const [etat, setEtat] = useState("");
    const navigate = useHistory();
    const {state} = props.location

    const toggle = () =>{
        setIsOpen(!isOpen)
    }
    console.log(state)

    const getRole = async () => {
        if(currentUser){
            db.collection("utilisateurs")
            .where("id_utilisateur", "==", currentUser.uid)
            .onSnapshot((querySnapshot) => {
                let user;
                querySnapshot.forEach((doc) => {
                user = doc.data().role_utilisateur;
                });
              setRole(user);
              if(role == "candidat"){
                db.collection("candidatures")
                  .where("id_utilisateur", "==", currentUser.uid)
                  .where("id_offre", "==", state.id_offre)
                  .onSnapshot((querySnapshot) => {
                      querySnapshot.forEach((doc) => {
                        if (doc.exists) {
                            setEtat("vrai")
                        } else {
                           setEtat("faux")
                        }
                    
                      });
                  });}
                
            }); 
      
        //candidatures
        
        }        
    } 


    const getRecruteur = async () => {
        if(state.id_recruteur !== ""){
                await db.collection("recruteurs")
                .where("id_utilisateur", "==", state.id_recruteur) 
                .onSnapshot((querySnapshot) => {
                    var list = []
                    querySnapshot.forEach((doc) => {
                        list.push({...doc.data(), id: doc.id })
                    
                    });
                    setRecruteur(list)              
                });
                }
        }
 
      useEffect(()=> {
        getRecruteur();
        getRole();
    }, [])


    const verification = () => {
        if(!currentUser){
            navigate.push('/candidats/connexion', { replace: true });
        }else{
            if(role !== "candidat"){
                alert("Vous n'êtes pas connecté en tant que candidat") 
            }else{
                if(etat == "vrai"){
                    alert("Vous avez déjà postuler à cette offre!")
                }else{
                    navigate.push({pathname:"/candidats/postuler", state:{
                        id_offre:state.id_offre, 
                        id_recruteur:state.id_recruteur, 
                        date_pub: state.date_debut,
                        titre_offre:state.titre_offre,
                        type_contrat: state.type_contrat,
                        localisation: state.ville_offre,
                        niveau_requis:state.experience,
                        niveau_etude: state.niveau_etude,
                        secteur_activite: state.secteur_activite,
                        description_offre: state.description_offre,
                        responsabilite_offre: state.responsabilite_offre
                     }});
                }
            }
        }
    }


    return (
        <>
            <ScrollToTop/> 
            <HeadSection>
                <Sidebar  isOpen={isOpen}  toggle={toggle}/>
                <NavOffre   toggle={toggle}/>
            </HeadSection> 
            <PageContent className={classes.root}>
                <Container maxWidth={false} >
                    <Grid container spacing={3} lg={12} md={12}  xs={12}>
                            
                            <Grid item lg={4} md={6}  xs={12} >
                            <Card>
                                    <List component="nav" className={classes.rootList} aria-label="contacts">
                                    <ListItem button>
                                        <Typography  variant="h5">
                                        Infos clés
                                        </Typography>
                                        
                                    </ListItem>
                                    <ListItem button>
                                        <ListItemIcon>
                                        <ArrowRightAlt className={classes.iconStyle} />
                                        </ListItemIcon>
                                        <ListItemText primary="Titre de l'offre" secondary={state.titre_offre} />
                                    </ListItem>
                                    <ListItem button>
                                    <ListItemIcon>
                                        <ArrowRightAlt className={classes.iconStyle}  />
                                        </ListItemIcon>
                                        <ListItemText  primary="Date de publication" secondary={state.date_pub} />
                                    </ListItem>
                                    <ListItem button>
                                    <ListItemIcon>
                                        <ArrowRightAlt className={classes.iconStyle}  />
                                        </ListItemIcon>
                                        <ListItemText  primary="Niveau de formation requis" secondary={state.niveau_etude} />
                                    </ListItem>
                                    <ListItem button>
                                    <ListItemIcon>
                                        <ArrowRightAlt className={classes.iconStyle}  />
                                        </ListItemIcon>
                                        <ListItemText  primary="Type de contrat" secondary={state.type_contrat.toUpperCase()} />
                                    </ListItem>
                                    <ListItem button>
                                    <ListItemIcon>
                                        <ArrowRightAlt className={classes.iconStyle}  />
                                        </ListItemIcon>
                                        <ListItemText  primary="Secteur d'activité" secondary={state.secteur_activite} />
                                    </ListItem>
                                    <ListItem button>
                                    <ListItemIcon>
                                        <ArrowRightAlt className={classes.iconStyle}  />
                                        </ListItemIcon>
                                        <ListItemText  primary="Localisation du poste" secondary={state.localisation} />
                                    </ListItem>
                                    <ListItem button>
                                    <ListItemIcon>
                                        <ArrowRightAlt className={classes.iconStyle}  />
                                        </ListItemIcon>
                                        <ListItemText  primary="Années d'expérience" secondary={state.niveau_requis} />
                                    </ListItem>
                                    </List>
                                    </Card>
                            </Grid>
                            <Grid item lg={4} md={6}  xs={12} >
                              <Card className={classes.rootCard}>
                                    <CardContent>
                                       <Typography variant="h5" component="h2">
                                        Description de l'offre
                                        </Typography>
                                        <Typography variant="body2" component="p" align="justify" >
                                           {state.description_offre}
                                        </Typography>
                                        <Typography variant="h5" component="h2">
                                          Responsabilités
                                        </Typography>
                                        <Typography variant="body2" component="p" align="justify" >
                                           {state.responsabilite_offre}
                                        </Typography>
                                    
                                    </CardContent>
                                    </Card>
                            </Grid>
                            <Grid item lg={4} md={6}  xs={12} >
                    { recruteur.map((item,index) =>
                                (
                            <Card className={classes.rootCard}>

                              
                               
                              
                                <CardHeader key={index} title="  Information de l'entreprise" >
                                       
                                    </CardHeader>
                                    <Avatar  className={classes.avatar}  src={item.image_url} />
                                <CardContent>
                                   <Box  display="flex"  className={classes.info}>
                                        <Typography className={classes.bold}  variant="body3">Société:</Typography>
                                        <Typography variant="body1" align="left" >{item.nom_recruteur} </Typography>
                                    </Box>
                                    <Box  display="flex"  className={classes.info}>
                                        <Typography className={classes.bold}  variant="body3">Contact:</Typography>
                                        <Typography variant="body1" align="left">Contact: {item.telephone_recruteur} </Typography>
                                    </Box>
                                   
                                    <Box  display="flex"  className={classes.info}>
                                        <Typography className={classes.bold}  variant="body3">Site web:</Typography>
                                        <Typography className={classes.bold}  align="justify" variant="body2">
                                            
                                            <Link href={item.url_recruteur} target="_blank" rel="noopener" className={classes.link}> 
                                            {item.url_recruteur}
                                            </Link>
                                        </Typography>
                                    </Box>
                                    <Box  display="flex"  className={classes.info}>
                                        <Typography className={classes.bold}  variant="body3">Adresse:</Typography>
                                        <Typography className={classes.bold}  align="justify" variant="body2">
                                            {item.adresse_recruteur} 
                                        </Typography>
                                    </Box>
                                    <Typography variant="body2" component="p" align="justify" >
                                        Lorem ipsum dolor sit, amet consectetur adipisicing elit.
                                        Quis provident, ipsa aperiam eaque deserunt dicta sint optio
                                        deleniti quaerat. Magni, architecto enim velit magnam
                                        quisquam assumenda veritatis doloremque maxime nam!
                                    </Typography>
                                    <Box  display="flex"  className={classes.info}>
                                        <Link className={classes.link}> <Email/>  Email</Link>
                                        <Link  className={classes.link}> <FiHeart/> Sauvegarder</Link>
                                    </Box>
                                
                                </CardContent>
                                  
                               
                                
                                <CardActions>
                                    <Button 
                                        size="small"
                                        className={classes.btnPostuer}
                                        onClick={() => {verification()} }
                                    >
                                        Postuler
                                    </Button>
                                    <FacebookShareButton 
                                        url="https://jobsgabon.com/candidats/offre/"
                                        quote={state.titre_offre}
                                        hashtag="#JOBSGABON #RECHERCHEEMPLOI #EMPLOIGABON"
                                    >
                                        <FacebookIcon size={30} logoFillcolor="white" round={true}></FacebookIcon>
                                    </FacebookShareButton>
                                    <LinkedinShareButton 
                                        url="https://jobsgabon.com/candidats/offre/"
                                        quote={state.titre_offre}
                                        hashtag="#JOBSGABON #RECHERCHEEMPLOI #EMPLOIGABON"
                                    >
                                        <LinkedinIcon size={30} logoFillcolor="white" round={true}></LinkedinIcon>
                                    </LinkedinShareButton>
                                    <PinterestShareButton 
                                        url="https://jobsgabon.com/candidats/offre/"
                                        quote={state.titre_offre}
                                        hashtag="#JOBSGABON #RECHERCHEEMPLOI #EMPLOIGABON"
                                    >
                                        <PinterestIcon size={30} logoFillcolor="white" round={true}></PinterestIcon>
                                    </PinterestShareButton>
                                    <TwitterShareButton 
                                        url="https://jobsgabon.com/candidats/offre/"
                                        quote={state.titre_offre}
                                        hashtag="#JOBSGABON #RECHERCHEEMPLOI #EMPLOIGABON"
                                    >
                                        <TwitterIcon size={30} logoFillcolor="white" round={true}></TwitterIcon>
                                    </TwitterShareButton>
                                    <WhatsappShareButton 
                                        url="https://jobsgabon.com/candidats/offre/"
                                        quote={state.titre_offre}
                                        hashtag="#JOBSGABON #RECHERCHEEMPLOI #EMPLOIGABON"
                                    >
                                        <WhatsappIcon size={30} logoFillcolor="white" round={true}></WhatsappIcon>
                                    </WhatsappShareButton>
                                </CardActions>
                                </Card>
                                 ))}  
                            </Grid>
                    </Grid>
                </Container>
            </PageContent>
            <Footer/>
        </>
    ) 
}

export default Offre
