import React from 'react';
import { makeStyles,
        Card,
        CardHeader,
        CardMedia,
        CardContent,
        CardActions,
        Collapse,
        Avatar,
        Typography,
        Button
} from '@material-ui/core';
import clsx from 'clsx';
import IconButton from '@material-ui/core/IconButton';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { Favorite, Share } from '@material-ui/icons';
import { useHistory} from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: '100%',
    
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
    color: '#1a1359'
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: '#1a1359', 
    fontSize: '12px',
    
  },
  textLink: {
    color: '#1a1359',

   ' &:hover':{
      color: '#008647',
    },}

}));

const CardFull = (props) => {


  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const navigate = useHistory();

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const goTo = () => {
    navigate.push({pathname:`${props.lien}`, state:{
              titre:props.titre_poste
    } })
  }

  return (

    
    <Card className={classes.root}>
      <CardHeader
        avatar={
          <Avatar aria-label="recipe" className={classes.avatar}>
            {props.categorie}
          </Avatar>
        }
        action={
          <IconButton aria-label="settings">
            <MoreVertIcon />
          </IconButton>
        }
        title={props.titre_poste}
        subheader={props.date_pub}
      />
      <CardMedia
        className={classes.media}
        image={props.image_poste}
        title={props.titre_poste}
      />
      <CardContent>
        <Typography variant="body2" color="textPrimary" align="justify" component="p">
             {props.contenu.substring(0, 200)}...
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <Button onClick={()=>navigate.push(`${props.lien}/${props.titre_poste}`)} size="small" className={classes.textLink} color="primary">
          Lire plus
        </Button>
        
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          <Typography paragraph>Contenu:</Typography>
          <Typography align="justify" paragraph>
            {props.contenu}
          </Typography>
        </CardContent>
      </Collapse>
     </Card> 
  
  );
}

export default CardFull;