import React,{useState,useEffect} from 'react'
import { 
    Container,
    Grid ,
    Avatar,
    Box, 
    Card, 
    CardContent,
    makeStyles, 
    } from '@material-ui/core'
import styled from 'styled-components'
import ProfilForm from '../Forms/ProfilForm'
import { toast } from "react-toastify";
import app from '../../../firebase/base'; 
import Controls from '../controls/Controls';
import { EditOutlined } from '@material-ui/icons'
import CardFull from '../Cards/CardFull';


const useStyles = makeStyles((theme)=> ({
    root:{
        
        paddingBottom: theme.spacing(3),
        paddingTop: theme.spacing(3),
       
    },
    avatar:{
        height:100,
        width:100

    },
     detailPro:{
         justifyContent:'center',
         alignItems:'center'
     }
}));

const CompteContent = styled.div`
  
    padding: 10px;


` 

const ProfilGroup = () => {

    const classes = useStyles();
    const [profil, setProfil] = useState([]);
    const [currentID, setCurrentID] = useState("");
    const db = app.firestore();
    const storage = app.storage();
    var indice;

    const getProfil = async () => {
        db.collection("jobsgabon")
          .onSnapshot((querySnapshot) => {
              var listProfil = [];
              querySnapshot.forEach((doc) => {
                if(doc.exists){
                    listProfil.push({ ...doc.data(), id: doc.id })
                    indice = 1;
                }else
                    indice = 0;     
              });
              setProfil(listProfil); 
            
          });
     } 
 
     useEffect(() => {
        getProfil(); 
      }, [])
 
     const addOrEdit = async (obj,file) => {
        console.log(obj)
        if(currentID == ""){
            if(indice == 1){
                toast("Echec! Vous avez déjà ajouté un profil, vous pouvez le modifier", {
                    type: "info",
                });

            }else{

                if(file !== null){
                    const uploadTask = storage.ref(`user-image/${file.name}`).put(file);
                    uploadTask.on('state_changed', snapshot => {
                        const progress = (snapshot.bytesTransferred/snapshot.totalBytes) * 100;
                        console.log(progress);
        
                    }, err => {
                        console.log(err.message)
                    }, ()=>{
                        storage.ref('user-image').child(file.name).getDownloadURL().then(url => {
                            db.collection('jobsgabon').doc().set({
                                nom: obj.nom,
                                secteur_activite:obj.secteur_activite,
                                telephone:obj.telephone,
                                email:obj.email,
                                site_internet:obj.site_internet,
                                description:obj.description,
                                adresse:obj.adresse,
                                youtube:obj.youtube,
                                facebook:obj.facebook,
                                instagram:obj.instagram,
                                twitter:obj.twitter,
                                linkedin:obj.linkedin,
                                logo:url
                            })
                        }).then(()=>{
                            toast("Informations ajouté avec succès", {
                                type: "success",
                                })
                        })
                    })
                }else{

                    await db.collection("jobsgabon").doc().set(obj);
                        toast("Informations ajouté avec succès", {
                        type: "success",
                    });
                }

            }

        }else{

            if(file !== null){
                const uploadTask = storage.ref(`user-image/${file.name}`).put(file);
                uploadTask.on('state_changed', snapshot => {
                    const progress = (snapshot.bytesTransferred/snapshot.totalBytes) * 100;
                    console.log(progress);
    
                }, err => {
                    console.log(err.message)
                }, ()=>{
                    storage.ref('user-image').child(file.name).getDownloadURL().then(url => {
                        db.collection('jobsgabon').doc(currentID).update({
                            nom: obj.nom,
                            secteur_activite:obj.secteur_activite,
                            telephone:obj.telephone,
                            email:obj.email,
                            site_internet:obj.site_internet,
                            description:obj.description,
                            adresse:obj.adresse,
                            youtube:obj.youtube,
                            facebook:obj.facebook,
                            instagram:obj.instagram,
                            twitter:obj.twitter,
                            linkedin:obj.linkedin,
                            logo:url
                        })
                    }).then(()=>{
                        toast("Mise à jour ajouté avec succès !", {
                            type: "success",
                            })
                        setCurrentID("")
                    })
                })

            }else{ 
                await db.collection("jobsgabon").doc(currentID).update(obj);
                toast("Mise à jour avec succès !!", {
                    type: "info",
                    });
                setCurrentID("");

            }

        }

     }

    

    return (
        <>
         <CompteContent>
            <Container maxWidth="lg" >
                    <Grid container  spacing={2}>
                         <Grid lg={4} md={6} xs={12}>
                            <Card
                                    className={classes.root}
                                >
                                <CardContent >
                                  {profil.map((item,index) => (
                                    <Box 
                                             alignItems="center"
                                             display="flex"
                                             flexDirection="row"
                                             key={index}
                                        >
                                        <Grid  justify="center" container lg={12}  >
                                            <Avatar 
                                                className={classes.avatar}
                                                src={item.logo}
                                            />
                                                
                                                <CardFull  item={item} />
                                                <Box>
                                                    <Controls.ActionButton
                                                        color="primary"
                                                        onClick={() => {setCurrentID(item.id)}}
                                                    >
                                                       
                                                        <EditOutlined fontSize="default"/> Editer
                                                    </Controls.ActionButton>
                                                        
                                                </Box>

                                         </Grid>
                        
                                        </Box>
                                      ))
                                    }
                                    </CardContent>
                                </Card>
                            </Grid>

                            <Grid item lg={8} md={6} xs={12} >
                                <ProfilForm  {...{addOrEdit,currentID,profil}} />
                            </Grid>
                    </Grid>
            </Container>
          </CompteContent>
        </>
    )
}

export default ProfilGroup
