import React,{useState} from 'react'
import SidebarAuth from '../components/NavbarHome/SidebarAuth'
import ScrollToTop from '../components/Signin/ScrollToTop'
import SecondNavbar from '../components/NavbarHome/SecondNavbar'
import Footer from '../components/Footer/Footer'
import ForgotPassword from '../components/Auth/ForgotPassword'

const MotDepasseOublie = () => {

    const [isOpen, setIsOpen] = useState(false)
    const toggle = () =>{
        setIsOpen(!isOpen)
    }


    return (
        <>
        <ScrollToTop/>
            <SidebarAuth isOpen={isOpen}  toggle={toggle}/>
            <SecondNavbar  toggle={toggle}/>
            <ForgotPassword/>
            <Footer/> 
        </>
    )
}

export default MotDepasseOublie
