import React, {useState,useEffect} from 'react'
import styled from 'styled-components'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import Footer from '../../components/Footer/Footer'
import BreadCrumbs from '../components/Breadcrumbs'
import NavbarBottom from '../components/NavbarBottom'
import Sidebar from '../components/Publicite/Sidebar'
import PubLaterale from '../components/Publicite'
import { Container,makeStyles, Grid, Typography, Box, Card, CardHeader, CardContent, CardMedia } from '@material-ui/core'
import SearchBar from '../components/SearchBar/SearchBar'
import app from '../../firebase/base';
import ListPost from '../components/List/ListPost'
import {useParams} from 'react-router-dom'
import PubSlide from '../../components/Slides/PubSlide'


const HeadSection = styled.div`
   position:fixed; 
   width:100%;
   z-index:10;
   background-color:#fff;
   box-shadow: 0 6px 20px rgba(10, 10, 10, 0.1);

`

const PageContent = styled.div`   
   padding-top: 185px;
   margin-left:30px;
   margin-bottom: 20px;

   @media screen and (max-width: 768px){
       margin-left:5px;
    }
  
`

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 2,
     
    },
    floatedcontainer:{
        width: "100%"
    },
    floated : {
        float: "right",
        width: "350px",
        height: "250px",
        paddingLeft:"5px"
    }
  }));


const Single_actu = (props) => {

    const classes = useStyles();
    

    const [isOpen, setIsOpen] = useState(false)
    const [actualites, setActualites] = useState([])
    const [conseils, setConseils] = useState([])
    const [datactu, setDatactu] = useState([])
    const [etat,setEtat] = useState(null)

    const [etatPub, setEtatPub] = useState(null);
    const [pub, setPub] = useState([])
   // const {titre} = props.location.state;
    const {titre_poste} = useParams();
    const db = app.firestore(); 

    const toggle = () =>{
        setIsOpen(!isOpen)
    }
  console.log(titre_poste)
    const fetchActu = async (titre) => {
        if(titre !== ""){
            await db.collection("postes")
            .where("titre_poste", "==", titre) 
            .onSnapshot((querySnapshot) => {
                var list = []
                querySnapshot.forEach((doc) => {
                 list.push({...doc.data(), id: doc.id })
                });
                setDatactu(list) 
                setEtat(true)             
            });
        }else{
            await db.collection("postes")
            .where("categorie", "==", "actualite") 
            .onSnapshot((querySnapshot) => {
                var list = []
                querySnapshot.forEach((doc) => {
                 list.push({...doc.data(), id: doc.id })
                });
                setDatactu(list)
                setEtat(false)                
            });
        }
     
    }
    

    const getActu = async () => { 
        await db.collection("postes")
           .where("categorie", "==", "actualite") 
           .onSnapshot((querySnapshot) => {
               var list = []
               querySnapshot.forEach((doc) => {
                list.push({...doc.data(), id: doc.id })
               });
               setActualites(list)              
           });
        }

        const getConseil = async () => {
            await db.collection("postes")
               .where("categorie", "==", "conseil") 
               .onSnapshot((querySnapshot) => {
                   var list = []
                   querySnapshot.forEach((doc) => {
                    list.push({...doc.data(), id: doc.id })
                   });
                   setConseils(list)              
               });
            
            }
            const getPub = async () => {
                await db.collection("publicites")
                   .where("categorie", "==", "laterale")
                   .where("visibilite", "==", "on") 
                   .onSnapshot((querySnapshot) => {
                       var list = []
                       querySnapshot.forEach((doc) => {
                        if(doc.exists){
                            setEtatPub(true)
                            list.push({...doc.data(), id: doc.id })   
                        }else{
                            setEtatPub(false)
                        }
                       
                       });
                       setPub(list)              
                   });
                
                }
    
      useEffect(()=> {
        getPub();
        getActu()
        getConseil()
        fetchActu(titre_poste);
    }, [titre_poste])
    return (
         <>
            <ScrollToTop/>
            <HeadSection>
                <Sidebar  isOpen={isOpen}  toggle={toggle}/>
                <PubLaterale   toggle={toggle}/>
                <NavbarBottom/>
                <BreadCrumbs/>
            </HeadSection>

            <PageContent className={classes.root}>
                    <Container>                    
                            <Grid container spacing={3} >
                                <Grid xs={12}>
                                    <SearchBar/>
                                </Grid>
                                <Grid lg={8} md={6}  xs={12}> 
                                    <Box mt={2}>
                                        {etat && (
                                        <>
                                        {datactu.map((item,index) =>(
                                        <Card key={index} >
                                            <CardHeader  title={item.titre_poste} subheader={`Par  ${item.auteur} le ${item.date_pub}`} />
                                            <CardContent>
                                            <Typography className={classes.floatedcontainer} align="justify" >
                                            <CardMedia  
                                                image={item.image_poste}
                                                title={item.titre_poste}
                                                height="200"
                                                component="img"
                                                alt=""
                                                className={classes.floated}
                                            />
                                                {item.contenu}
                                            </Typography>
                                            </CardContent>

                                        </Card>
                                         )) }
                                         </>
                                        )}
                                    </Box>
                                    <Box mt={2}>
                                        {!etat && (
                                        <>
                                        {datactu.map((item,index) =>(
                                        <Card key={index} >
                                            <CardHeader  title={item.titre_poste} subheader={`Par  ${item.auteur} le ${item.date_pub}`} />
                                            <CardContent>
                                            <Typography className={classes.floatedcontainer} align="justify" >
                                            <CardMedia  
                                                image={item.image_poste}
                                                title={item.titre_poste}
                                                height="200"
                                                component="img"
                                                alt=""
                                                className={classes.floated}
                                            />
                                                {item.contenu}
                                            </Typography>
                                            </CardContent>
                                           

                                        </Card>
                                       
                                         )) }
                                      </>
                                        )}
                                    </Box>
                                    
                                </Grid>
                                <Grid lg={4} md={6}  xs={12}>
                                    <Box mt={1} mb={1}>
                                       <ListPost  actualites={actualites} conseils={conseils} />
                                    </Box>
                                    {etatPub === true &&
                                        <>
                                            <PubSlide  dataPub={pub} />
                                        </>
                                     }
                                </Grid>
                            </Grid>

                    </Container>
            </PageContent>
            <Footer/>  
        </>
    )
}

export default Single_actu
