import React,{useState,useEffect,useContext}  from 'react'
import { Box, Button, Dialog,
    DialogActions, DialogContent,
    DialogTitle,
    Divider,
    Grid,
     IconButton, Link, makeStyles,
    Typography,
} from '@material-ui/core'
import {Close as CloseIcon, Email} from '@material-ui/icons'
import { FiHeart } from 'react-icons/fi'
import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';
import { useHistory} from 'react-router-dom';


const useStyles = makeStyles({
    info:{
        '& > *' :{
            margin: '5px',

        },

    },
    link:{
          justifyContent:'center',
          alignItems:'center',
          display: 'flex',
          cursor: 'pointer',
          color:'#1a1359',

    },
    titre:{
        color: '#008647'
     },
     bold:{
         fontWeight: 'bold'
     },
     boxTitle:{
        backgroundColor:"#008647"
    },
    iconColor:{
        color:"#fff"
    },
    buttonStyle:{
        backgroundColor:"#1a1359",
        color:'#fff',

        '&:hover': {
            backgroundColor:"#008647",
            border: '1px solid #fff'
        }

    }
})

const ViewJobModal = (props) => {

    const classes = useStyles()
    const db = app.firestore()
    const {currentUser} = useContext(AuthContext);
    const [recruteur, setRecruteur] = useState([])
    const [role, setRole] = useState("");
    const [etat, setEtat] = useState(null);
    const navigate = useHistory();
    const {utilisateur}  = props


    const getRole = async () => {
        if(currentUser){
            db.collection("utilisateurs")
            .where("id_utilisateur", "==", currentUser.uid)
            .onSnapshot((querySnapshot) => {
                let user;
                querySnapshot.forEach((doc) => {
                user = doc.data().role_utilisateur;
                });
              setRole(user);

            });

        //candidatures
        if(role == "candidat"){
            db.collection("candidatures")
              .where("id_candidat", "==", currentUser.uid)
              .where("id_offre", "==", props.job.id)
              .onSnapshot((querySnapshot) => {
                  querySnapshot.forEach((doc) => {
                    if (doc.exists) {
                        setEtat(true)
                    } else {
                       setEtat(false)
                    }

                  });
              });}
        }
    }


    const getRecruteur = async () => {
        if(utilisateur !== ""){
                await db.collection("recruteurs")
                .where("id_utilisateur", "==", utilisateur)
                .onSnapshot((querySnapshot) => {
                    var list = []
                    querySnapshot.forEach((doc) => {
                        list.push({...doc.data(), id: doc.id })

                    });
                    setRecruteur(list)
                });
                }
        }

      useEffect(()=> {
        getRecruteur();
        getRole();
    }, [])



    const verification = () => {
        if(!currentUser){
            navigate.push('/candidats/connexion', { replace: true });
        }else{
            if(role !== "candidat"){
                alert("Vous n'êtes pas connecté en tant que candidat")
            }else{
                if(etat === true){
                    alert("Vous avez déjà postuler à cette offre!")
                }else{
                    navigate.push({pathname:"/candidats/postuler", state:{
                        id_offre:props.job.id,
                        id_recruteur:utilisateur,
                        date_pub: props.job.date_debut,
                        titre_offre:props.job.titre_offre,
                        type_contrat: props.job.type_contrat,
                        localisation: props.job.ville_offre,
                        niveau_requis:props.job.experience,
                        niveau_etude: props.job.niveau_etude,
                        secteur_activite: props.job.secteur_activite,
                        description_offre: props.job.description_offre
                     }});
                }
            }
        }



    }




    return (
        <Dialog open={!!Object.keys(props.job).length}   fullWidth="sm"  fullScreen>
        <DialogTitle  className={classes.boxTitle} >
            <Box   display="flex" justifyContent="space-between" alignItems="center">
                 <Typography  variant="h6" >{props.job.titre_offre}</Typography>
                <IconButton onClick={props.closeModal} >
                    <CloseIcon className={classes.iconColor} />
                </IconButton>
            </Box>
        </DialogTitle>
        <Divider/>
        <DialogContent>
            <Grid container spacing={3} lg={12} md={12}  xs={12}>

                <Grid item lg={4} md={8} xs={12}>
                      <Box>
                        <Box  className={classes.info}>
                            <Typography className={classes.titre} variant="h6" >Infros clés</Typography>
                        </Box>
                      </Box>
                    <Box>
                        <Box display="flex"  className={classes.info}>
                            <Typography variant="body3">Titre de l'offre:</Typography>
                            <Typography className={classes.bold} variant="body2">{props.job.titre_offre}</Typography>
                        </Box>
                    </Box>
                    <Box>
                        <Box display="flex"  className={classes.info}>
                            <Typography variant="body3">Date de publication:</Typography>
                            <Typography className={classes.bold}  variant="body2">
                                {props.job.date_debut}
                            </Typography>
                        </Box>
                    </Box>
                    <Box>
                        <Box display="flex"  className={classes.info}>
                            <Typography variant="body3">Niveau de Formation:</Typography>
                            <Typography className={classes.bold}  variant="body2">{props.job.niveau_etude}</Typography>
                        </Box>
                    </Box>

                    <Box>
                        <Box display="flex"  className={classes.info}>
                            <Typography variant="body3">Type de contrat:</Typography>
                            <Typography className={classes.bold}  variant="body2">{props.job.type_contrat}</Typography>
                        </Box>
                    </Box>

                    <Box>
                        <Box display="flex"  className={classes.info}>
                            <Typography variant="body3">Secteur d'activité:</Typography>
                            <Typography className={classes.bold}  variant="body2">{props.job.secteur_activite}</Typography>
                        </Box>
                    </Box>
                    <Box>
                        <Box display="flex" className={classes.info}>
                            <Typography variant="body3">Localisation du poste:</Typography>
                            <Typography className={classes.bold}  variant="body2">{props.job.ville_offre}</Typography>
                        </Box>
                    </Box>
                    <Box>
                        <Box display="flex" className={classes.info}>
                            <Typography variant="body3">Année d'expérience:</Typography>
                            <Typography className={classes.bold}  variant="body2">{props.job.experience}</Typography>
                        </Box>
                    </Box>


                </Grid>

                <Grid item lg={4} md={8} xs={12}>
                     <Box>
                        <Box  className={classes.info}>
                            <Typography className={classes.titre}  variant="h6" >Description</Typography>
                            <Typography align="justify" variant="body3">{props.job.description_offre}</Typography>
                         </Box>
                    </Box>
                </Grid>

                <Grid item lg={4} md={8} xs={12}>

            {recruteur.map((item,index) =>
                   (
                    <Grid key={index}>
                    <Box >
                        <Box  className={classes.info}>
                            <Typography className={classes.titre}  variant="h6" >Entreprise</Typography>
                        </Box>
                    </Box>
                    <Box>
                        <Box  className={classes.info}>
                             <img src={item.image_url} style={{width:'90px'}} alt="société"/>
                        </Box>
                    </Box>
                    <Box>
                        <Box    className={classes.info}>
                            <Typography align="justify" className={classes.bold}  variant="h5">
                                {item.nom_recruteur}
                            </Typography>
                        </Box>
                    </Box>
                    <Box>
                        <Box  display="flex"  className={classes.info}>
                            <Typography className={classes.bold}  variant="body3">Contact:</Typography>
                            <Typography className={classes.bold}  align="justify" variant="body2">
                                {item.telephone_recruteur}
                            </Typography>
                        </Box>
                    </Box>
                    <Box>
                        <Box  display="flex"  className={classes.info}>
                            <Typography className={classes.bold}  variant="body3">Site web:</Typography>
                            <Typography className={classes.bold}  align="justify" variant="body2">

                                <Link href={item.url_recruteur} target="_blank" rel="noopener" className={classes.link}>
                                {item.url_recruteur}
                                </Link>
                            </Typography>
                        </Box>
                    </Box>
                    <Box>
                        <Box  display="flex"  className={classes.info}>
                            <Typography className={classes.bold}  variant="body3">Adresse:</Typography>
                            <Typography className={classes.bold}  align="justify" variant="body2">
                                {item.adresse_recruteur}
                            </Typography>
                        </Box>
                    </Box>
                    <Box>
                        <Box  display="flex"  className={classes.info}>
                            <Typography align="justify" variant="body3">
                                {item.description_recruteur}
                            </Typography>
                        </Box>
                    </Box>
                    <Box>
                        <Box  display="flex"  className={classes.info}>
                               <Link className={classes.link}> <Email/>  Email</Link>
                               <Link  className={classes.link}> <FiHeart/> Sauvegarder</Link>
                        </Box>
                    </Box>
                    </Grid>
                        ))

                    }

                </Grid>

            </Grid>

        </DialogContent>
        <Divider/>
        <DialogActions  className={classes.boxTitle}>

                <Button onClick={() => {verification()} } className={classes.buttonStyle} variant="contained" disableElevation  >Postuler</Button>

        </DialogActions>
    </Dialog>
    )
}

export default ViewJobModal
