import React,{useState,useEffect,useContext} from 'react'
import {
    ContentHome,
    Content,
    ContentH1,
    ContentH2,
    Pdf,
    PdfLink,
    ContentImg,
} from './HomeElements'
import Img from '../../../assets/images/gorgeous-positive.jpg'
import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';






const HomeContent = () => {
    const {currentUser} = useContext(AuthContext);
    const [candidatObjects, setCandidatObjects] = useState([])
    const [cvObjects, setCvObjects] = useState([]);
    const db = app.firestore();

    const getData = async () => {
        db.collection("candidats")
            .where("id_utilisateur", "==", currentUser.uid)
            .onSnapshot((querySnapshot) => {
                var list = [];
                querySnapshot.forEach((doc) => {
                    list.push({ ...doc.data(), id: doc.id })
                });
                setCandidatObjects(list)
                
            });
        }

        const getCV = async () => {
            db.collection("cv")
              .where("id_utilisateur", "==", currentUser.uid)
              .onSnapshot((querySnapshot) => {
                  var listCV = [];
                  querySnapshot.forEach((doc) => {
                      listCV.push({ ...doc.data(), id: doc.id })
                  });
                  setCvObjects(listCV); 
              });
         } 
         

        useEffect(()=> {
            getData();
            getCV()
        }, [])


    return (
        <ContentHome>
            <ContentImg src={Img} alt="travailleur" /> 
            <Content>
              
               {candidatObjects.map((item,index) =>
                    (
                        <>
                        <ContentH1 key={index} >{item.nom_candidat} {item.prenom_candidat} </ContentH1>
                        
                    </>
                    )
               )}
               {cvObjects.map((item,index) => (
                <>
                 <ContentH2 key={index}>{item.intitule_cv}</ContentH2>
                <Pdf> 
                  
                    <PdfLink   href={item.fichier_cv} target="_blank"> 
                            Télecharger CV
                    </PdfLink>
                  
                </Pdf>
                </>
                ))}
            </Content>
        </ContentHome>
    )
}

export default HomeContent
