import React, {useState,useEffect} from 'react'
import { Box,makeStyles, Grid, InputAdornment, Paper,TableCell,TableRow, Toolbar, TableBody } from '@material-ui/core'
import Controls from '../controls/Controls'
import {  PeopleOutlineTwoTone, Search } from '@material-ui/icons'
import useTable from '../useTable';
import CloseIcon from '@material-ui/icons/Close';
import VisibilityIcon from '@material-ui/icons/Visibility';
import Notification from '../Notification';

import Popup from '../Popup';
import ConfirmDialog from '../ConfirmDialog';
import './table.css'
import CandidatForm from '../Forms/CandidatForm';
import PageHeader from '../PageHeader';

import app from '../../../firebase/base';

const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '50%',
    },
    newButton: {
        position: 'absolute',
        right: '10px',
        color: '#1a1359',
        borderColor: '#1a1359'
    }
}))

const headCells = [
    { id: 'nom_candidat', label: 'nom' },
    { id: 'prenom_candidat', label: 'prenom' },
    { id: 'email_candidat', label: 'email' },
    { id: 'telephone_candidat', label: 'Télephone' },
    { id: 'ville_candidat', label: 'ville' },
    { id: 'province_candidat', label: 'province' },
    { id: 'situation_candidat', label: 'situation' },
    { id: 'actions', label: 'Actions', disableSorting: true }
]

const TableCandidat = (props) => {
    const classes = useStyles();

    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [records,setRecords] = useState([])
    const [openPopup, setOpenPopup] = useState(false)
    const [recordForEdit, setRecordForEdit] = useState(null)
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })

    const db = app.firestore();
    const listCandidat = [];


    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records,headCells,filterFn);

    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.nom_candidat.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchVille = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.ville_candidat.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchProvince = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.province_candidat.toLowerCase().includes(target.value))
            }
        })
    }
    const getCandidat = async () => {
        await db.collection("candidats")
                .onSnapshot((querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        listCandidat.push({...doc.data(), id: doc.id })
                    });
                    setRecords(listCandidat)              
                });
      }

    const addOrEdit = async (obj,resetForm) => {
        try{
            if (obj.id == ""){
                 await  db.collection("candidats").doc().set(obj)   
            }else{
                 await db.collection("candidats").doc(obj.id).update(obj)
            }
            resetForm()
            setRecordForEdit(null)
            setOpenPopup(false)
            getCandidat()
            setNotify({
                    isOpen: true,
                    message: 'Validation avec Succes',
                    type: 'success'
              })
            }catch (error) {
            console.error(error);
          }
    }

    const openInPopup = item => {
        setRecordForEdit(item)
        setOpenPopup(true)
    }

    const onDelete = async (id) => {
        await db.collection("candidats").doc(id).delete();
        getCandidat();
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        })
    }

    useEffect(()=> {
        getCandidat();
    }, [])

    return (
        <Box p={2}>
            <PageHeader
                title="Gestion des candidats"
                subTitle="Visualiser les informations des candidats, supprimer"
                icon={<PeopleOutlineTwoTone fontSize="large" />}
             
            />
               <Paper  >
                <Toolbar>
                    <Grid  container spacing={2}  xs={12}>
                      <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par nom"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearch}  
                            />
                         </Grid>
                        <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par ville"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchVille}  
                            />
                         </Grid>
                         <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par province"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchProvince}  
                            />
                         </Grid>
                     
                    </Grid>
                </Toolbar>
                  
                  <Grid className="container">
                    <TblContainer>
                        <TblHead/>
                        <TableBody>
                        {
                                recordsAfterPagingAndSorting().map( (item,index) =>
                                    (<TableRow key={index}>
                                        <TableCell>{item.nom_candidat}</TableCell>
                                        <TableCell>{item.prenom_candidat}</TableCell>
                                        <TableCell>{item.email_candidat}</TableCell>
                                        <TableCell>{item.telephone_candidat}</TableCell>
                                        <TableCell>{item.ville_candidat}</TableCell>
                                        <TableCell>{item.province_candidat}</TableCell>
                                        <TableCell>{item.situation_candidat}</TableCell>
                                        <TableCell>
                                            <Controls.ActionButton
                                                color="primary"
                                                onClick={() => { openInPopup(item) }}
                                              >
                                                <VisibilityIcon fontSize="small" /> 
                                            </Controls.ActionButton>
                                            <Controls.ActionButton
                                                color="secondary"
                                                onClick={() => {
                                                    setConfirmDialog({
                                                        isOpen: true,
                                                        title: 'Vous ne pouvez pas supprimer cette information',
                                                        subTitle: "Cliquer sur nom pour fermer",
                                                        //onConfirm: () => { onDelete(item.id) }
                                                    })
                                                }}
                                              >
                                                <CloseIcon fontSize="small" />
                                            </Controls.ActionButton>
                                        </TableCell>
                                    </TableRow>)
                                )
                            }
                        </TableBody>
                    </TblContainer>
                    <TblPagination />
                    </Grid>
             </Paper>
             <Popup
                  title="Informations de la candidat"
                  openPopup={openPopup}
                  setOpenPopup={setOpenPopup}
             >
                 
                 <CandidatForm
                   recordForEdit={recordForEdit}
                   addOrEdit={addOrEdit}
                 />
             </Popup>
             <Notification
                notify={notify}
                setNotify={setNotify}
            />
             <ConfirmDialog
                confirmDialog={confirmDialog}
                setConfirmDialog={setConfirmDialog}
             />
        </Box>
    )
}

export default TableCandidat
