import React,{useState} from 'react'
import { NavPub, NavPubContainer, NavPubLogo,NavPubMenu ,
    MobileIcon,NavBtn,LogoImg,ArrowLeft,ButtonBack
} from './PubLateraleElement'
import {FaBars} from 'react-icons/fa'
import {animateScroll as scroll} from 'react-scroll'
import Img from '../../../assets/logo/new-logo-jobsgabon-1.png'
import { useHistory} from 'react-router-dom';





const NavOffre = ({toggle}) => {

    const toggleHome = () => {
        scroll.scrollToTop(); 
    }
    const [hover, setHover] = useState(false)
    const navigate = useHistory();
 
    const onHover = () =>{
        setHover(!hover)
    }
    return (
        <NavPub>
             <NavPubContainer>
                 <NavPubLogo to="/"  onClick={toggleHome}>
                    <LogoImg src={Img} alt="logo JOBSGABON" />
                 </NavPubLogo>
                 <MobileIcon  onClick={toggle} >
                        <FaBars />
                 </MobileIcon>
                 <NavPubMenu> 
                       
                 </NavPubMenu>
             </NavPubContainer>
               <NavBtn>
                    <ButtonBack 
                        onClick={()=>{navigate.goBack()}}
                        onMouseEnter={onHover} 
                        onMouseLeave={onHover}
                        primary='false'
                        dark='false'  
                    >
                        <ArrowLeft/> Retour
                    </ButtonBack>
                </NavBtn>
        </NavPub>
    )
}

export default NavOffre
