import React from 'react'
import "./CompanyElement.css"
import Carousel from 'react-elastic-carousel'
import CardCompany from './CardCompany';
import {dataCompany} from './dataCompany'

const CompanySection = () => {
    
        const breakPoints =[
            {width:375, itemsToShow: 1},
            {width:500, itemsToShow: 3},
            {width:768, itemsToShow: 5},
            {width:1130, itemsToShow: 4},
            {width:1200, itemsToShow: 5},
           
    
        ];
        return (
            <div className="company__section">
                <div className="company__wrapper">
                    <h1 className="company__heading">Entreprises en vedette</h1>
                                {/* <Button 
                                       buttonSize='btn--medium'
                                        className="btn"
                                       buttonStyle='btn--primary'    
                                     >
                                        Voir plus
                                </Button>   */ }   
                        <div className="company__container">
                            <Carousel  breakPoints={breakPoints}
                                        enableAutoPlay 
                                        autoPlaySpeed={2500} 
                                        itemPadding={[5,10]} 
                                        itemsToShow={5}
                                >

                               { 
                                dataCompany.map(company =>
                                <CardCompany key={company.id} {...company}  />)
                               }
                               
                            </Carousel>
                         
                          
                        </div>
                </div>
            </div>
    )
}

export default CompanySection
