import React, {useEffect,useContext }  from 'react'
import { Grid } from '@material-ui/core'
import Controls from '../controls/Controls'
import { Form, useForm } from '../useForm'
import {getContratCollection,getVilleCollection,getSecteurActivite,getProvinceCollection} from '../../data/collection'
import { AuthContext } from '../../../firebase/AuthContext';
import styled from 'styled-components'




const BlockCache = styled.div`
 display:none; 
`;
 

const OffreForm = (props) => {

    const {addOrEdit, recordForEdit} = props
    const {currentUser} = useContext(AuthContext);

    const initialValues = {
        id:'',
        societe:'',
        titre_offre: '',
        id_utilisateur:currentUser.uid,
        description_offre: '',
        responsabilite_offre:'',
        responsable_recru: '',
        fonction_offre: '',
        secteur_activite:'',
        niveau_etude:'',
        experience:'',
        type_contrat: '', 
        ville_offre: '',
        province_offre: '',
        date_ajout: new Date(),    
    }

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('titre_offre' in fieldValues)
            temp.titre_offre = fieldValues.titre_offre ? "" : "Champ requis."
        if ('societe' in fieldValues)
            temp.societe = fieldValues.societe ? "" : "Champ requis."
        if ('description_offre' in fieldValues)
            temp.description_offre = fieldValues.description_offre ? "" : "Champ requis."
        if ('id_utilisateur' in fieldValues)
            temp.id_utilisateur = fieldValues.id_utilisateur ? "" : "Champ requis."
        if ('responsable_recru' in fieldValues)
            temp.responsable_recru = fieldValues.responsable_recru ? "" : "Champ requis."
        if ('fonction_offre' in fieldValues)
            temp.fonction_offre = fieldValues.fonction_offre ? "" : "Champ requis."
        if ('niveau_etude' in fieldValues)
            temp.niveau_etude = fieldValues.niveau_etude ? "" : "Champ requis."
        if ('experience' in fieldValues)
            temp.experience = fieldValues.experience ? "" : "Champ requis."
        if ('type_contrat' in fieldValues)
            temp.type_contrat = fieldValues.type_contrat.length !== 0 ? "" : "Champ requis."
        if ('secteur_activite' in fieldValues)
            temp.secteur_activite = fieldValues.secteur_activite.length !== 0 ? "" : "Champ requis."
        if ('ville_offre' in fieldValues)
            temp.ville_offre = fieldValues.ville_offre.length !== 0 ? "" : "Champ requis."
        if ('province_offre' in fieldValues)
            temp.province_offre = fieldValues.province_offre.length !== 0 ? "" : "Champ requis."
        setErrors({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    } =useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }
   

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit])

    return (
        <Form  onSubmit={handleSubmit}>
                <Grid container spacing={2}>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="titre_offre"
                              label="titre_offre"
                              value={values.titre_offre}
                              onChange={handleInputChange}
                              error={errors.titre_offre}

                        />
                        <BlockCache>
                            <Controls.Input
                                name="id_utilisateur"
                                label="Identifiant"
                                value={values.id_utilisateur}
                                onChange={handleInputChange} 
                                error={errors.id_utilisateur}
                                        
                            /> 
                        </BlockCache>
                        <Controls.Input
                              name="responsable_recru"
                              label="responsable_recru"
                              value={values.responsable_recru}
                              onChange={handleInputChange}
                              error={errors.responsable_recru}
                        />
                         <Controls.Select
                            name="type_contrat"
                            label="type_contrat"
                            value={values.type_contrat}
                            onChange={handleInputChange}
                            options={getContratCollection()}
                            error={errors.type_contrat}
                        />
                        <Controls.Select
                            name="secteur_activite"
                            label="secteur_activite"
                            value={values.secteur_activite}
                            onChange={handleInputChange}
                            options={getSecteurActivite()}
                            error={errors.secteur_activite}
                        />
                        <Controls.Input
                              name="experience"
                              label="années d'expérience"
                              value={values.experience}
                              onChange={handleInputChange}
                              error={errors.experience}

                        />
                         <Controls.Select
                            name="province_offre"
                            label="province_offre"
                            value={values.province_offre}
                            onChange={handleInputChange}
                            options={getProvinceCollection()}
                            error={errors.province_offre}
                        />
                         
                    </Grid>
                    <Grid item xs={6}> 
                      <Controls.Input
                              name="societe"
                              label="nom de votre société"
                              value={values.societe}
                              onChange={handleInputChange}
                              error={errors.societe}

                        />
                         <Controls.Input
                              name="date_ajout"
                              label="date d'ajout"
                              value={values.date_ajout}
                              onChange={handleInputChange}
                              error={errors.date_ajout}
                              disabled="true"

                        />
                        <Controls.Input
                              name="niveau_etude"
                              label="niveau détude ou autre"
                              value={values.niveau_etude}
                              onChange={handleInputChange}
                              error={errors.niveau_etude}
                        />
                        <Controls.Input
                              name="description_offre"
                              label="description_offre"
                              rows="4"
                              multiline="multiline"
                              value={values.description_offre}
                              onChange={handleInputChange}
                              error={errors.description_offre}
                        />
                        <Controls.Input
                              name="responsabilite_offre"
                              label="responsabilite_offre"
                              rows="4"
                              multiline="multiline"
                              value={values.responsabilite_offre}
                              onChange={handleInputChange}
                              error={errors.responsabilite_offre}
                        />
                        <Controls.Input
                              name="fonction_offre"
                              label="fonction_offre"
                              value={values.fonction_offre}
                              onChange={handleInputChange}
                              error={errors.fonction_offre}
                        />
                        <Controls.Select
                            name="ville_offre"
                            label="ville_offre"
                            value={values.ville_offre}
                            onChange={handleInputChange}
                            options={getVilleCollection()}
                            error={errors.ville_offre}
                        />
                     
                    
                    </Grid>
 
                 
                    <div>
                        <Controls.Button
                            type="submit"
                            text="Valider" />
                        <Controls.Button
                            text="Effacer"
                            color="default"
                            onClick={resetForm} />
                    </div>
                </Grid>
        </Form>
    ) 
}

export default OffreForm
