import React,{ useState,useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import app from '../../../firebase/base';
import { Lens } from '@material-ui/icons';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #008647'
  },
  listeStyle:{
    color:'#1a1359',
}
}));

export default function Contact(props) {

  const classes = useStyles();
  const db = app.firestore();
  const [infos, setInfos] = useState([]);
  const {contData} = props


  const getCandidat = async () => {
    db.collection("candidats")
      .where("id_utilisateur", "==", contData.id_utilisateur)
      .onSnapshot((querySnapshot) => {
          var List= []
          querySnapshot.forEach((doc) => {
            List.push({ ...doc.data(), id: doc.id })
          });
          setInfos(List)              
      });
 }

    useEffect(()=> {
        getCandidat();
        
    }, [])


  return (
    <List component="nav" className={classes.root} aria-label="contacts">
     {
        infos.map((item,index) => {
        return(
          <>  
            <ListItem button>  
                <ListItemIcon>
                <Lens className={classes.listeStyle} />
                </ListItemIcon>
                <ListItemText key={index} primary={item.nom_candidat} />
            </ListItem>
            <ListItem button>  
                <ListItemIcon>
                <Lens className={classes.listeStyle} />
                </ListItemIcon>
                <ListItemText primary={item.prenom_candidat} />
            </ListItem>
            <ListItem button>  
                <ListItemIcon>
                <Lens className={classes.listeStyle} />
                </ListItemIcon>
                <ListItemText primary={item.email_candidat} />
            </ListItem>
            <ListItem button>  
                <ListItemIcon>
                <Lens className={classes.listeStyle} />
                </ListItemIcon>
                <ListItemText primary={item.telephone_candidat} />
            </ListItem>
            <ListItem button>  
                <ListItemIcon>
                <Lens className={classes.listeStyle} />
                </ListItemIcon>
                <ListItemText primary={item.situation_candidat} />
            </ListItem>
            <ListItem button>  
                <ListItemIcon>
                <Lens className={classes.listeStyle} />
                </ListItemIcon>
                <ListItemText primary={item.ville_candidat} />
            </ListItem>
        </>
        
        )
        })
      }
    </List>
  );
}
