import React, {useEffect }  from 'react'
import { Grid } from '@material-ui/core'
import Controls from '../controls/Controls'
import { Form, useForm } from '../useForm'
import {getRole} from '../../data/collection'


const initialValues = {
    id:'',
    nom_admin: '',
    prenom_admin: '',
    email_admin: '',
    role_admin: '',
    pasword: '',
    date_ajout: new Date(), 
}


 
const UtilisateurForm = (props) => {

    const {addOrEdit, recordForEdit} = props

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('nom_admin' in fieldValues)
            temp.nom_admin = fieldValues.nom_admin ? "" : "Champ requis."
        if ('prenom_admin' in fieldValues)
            temp.prenom_admin = fieldValues.prenom_admin ? "" : "Champ requis."
        if ('email_admin' in fieldValues)
            temp.email_admin = (/$^|.+@.+..+/).test(fieldValues.email_admin) ? "" : "Email non valide."  
        if ('password' in fieldValues)
            temp.password = (/^[A-Za-z0-9\!\@\#\$\%\^\&\*\(\)\_\+\{\}\:\"\<\>\?\\| \[\]\/'\,\.\`\~]{8,16}$/).test(fieldValues.password) ? "" : "Le mot de passe doit contenir au moins 8 caractère(Majuscule,minuscule,chiffre,etc)" 
        if ('role_admin' in fieldValues)
            temp.role_admin = fieldValues.role_admin.length !== 0 ? "" : "Champ requis."
      
        setErrors({
            ...temp
        })
 
        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    } =useForm(initialValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit])

    return (
        <Form  onSubmit={handleSubmit}>
                <Grid container spacing={2}>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="nom_admin"
                              label="nom_admin"
                              value={values.nom_admin}
                              onChange={handleInputChange}
                              error={errors.nom_admin}
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="prenom_admin"
                              label="prenom_admin"
                              value={values.prenom_admin}
                              onChange={handleInputChange}
                              error={errors.prenom_admin}
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="email_admin"
                              label="email_admin"
                              value={values.email_admin}
                              onChange={handleInputChange}
                              error={errors.email_admin}
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Input
                              name="password"
                              label="mot de passe"
                              type="password"
                              value={values.password}
                              onChange={handleInputChange}
                              error={errors.password}
                        />
                    </Grid>
                    <Grid item xs={6}> 
                        <Controls.Select
                            name="role_admin"
                            label="role_admin"
                            value={values.role_admin}
                            onChange={handleInputChange}
                            options={getRole()}
                            error={errors.role_admin}
                        />
                    </Grid>
                    
                    <Grid item xs={6}> 
                        <Controls.DatePicker
                            name="date_ajout"
                            label="date_ajout"
                            value={values.date_ajout}
                            onChange={handleInputChange}
                        />
                    </Grid>
                 
                    <div>
                        <Controls.Button
                            type="submit"
                            text="Valider" />
                        <Controls.Button
                            text="Effacer"
                            color="default"
                            onClick={resetForm} />
                    </div>
                </Grid>
        </Form>
    )
}

export default UtilisateurForm
