import React from 'react';
import './App.css';
import {BrowserRouter as Router, Switch, Route, Redirect} from 'react-router-dom'

import Accueil from './pages/Accueil';

import ProfilEntreprise from './pages/ProfilEntreprise';
import Recruteur from './recruteurs/pages/Recruteur';
import Tarifs from './recruteurs/pages/Tarifs';
import LoginR from './recruteurs/pages/LoginR';


import Candidat from './candidats/pages/Candidat';
import Emplois from './candidats/pages/Emplois';
import Conseils from './candidats/pages/Conseils';
import Actualites from './candidats/pages/Actualites';
import Login from './candidats/pages/Login';

import Signin from './pages/Signin';
import Signup from './candidats/pages/Signup';
import { AuthProvider } from './firebase/AuthContext';
import SignupR from './recruteurs/pages/SignupR';
import PrivateLayout from './pages/PrivateLayout';
import Postuler from './candidats/pages/Postuler';

import { ToastContainer } from "react-toastify";
import Postuler2 from './candidats/pages/Postuler2';
import ContactezNous from './pages/ContactezNous';
import Apropos from './pages/Apropos';
import MotDepasseOublie from './pages/MotDepasseOublie';
import Register from './candidats/pages/Register';
import RegisterR from './recruteurs/pages/RegisterR';
import Erreur from './pages/Erreur';
import Offre from './candidats/pages/Offre';
import DonneesPerso from './pages/DonneesPerso';
import ConditionsUse from './pages/ConditionsUse';
import MentionsLegale from './pages/MentionsLegale';
import Cookies from './pages/Cookies';
import Single_actu from './candidats/pages/Single_actu';
import SingleConseil from './candidats/pages/SingleConseil';
import Aide from './pages/Aide';


 

function App() { 


 
  return (
    <>
   { /*basename={process.env.PUBLIC_URL}
      "homepage": "https://hanseroland.github.io/jobsgabon/",
      "predeploy": "npm run build",
      "deploy": "gh-pages -d build"
   
   */}
      <AuthProvider>
        <Router basename={process.env.PUBLIC_URL}  >
              <Switch>

                  <Route   exact path="/" component={Accueil} /> 
                  <Route   path="/profil_entreprise" component={ProfilEntreprise} />
                  <Route   path="/connexion" component={Signin} />
                  <Route   path="/contactez-nous" component={ContactezNous} />
                  <Route   path="/Apropos" component={Apropos} />
                  <Route   path="/mot-de-passe-oublier" component={MotDepasseOublie} />
                  <Route   path="/donnees-personnelles" component={DonneesPerso} />
                  <Route   path="/conditions-d-utilisation" component={ConditionsUse} />
                  <Route   path="/mentions-legales" component={MentionsLegale} />
                  <Route   path="/cookies" component={Cookies} />
                  <Route   path="/aide" component={Aide} />
                 
                  
                 
          
                  <Route   exact path="/candidats" component={Candidat} /> 
                  <Route   path="/candidats/emplois" component={Emplois} /> 
                  <Route   path="/candidats/offre" component={Offre} /> 
                  <Route   path="/candidats/conseils" component={Conseils} /> 
                  <Route   path="/candidats/single-conseil/:titre_poste" component={SingleConseil} />
                  <Route   path="/candidats/actualites" component={Actualites} /> 
                  <Route   path="/candidats/single-actualite/:titre_poste" component={Single_actu} />
                  <Route   path="/candidats/connexion" component={Login}/> 
                  <Route   path="/candidats/signup" component={Signup}/>
                  <Route   path="/candidats/inscription" component={Register}/>
                  <Route   path="/candidats/postuler" component={Postuler}/> 
                  <Route   path="/candidats/postuler_etape_2" component={Postuler2}/> 
                 
                  

                
                
                  <Route  exact path="/recruteur" component={Recruteur} />
                  <Route   path="/recruteur/tarifs" component={Tarifs} />  
                  <Route   path="/recruteur/connexion" component={LoginR} /> 
                  <Route   path="/recruteur/signup" component={SignupR}/>
                  <Route   path="/recruteur/dashbord/inscription" component={RegisterR}/>

                  
                  <PrivateLayout/>
                  <ToastContainer/> 
                  <Route path="/404"   component={Erreur}/> 
                  <Redirect path='*' to="/404" />
              </Switch>
        </Router>
      </AuthProvider>
    </>
  );
}

export default App;
