import React,{useState,useEffect,useContext} from 'react'
import ProtectedRoute from './PrivateRoute';
import Contact from '../candidats/pages/Contact'
import HomeCandidat from '../candidats/pages/HomeCandidat'
import Knowledges from '../candidats/pages/Knowledges'
import MesOffres from '../candidats/pages/MesOffres'
import Portefolio from '../candidats/pages/Portefolio'
import MonCompte from '../candidats/pages/MonCompte'
import EditCv from '../candidats/pages/EditCv'
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import app from '../firebase/base'
import { AuthContext } from '../firebase/AuthContext';

import DasboardR from '../recruteurs/pages/Dasboard';
import Candidatures from '../recruteurs/pages/Candidatures';
import OffresEmploi from '../recruteurs/pages/OffresEmploi';
import ListeCv from '../recruteurs/pages/ListeCv';
import ProfilRecruteur from '../recruteurs/pages/ProfilRecruteur';
import { ThemeProvider } from 'styled-components'
import { lightTheme, darkTheme } from '../recruteurs/styles/theme'
import { ThemeContext } from '../recruteurs/context/themeContext'

import Dasboard from '../admin/pages/Dasboard'
import Candidature from '../admin/pages/Candidatures'
import CvListe from '../admin/pages/CvListe'
import RecruteurProfil from '../admin/pages/RecruteurProfil'
import Utilisateurs from '../admin/pages/Utilisateurs'
import GesEmploi from '../admin/pages/GesEmplois'
import GesRecruteurs from '../admin/pages/GesRecruteurs'
import GesCandidats from '../admin/pages/GesCandidats'
import GesPostes from '../admin/pages/GesPostes'
import TelechargerCV from '../candidats/pages/TelechargerCV';
import Formations from '../candidats/pages/Formations';
import Experiences from '../candidats/pages/Experiences';
import AutreCompetences from '../candidats/pages/AutreCompetences';
import BibliothequeFram from '../candidats/pages/BibliothequeFram';
import Langues from '../candidats/pages/Langues';
import Langages from '../candidats/pages/Langages';
import ReseauxSociaux from '../candidats/pages/ReseauxSociaux';
import Interets from '../candidats/pages/Interets';
import Messages from '../admin/pages/Messages';
import Reglages from '../admin/pages/Reglages';
import Publicites from '../admin/pages/Publicites';
import {BrowserRouter as Router, Switch, Route, Redirect} from 'react-router-dom'



const PrivateLayout = () => {

    const {currentUser} = useContext(AuthContext);
    const context = useContext(ThemeContext);
    const [role, setRole] = useState("");
    const { theme } = context;
    const db = app.firestore(); 
    var user;

    const getRole = async () => {
            db.collection("utilisateurs")
            .where("id_utilisateur", "==", currentUser.uid)
            .onSnapshot((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                user = doc.data().role_utilisateur;
                });
            setRole(user);
                
            });        
     } 

    useEffect(() => {
        getRole();
    }, [])


    return (
        <>
        
          { role == "candidat" && (
             
             <React.Fragment>
                  <ProtectedRoute  exact path="/candidats/accueil" component={HomeCandidat} />
                  <ProtectedRoute   path="/candidats/competences" component={Knowledges}  />
                  <ProtectedRoute   path="/candidats/portefolio" component={Portefolio}/>
                  <ProtectedRoute   path="/candidats/contact" component={Contact}  />
                  <ProtectedRoute   path="/candidats/mescandidatures" component={MesOffres} />
                  <ProtectedRoute   path="/candidats/moncompte" component={MonCompte}  />
                  <ProtectedRoute   path="/candidats/editercv" component={EditCv} />
                  <ProtectedRoute   path="/candidats/telecharger-cv" component={TelechargerCV} />
                  <ProtectedRoute   path="/candidats/formations" component={Formations} />
                  <ProtectedRoute   path="/candidats/experiences" component={Experiences} />
                  <ProtectedRoute   path="/candidats/autres-competences" component={AutreCompetences} />
                  <ProtectedRoute   path="/candidats/bibliotheque-framwork" component={BibliothequeFram} />
                  <ProtectedRoute   path="/candidats/langues" component={Langues} />
                  <ProtectedRoute   path="/candidats/langages-informatiques" component={Langages} />
                  <ProtectedRoute   path="/candidats/reseaux-sociaux" component={ReseauxSociaux}/>
                  <ProtectedRoute   path="/candidats/interets" component={Interets} />
                  

                {/*  <ProtectedRoute   path="/candidats/inscription" component={Register} /> */}
                  <ToastContainer/>  
             </React.Fragment>
            
              ) 
            }
            { role == "recruteur" && (
                <React.Fragment>
                        <ThemeProvider theme={theme === 'light' ? lightTheme : darkTheme}> 
                            <ProtectedRoute exact path="/recruteur/dashboard" component={DasboardR} />
                            <ProtectedRoute path="/recruteur/dashbord/candidature" component={Candidatures} />
                            <ProtectedRoute path="/recruteur/dashbord/offresemploi" component={OffresEmploi} />
                            <ProtectedRoute path="/recruteur/dashbord/cvtheque" component={ListeCv} /> 
                            <ProtectedRoute path="/recruteur/dashbord/profil" component={ProfilRecruteur} />
                          {/*  <ProtectedRoute path="/recruteur/dashbord/inscription" component={RegisterR} /> */}
                            <ToastContainer/> 
                    </ThemeProvider>
                </React.Fragment> 

              ) 
            }
             { role == "admin" && (
                <React.Fragment>
                        <ThemeProvider theme={theme === 'light' ? lightTheme : darkTheme}> 
                        <ProtectedRoute path="/admin/dashboard" exact component={Dasboard} />
                        <ProtectedRoute path="/admin/dashboard/gestion_utilisateurs" component={Utilisateurs} />
                        <ProtectedRoute path="/admin/dashboard/gestion_candidatures" component={Candidature} />
                        <ProtectedRoute path="/admin/dashboard/gestion_emplois" component={GesEmploi}/>
                        <ProtectedRoute path="/admin/dashboard/gestion_recruteurs" component={GesRecruteurs}/>
                        <ProtectedRoute path="/admin/dashboard/gestion_candidats" component={GesCandidats}/>
                        <ProtectedRoute path="/admin/dashboard/gestion_cv" component={CvListe} />
                        <ProtectedRoute path="/admin/dashboard/gestion_postes" component={GesPostes}/>
                        <ProtectedRoute path="/admin/dashboard/messages" component={Messages}/>
                        <ProtectedRoute path="/admin/dashboard/profil" component={RecruteurProfil} />
                        <ProtectedRoute path="/admin/dashboard/reglages" component={Reglages} />
                        <ProtectedRoute path="/admin/dashboard/publicites" component={Publicites} />
                        <ToastContainer/> 
                    </ThemeProvider>
                </React.Fragment>

              ) 
            }

        </>
    )
}

export default PrivateLayout
