import React, {useState} from 'react'
import { MainContainer } from '../components/main/MainElements'
import MobileSidebar from '../components/MobileSidebar/Index'
import Sidebar from '../components/Sidebar/Index'
import Topnav from '../components/Topnav/Index'
import {withRouter} from 'react-router-dom'
import CvthequePlus from '../../cvtheque/components/rubriqueCV/CvthequePlus'


const ListeCv = () => {
    const [isOpen, setIsOpen] = useState(false)

    const toggle = () => {
        setIsOpen(!isOpen)
    }
    return (  
        <>
           <Sidebar/>
           <MainContainer>
               <MobileSidebar isOpen={isOpen}  toggle={toggle} />
               <Topnav toggle={toggle} />
               <CvthequePlus/>
           </MainContainer>
        </>
    )
}

export default withRouter(ListeCv)
