import React from 'react'
import { TextField } from '@material-ui/core';

export default function Input(props) {

    const { name,rows,type,disabled, label, value,error=null, onChange } = props;
    return (
        <TextField
            style={{backgroundColor: '#fff'}}
            variant="outlined"
            label={label} 
            disabled={disabled}
            fullWidth
            multiline
            rows={rows}
            type={type}
            name={name}
            value={value}
            onChange={onChange} 
            {...(error && {error:true,helperText:error})}
        /> 
    )
}
