import React, {useState,useEffect,useContext} from 'react'
import { Box,makeStyles, Grid, InputAdornment, Paper,TableCell,TableRow, Toolbar, TableBody } from '@material-ui/core'
import Controls from '../controls/Controls'
import { Search, Visibility, Work } from '@material-ui/icons'
import useTable from '../useTable';
import Popup from '../Popup';
import './table.css'
import PageHeader from '../PageHeader';
import app from '../../../firebase/base';
import Connaissances from '../knowleges/Connaissances';
 

const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '50%',
    },
    newButton: {
        
        right: '10px',
        color: '#1a1359',
        borderColor: '#1a1359',
        justifyContent:'center',
        alignItems: 'center',
        display:'flex-start'
    },
  
}))

const headCells = [
    { id: 'intitule_cv', label: 'Titre' },
    { id: 'secteur_activite', label: 'Secteur activite' },
    { id: 'fichier_cv', label: 'Fichier' },
    { id: 'actions', label: 'Actions', disableSorting: true }
]

const CvthequeAdmin = (props) => {
    
    const classes = useStyles();

    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [records, setRecords] = useState([])
    const [openPopup, setOpenPopup] = useState(false)
    const [recordForEdit, setRecordForEdit] = useState(null)
  

   // const {currentUser} = useContext(AuthContext);
    const db = app.firestore();
    const listCv = [];

 
    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records,headCells,filterFn);

    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.intitule_cv.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchContrat = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.secteur_activite.toLowerCase().includes(target.value))
            }
        })
    }

   

    const getCv = async () => {
        await db.collection("cv")
             .onSnapshot((querySnapshot) => {
               querySnapshot.forEach((doc) => {
                 listCv.push({...doc.data(), id: doc.id })
               });
               setRecords(listCv)               
           });
      }
 

    const openInPopup = item => {
        setRecordForEdit(item)
        setOpenPopup(true)
    }

  

    useEffect(()=> {
        getCv();
    }, [])

    return (
        <Box p={2}>
                <PageHeader
                title="Cvthèque"
                subTitle="Accéder aux CV de milliers de candidats"
                icon={<Work fontSize="large" />}
              
             
            />
               <Paper  >
                <Toolbar>
                    <Grid container spacing={2}  xs={12}>
                        <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par titre"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearch}  
                            />
                       </Grid>
                       <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par secteur"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchContrat}  
                            />
                       </Grid>
                       
                    </Grid>
                </Toolbar>
                  
                  <Grid className="container">
                    <TblContainer>
                        <TblHead/>
                        <TableBody>
                        {
                                recordsAfterPagingAndSorting().map(item =>
                                    (<TableRow key={item.id}>
                                        <TableCell>{item.intitule_cv}</TableCell>
                                        <TableCell>{item.secteur_activite}</TableCell>
                                        <TableCell>
                                            <a  href={item.fichier_cv}
                                                 target="_blank" rel="noopener noreferrer">
                                                 Fichier
                                            </a>
                                        </TableCell>
                                        <TableCell>
                                            <Controls.ActionButton
                                                color="primary"
                                                onClick={() => { openInPopup(item) }}
                                              >
                                                <Visibility fontSize="small" /> 
                                            </Controls.ActionButton>
                                           
                                        </TableCell>
                                    </TableRow>)
                                )
                            }
                        </TableBody>
                    </TblContainer>
                    <TblPagination />
                    </Grid>
             </Paper>
             <Popup
                  title="CVTHEQUE"
                  openPopup={openPopup}
                  setOpenPopup={setOpenPopup}
             >
                <Connaissances
                  recordForEdit={recordForEdit}
                /> 
                 
             </Popup>
           
            
        </Box>
    )
}

export default CvthequeAdmin
