import React,{ useState,useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import StarIcon from '@material-ui/icons/Star';
import app from '../../../firebase/base';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #008647'
  },
  listeStyle:{
    color:'#1a1359',
}
}));

export default function Interets(props) {

  const classes = useStyles();
  const db = app.firestore();
  const [interets, setInterets] = useState([]);
  const {intrets} = props


  const getInterets = async () => {
    db.collection("interets")
      .where("id_utilisateur", "==", intrets.id_utilisateur)
      .onSnapshot((querySnapshot) => {
          var List= []
          querySnapshot.forEach((doc) => {
            List.push({ ...doc.data(), id: doc.id })
          });
          setInterets(List)              
      });
 }

    useEffect(()=> {
        getInterets();
        
    }, [])


  return (
    <List component="nav" className={classes.root} aria-label="contacts">
     {
        interets.map((item,index) => {
        return(
        <ListItem button>  
            <ListItemIcon>
            <StarIcon className={classes.listeStyle} />
            </ListItemIcon>
            <ListItemText key={index} primary={item.interet} />
        </ListItem>)
        })
      }
    </List>
  );
}
