import React, {useState} from 'react'
import { MainContainer } from '../components/main/MainElements'
import MobileSidebar from '../components/MobileSidebar/Index'
import Sidebar from '../components/Sidebar/Index'
import TableCandidature from '../components/Tableaux/TableCandidature'
import Topnav from '../components/Topnav/Index'
import {withRouter} from 'react-router-dom'


const Candidatures = () => {
    const [isOpen, setIsOpen] = useState(false)

    const toggle = () => {
        setIsOpen(!isOpen)
    }
    return (     
        <>
           <Sidebar/>
           <MainContainer>
               <MobileSidebar isOpen={isOpen}  toggle={toggle} />
               <Topnav toggle={toggle} />
               <TableCandidature/>            
           </MainContainer>
        </>
    )
}

export default withRouter(Candidatures)
