import React, {useState,useEffect} from 'react'
import { Container, Grid,makeStyles, Typography } from '@material-ui/core'
import BreadCrumbs from '../components/Breadcrumbs'
import NavbarBottom from '../components/NavbarBottom'
import PubLaterale from '../components/Publicite'
import styled from 'styled-components'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import Footer from '../components/Footer/Footer'
import ByCity from '../components/SearchOption/ByCity'
import ByState from '../components/SearchOption/ByState'
import ByOther from '../components/SearchOption/ByOther' 
import Sidebar from '../components/Publicite/Sidebar'
import CardOne from '../components/Cards/CardOne'
import app from '../../firebase/base';




const HeadSection = styled.div`
   position:fixed;
   width:100%;
   z-index:10;
   background-color:#fff;
   box-shadow: 0 6px 20px rgba(10, 10, 10, 0.1);

`

const PageContent = styled.div`   
   padding-top: 205px; 
   margin-left:10px;
   margin-bottom: 20px;
   

   @media screen and (max-width: 768px){
       margin-left:5px;
    }
  
`

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 2,
     
    },
    textTitle:{
        fontWeight: 'bolder',
        color: '#1a1359'
    }
  }));

  
const Candidat = () => {


    const classes = useStyles();

    const [isOpen, setIsOpen] = useState(false)
    const [actualites, setActualites] = useState([])
    const [conseils, setConseils] = useState([])
    const db = app.firestore();

    const toggle = () =>{
        setIsOpen(!isOpen)
    }

    const getActu = async () => {
        
        await db.collection("postes")
           .where("categorie", "==", "actualite") 
           .where("visibilite", "==", "on")
           .onSnapshot((querySnapshot) => {
               var list = []
               querySnapshot.forEach((doc) => {
                list.push({...doc.data(), id: doc.id })
               });
               setActualites(list)              
           });
        
        }

        const getConseil = async () => {
        
            await db.collection("postes")
               .where("categorie", "==", "conseil")
               .where("visibilite", "==", "on") 
               .onSnapshot((querySnapshot) => {
                   var list = []
                   querySnapshot.forEach((doc) => {
                    list.push({...doc.data(), id: doc.id })
                   });
                   setConseils(list)              
               });
            
            }
    
    
      useEffect(()=> {
        getActu()
        getConseil()
    }, [])
    return (
        <>
        <ScrollToTop/>
        <HeadSection>
            <Sidebar  isOpen={isOpen}  toggle={toggle}/>
            <PubLaterale   toggle={toggle}/>
            <NavbarBottom/>
            <BreadCrumbs/>
        </HeadSection>
          
        <PageContent className={classes.root}>
                 <Container>
                    <Grid container spacing={5} lg={12} md={12}  xs={12}>
                             <Grid item xs={12}>
                                <Typography className={classes.textTitle} align="center" gutterBottom variant="h4" component="h2">
                                    Conseils et coaching
                                </Typography>
                             </Grid>
                           {conseils.slice(0,3).map((item,index) => 
                              <Grid item lg={4} md={6}  xs={12}  key={index}>
                                 <CardOne {...item}  />
                              </Grid>
                             )}

                            <Grid item xs={12}>
                           
                                <Typography className={classes.textTitle} align="center" gutterBottom variant="h4" component="h2">
                                    Recherche rapide
                                </Typography>
                            </Grid>
                         
                          
                              <Grid item lg={4} md={6}  xs={12}>
                                  <ByCity/>
                              </Grid>
                              <Grid item lg={4} md={6}  xs={12}>
                                  <ByState/>
                              </Grid>
                              <Grid item lg={4} md={6}  xs={12}>
                                    <ByOther/>
                              </Grid>
                           

                            <Grid item xs={12}>
                                <Typography className={classes.textTitle} align="center" gutterBottom variant="h4" component="h2">
                                   Actualités
                                </Typography>
                            </Grid>

                            {actualites.slice(0,3).map((item,index) => 
                              <Grid item lg={4} md={6}  xs={12}  key={index} >
                                   <CardOne {...item}  />
                              </Grid>
                             )}
                    </Grid>
                 </Container>
                 </PageContent>
         
        <Footer/>
        </>
    ) 
}

export default Candidat


