import React, { useState,useContext,useEffect } from 'react';
import { LanguesFramworks } from './LangElements';
import ProgressBarre from './ProgressBarre';
import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';

const Langages = (props) => {

    const {currentUser} = useContext(AuthContext);
    const db = app.firestore();
    const {lang} = props
    const [langues, setLangues] = useState([]);
    const [langages, setLangages] = useState([]);
    const [framBib, setFramBib] = useState([]);

    const getLangues = async () => {
        db.collection("langues")
          .where("id_utilisateur", "==", lang.id_utilisateur)
          .onSnapshot((querySnapshot) => {
              var ListLangues = []
              querySnapshot.forEach((doc) => {
                ListLangues.push({ ...doc.data(), id: doc.id })
              });
              setLangues(ListLangues)              
          });
     }

    const getLangages = async () => {
        db.collection("langages")
          .where("id_utilisateur", "==", lang.id_utilisateur)
          .onSnapshot((querySnapshot) => {
              var ListLangages = []
              querySnapshot.forEach((doc) => {
                ListLangages.push({ ...doc.data(), id: doc.id })
             
              });
              setLangages(ListLangages)              
          });
     }

     const getFramBib = async () => {
        db.collection("bibliotheque_framwork")
          .where("id_utilisateur", "==", lang.id_utilisateur)
          .onSnapshot((querySnapshot) => {
              var ListFram = []
              querySnapshot.forEach((doc) => {
                ListFram.push({ ...doc.data(), id: doc.id })
              });
              setFramBib(ListFram)              
          });
     }

     useEffect(()=> {
        getLangues();
        getLangages();
        getFramBib();
    }, [])

    return (

        <LanguesFramworks className="languesFramworks">
                <ProgressBarre
                    data={langues}
                    className="languagesDisplay"
                    title="langues"
                />
                <ProgressBarre
                    data={langages} 
                    className="langInfoDisplay"
                    title="Langages informatiques"
                />
                <ProgressBarre
                    data={framBib}
                    className="framworksDisplay"
                    title="Framworks & Bibliothèques"
                />
            </LanguesFramworks>
    )
}

export default Langages
