import React,{useState,useEffect} from 'react'
import SidebarAuth from '../components/NavbarHome/SidebarAuth'
import ScrollToTop from '../components/Signin/ScrollToTop'
import Footer from '../components/Footer/Footer'
import { Box,makeStyles, Card, CardContent, CardHeader, Container, Divider, Grid, Typography } from '@material-ui/core'
import CardRecruteur from '../admin/components/Cards/CardRecruteur'
import CardOffre from '../admin/components/Cards/CardOffre'
import CardCandidats from '../admin/components/Cards/CardCandidats'
import SecondNavbar from '../components/NavbarHome/SecondNavbar'
import app from '../firebase/base';



const useStyles = makeStyles(theme => ({
 
    header: {
        backgroundColor:'#1a1359',
        color: '#fff'
       
    }
}))
const Apropos = () => {
    const [isOpen, setIsOpen] = useState(false)
    const classes = useStyles();
    const db = app.firestore();

    const [offreEmplois, setOffreEmploi] = useState([])
    const [admin, setAdmin] = useState([])
    const [recruteurs, setRecruteurs] = useState([])
    const [candidats, setCandidats] = useState([])
    const [details, setDetails] = useState([]);

    const [offreTaille, setOffreTaille] = useState("")
    const [adminTaille, setAdminTaille] = useState("")
    const [recruteursTaille, setRecruteursTaille] = useState("")
    const [candidatsTaille, setCandidatsTaille] = useState("")

    const toggle = () =>{
        setIsOpen(!isOpen)
    }

   


  const getLiens = async () => {
   
    await db.collection("jobsgabon")
      .onSnapshot((querySnapshot) => {
        var list=[]
          querySnapshot.forEach((doc) => {
            list.push({...doc.data(), id: doc.id })
          });
        setDetails(list);  
      });  
  } 

    

    const getOffre = async () => {
        await db.collection("offre_emploi")
                .onSnapshot((querySnapshot) => {
                    var listOffre = []
                    setOffreTaille(querySnapshot.size)
                    querySnapshot.forEach((doc) => {
                        listOffre.push({...doc.data(), id: doc.id })
                    });
                    setOffreEmploi(listOffre)              
                });
     }

     const getAdmin = async () => {
        await db.collection("administration")
                .onSnapshot((querySnapshot) => {
                    var listAdmin = []
                    setAdminTaille(querySnapshot.size)
                    querySnapshot.forEach((doc) => {
                        listAdmin.push({...doc.data(), id: doc.id })
                    });
                    setAdmin(listAdmin)              
                });
     }

     const getCandidats = async () => {
        await db.collection("candidats")
                .onSnapshot((querySnapshot) => {
                    var listCand = []
                    setCandidatsTaille(querySnapshot.size)
                    querySnapshot.forEach((doc) => {
                        listCand.push({...doc.data(), id: doc.id })
                    });
                    setCandidats(listCand)              
                });
     }


     const getRecruteurs = async () => {
        await db.collection("recruteurs")
                .onSnapshot((querySnapshot) => {
                    var listRecru = []
                    setRecruteursTaille(querySnapshot.size)
                    querySnapshot.forEach((doc) => {
                        listRecru.push({...doc.data(), id: doc.id })
                    });
                    setRecruteurs(listRecru)              
                });
     }


     useEffect(()=> {
        getLiens();
        getOffre();
        getAdmin();
        getCandidats();
        getRecruteurs();
    }, [])


    return (
        <>
           <ScrollToTop/>
            <SidebarAuth isOpen={isOpen}  toggle={toggle}/>
            <SecondNavbar  toggle={toggle}/>
            <Box mt={4} mb={4}>
               <Container maxWidth={false} >
                    <Grid lg={12} md={12} xs={12} >
                            <Card>
                                <CardHeader 
                                   title="À propos de nous"
                                   subheader="JOBSGABON"
                                />
                                <Divider/>
                                <CardContent>
                                {details.map((item,index) => (
                                    <Grid key={index} container spacing={3}>
                                    
                                        <Grid lg={6} md={6} xs={12}>
                                       
                                        
                                            <Box ml={2} mt={2} >
                                               <h3>Qui sommes nous ?</h3>
                                                <Typography align="justify" >
                                                   {item.description}
                                                </Typography>
                                               </Box>
                                               <Box ml={2} mt={2}  >
                                               <h3>Notre mission</h3>
                                                <Typography align="justify" >
                                                    {item.mission}
                                                </Typography>
                                             </Box>  
                                           
                                             
                                        </Grid>
                                       
                                        <Grid lg={6} md={6} xs={12}>
                                             <Box ml={3} mt={2} >
                                               <h3>Contacts</h3>
                                                  <Typography> Téléphone : {item.telephone} </Typography>
                                                  <Typography> Email : {item.email} </Typography>
                                                  <Typography> adresse :{item.adresse} </Typography>
                                             </Box>
                                               
                                        </Grid>   
                                    </Grid>  
                                    ))}
                                     <Box  mt={4} alignItems="center" >
                                         
                                     <Grid
                                            container
                                            spacing={3}
                                            alignItems='center'
                                            justify='center'
                                        >
                                            <Grid item lg={3} sm={6} xl={3} xs={12}>
                                                <CardRecruteur  taille={recruteursTaille} />
                                            </Grid>
                                            <Grid item lg={3} sm={6} xl={3} xs={12}>
                                                <CardOffre  taille={offreTaille}/>
                                            </Grid>
                                            <Grid item lg={3} sm={6} xl={3} xs={12}>
                                                <CardCandidats  taille={candidatsTaille} />
                                            </Grid>
                                        </Grid>
                                            
                                     </Box>                                  
                                </CardContent>
                            </Card>
                    </Grid>
              </Container>
            </Box>
            <Footer/>  
        </>
    )
}

export default Apropos
