import React, { Component } from 'react';
import {portfolioData} from '../data/portfolioData'
import Project from './Project';
import styled from 'styled-components'
import './ProjectList.css'

const PortfolioContent = styled.div`
    width: 100%;
    display: grid;
    min-height: 95vh;
    grid-template-columns: 1fr;
    grid-template-rows: 50px 1fr;
    grid-template-areas: "1" "2";
    margin-left: 10px;
    position:relative;
    margin-left:270px;

    @media screen and (max-width: 1125px){
        margin-left: 0px; 
        margin-left:0px;
    }
    
`
const RadioDisplayUl = styled.ul`
    grid-area: 1;
    display: flex;
    width: 100%;
    padding: 1rem 2rem;
    margin: 0 auto;
    background: #1a1359;
    user-select: none;
    box-shadow: 0 1px 3px #333;

    @media (max-width: 520px){
        padding: 1rem 1rem;
           
    }

      
`

const RadioDisplayLi = styled.li`
    color: #fff;
    font-size: 1.1rem;
    text-transform: uppercase;
    padding: 0 1rem;
    flex: 1;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;

    @media (max-width: 520px){
        font-size: 0.7rem;
        padding: 0 0.5rem;
           
    }
`

const ProjectsContent = styled.div`
    grid-area: 2;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    
    @media (max-width: 520px){
            width: 100%;
           
        }
`

class ProjectList extends Component {
    state= {
        projects:portfolioData,
        radios : [
            {id:1, value:"javascript"},
            {id:2, value:"css"},
            {id:3, value:"react"},
            {id:4, value:"php"},
        ],
        selectedRadio: 'javascript'
    };

    handleRadio = (event) =>{
        const radio =event.target.value;
        this.setState({selectedRadio:radio})
    }


    render() {
        const {projects,radios,selectedRadio}= this.state;
        return (    
            <PortfolioContent className="portfolioContent">
                <RadioDisplayUl  className="radioDisplay">
                   {
                       radios.map((radio)=>{
                           return(
                            <RadioDisplayLi key={radio.id}>
                                    <input type="radio"
                                     name="radio" 
                                     id={radio.value}
                                     checked={radio.value===selectedRadio}
                                     value={radio.value}
                                     onChange={this.handleRadio}
                                     />
                                     <label htmlFor={radio.value}>{radio.value}</label>
                            </RadioDisplayLi>
                           )
                       })
                   }
                </RadioDisplayUl>
                <ProjectsContent className="projects"> 
                    {
                        projects
                        .filter(item=> item.languages.includes(selectedRadio))
                        .map((item) =>{
                          return(
                            <Project
                              key={item.id}
                               item={item}
                            />
                          )
                        })
                    }
                </ProjectsContent>
            </PortfolioContent>
        );
    }
}

export default ProjectList;
