import React,{useState,useEffect,useContext}  from 'react'
import Sidebar from '../components/Sidebar/Index'
import {CopyToClipboard} from 'react-copy-to-clipboard'
import { ConatactBox, 
         ContactContent, 
         ContactH1, 
         ContactHeader, 
         ContactI, 
         ContactLI, 
         ContactSection,
         ContactSpan,
         ContactUl ,
         ContactSpanMobile,
         SocialNetWork,
         SocialUl,
         SocialLink,
         ContactImg
} from '../components/Contact/ContactElements'
import { FaEnvelope, FaMapMarkedAlt, FaMobileAlt } from 'react-icons/fa'
import Img from '../../assets/images/map.jpg'
import { IconContext } from 'react-icons/lib';
import ScrollToTop from '../../components/Signin/ScrollToTop'
import {withRouter} from 'react-router-dom'
import MobileSidebar from '../components/Sidebar/MobileSidebar'
import app from '../../firebase/base';
import { AuthContext } from '../../firebase/AuthContext';



const Contact = () => {

    const [isOpen, setIsOpen] = useState(false)
    const {currentUser} = useContext(AuthContext);
    const [candidatObjects, setCandidatObjects] = useState([])
    const db = app.firestore();

    const toggle = () =>{
        setIsOpen(!isOpen)
    }

    const getData = async () => {
        db.collection("candidats")
            .where("id_utilisateur", "==", currentUser.uid)
            .onSnapshot((querySnapshot) => {
                var list = [];
                querySnapshot.forEach((doc) => {
                    list.push({ ...doc.data(), id: doc.id })
                });
                setCandidatObjects(list)
                
            });
        }

        useEffect(()=> {
            getData();
        }, [getData]);


    return (
        <>
        <IconContext.Provider value={{  size: 25 }}>
        <ScrollToTop/>
         <ContactSection>
            <MobileSidebar  isOpen={isOpen}  toggle={toggle}/>
            <Sidebar toggle={toggle}/> 
             <ContactContent>
                 <ContactHeader>
                     <ContactImg src={Img} alt=""  />
                 </ContactHeader>
                
                { 
                   candidatObjects.map((item) => {
                    return( 
                    <ConatactBox>
                        <ContactH1>Contactez-moi</ContactH1>
                        <ContactUl>

                            <ContactLI>
                                <ContactI><FaMapMarkedAlt/> </ContactI>
                                <ContactSpan>{item.ville_candidat}</ContactSpan>
                            </ContactLI>

                            <ContactLI>
                                <ContactI><FaMobileAlt/></ContactI>
                                <CopyToClipboard text={item.telephone_candidat}>
                                    <ContactSpanMobile 
                                        onClick={()=>{ alert('Téléphone copié !');}} >
                                            {item.telephone_candidat}
                                    </ContactSpanMobile>
                                </CopyToClipboard>
                            </ContactLI>

                            <ContactLI>
                                <ContactI><FaEnvelope/></ContactI>
                                <CopyToClipboard text={item.email_candidat}>
                                    <ContactSpanMobile 
                                        onClick={()=>{ alert('Mail copié !');}} >
                                            {item.email_candidat}
                                    </ContactSpanMobile>
                                </CopyToClipboard>
                            </ContactLI>
                        </ContactUl>
                    </ConatactBox>
                    )  
                })
                }

                <SocialNetWork>
                    <SocialUl>
                        <SocialLink href="" rel="nooper noreferrer" target="_blank">
                            
                           
                        </SocialLink>

                        <SocialLink href="" rel="nooper noreferrer" target="_blank">
                           
                            
                        </SocialLink>

                        <SocialLink href="" rel="nooper noreferrer" target="_blank">
                           
                           
                        </SocialLink>

                        <SocialLink href="" rel="nooper noreferrer" target="_blank">
                            
                           
                        </SocialLink>
                    </SocialUl>
                </SocialNetWork>


             </ContactContent>
         </ContactSection>
         </IconContext.Provider>
          
        </>
    )
}

export default withRouter(Contact);
