import React, {useState,useEffect,useContext} from 'react'
import { Box,makeStyles, Grid, InputAdornment, Paper,TableCell,TableRow, Toolbar, TableBody } from '@material-ui/core'
import Controls from '../controls/Controls'
import { Search, Work } from '@material-ui/icons'
import AddIcon from '@material-ui/icons/Add';
import useTable from '../useTable';
import CloseIcon from '@material-ui/icons/Close';
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';
import Popup from '../Popup';
import Notification from '../Notification';
import ConfirmDialog from '../ConfirmDialog';
import './table.css'
import OffreForm from '../Forms/OffreForm';
import PageHeader from '../PageHeader';
import app from '../../../firebase/base';
import { AuthContext } from '../../../firebase/AuthContext';


const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '50%',
    },
    newButton: {
        
        right: '10px',
        color: '#1a1359',
        borderColor: '#1a1359',
        justifyContent:'center',
        alignItems: 'center',
        display:'flex-start'
    },
  
}))

const headCells = [
    { id: 'societe', label: 'Societe' },
    { id: 'titre_offre', label: 'Titre' },
    { id: 'description_offre', label: 'Description' },
    { id: 'responsabilite_offre', label: 'Responsabilité' },
    { id: 'responsable_recru', label: 'Responsable' },
    { id: 'fonction_offre', label: 'Fonction' },
    { id: 'type_contrat', label: 'Contrat' },
    { id: 'secteur_activite', label: 'Sect activité' },
    { id: 'niveau_etude', label: 'Nivéau' },
    { id: 'experience', label: 'Expérience' },
    { id: 'ville_offre', label: 'Ville' },
    { id: 'province_offre', label: 'Province' },
    { id: 'actions', label: 'Actions', disableSorting: true }
]

const TableOffreEmploi = (props) => {
     
    const classes = useStyles();

    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [records, setRecords] = useState([])
    const [openPopup, setOpenPopup] = useState(false)
    const [recordForEdit, setRecordForEdit] = useState(null)
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })

    const {currentUser} = useContext(AuthContext);
    const db = app.firestore();
    const listOffre = [];

    const d = new Date();
    const mois = d.getMonth();
    const jour = d.getDate();
    const heure = d.getHours();
    const annee = d.getUTCFullYear();
    const minute = d.getUTCMinutes();
    const seconde = d.getUTCSeconds();
    const year = d.getYear();
    const tabMois = ["janvier","fevrier", "mars","avril","mai","juin",
                      "juillet","aout","septembre","octobre","novembre","decembre"
                    ]

 
    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records,headCells,filterFn);

    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.titre_offre.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchContrat = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.type_contrat.toLowerCase().includes(target.value))
            }
        })
    }

    const handleSearchVille = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.ville_offre.toLowerCase().includes(target.value))
            }
        })
    }

  const handleSearchProvince = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.province_offre.toLowerCase().includes(target.value))
            }
        })
    }

     const handleSearchDate = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value === "")
                    return items;
                else
                    return items.filter(x => x.date_debut.toLowerCase().includes(target.value))
            }
        })
    }

    const getOffreEmploi = async () => {
        await db.collection("offre_emploi")
           .where("id_utilisateur", "==", currentUser.uid)
           .onSnapshot((querySnapshot) => {
               querySnapshot.forEach((doc) => {
                listOffre.push({...doc.data(), id: doc.id })
              
               });
               setRecords(listOffre)              
           });
      }
 
    const addOrEdit = async (obj,resetForm) => {
        try{
            if (obj.id == "")
                await db.collection("offre_emploi").doc().set({
                    id:obj.id,
                    societe:obj.societe,
                    titre_offre: obj.titre_offre,
                    id_utilisateur:obj.id_utilisateur,
                    description_offre: obj.description_offre,
                    responsable_recru: obj.responsable_recru,
                    fonction_offre: obj.fonction_offre,
                    secteur_activite:obj.secteur_activite,
                    niveau_etude:obj.niveau_etude,
                    experience:obj.experience,
                    type_contrat: obj.type_contrat, 
                    ville_offre: obj.ville_offre,
                    province_offre: obj.province_offre,
                    date_ajout:obj.date_ajout, 
                    date_fin: obj.date_fin, 
                    date_debut: obj.date_debut ,
                    mois:tabMois[mois],
                    annees:annee,
                });
            else
                await db.collection("offre_emploi").doc(obj.id).update(obj);
            resetForm()
            setRecordForEdit(null)
            setOpenPopup(false)
            getOffreEmploi()
            setNotify({
                    isOpen: true,
                    message: 'Validation avec Succes',
                    type: 'success'
            })
        }catch (error) {
            console.error(error);
          }
        
    }

    const openInPopup = item => {
        setRecordForEdit(item)
        setOpenPopup(true)
    }

    const onDelete = async  (id) => {
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        })
        await db.collection("offre_emploi").doc(id).delete();
        getOffreEmploi()
        setNotify({
            isOpen: true,
            message: 'Suppression avec Success',
            type: 'error'
        })
    }
  

    useEffect(()=> {
        getOffreEmploi();
    }, [])

    return (
        <Box p={2}>
                <PageHeader
                title="Gestion des offres d'emploi"
                subTitle="Poster une nouvelle offre d'emploi, modifier et supprimer"
                icon={<Work fontSize="large" />}
              
             
            />
               <Paper  >
                <Toolbar>
                    <Grid container spacing={2}  xs={12}>
                        <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par titre"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearch}  
                            />
                       </Grid>
                       <Grid item lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par contrat"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchContrat}  
                            />
                       </Grid>
                       
                      <Grid item  lg={4} md={6} xs={12}  >
                             <Controls.Button 
                                text="Ajouter"
                               
                                variant="outlined"
                                startIcon={<AddIcon/>}
                                onClick={() => { setOpenPopup(true); setRecordForEdit(null); }}
                                className={classes.centerButton}
                                
                            />
                        </Grid>
                        <Grid item  lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par ville"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchVille}  
                            />  
                       </Grid>
                       <Grid item  lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par province"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchProvince}  
                            />  
                       </Grid>
                      <Grid item  lg={4} md={6} xs={12}>
                        <Controls.Input
                                label="Rechercher par date_début"
                                className={classes.searchInput}
                                InputProps={{
                                    startAdornment: (
                                    <InputAdornment position="start">
                                        <Search /> 
                                    </InputAdornment>)
                                }}  
                                onChange={handleSearchDate}  
                            />  
                       </Grid>
                    </Grid>
                </Toolbar>
                  
                  <Grid className="container">
                    <TblContainer>
                        <TblHead/>
                        <TableBody>
                        {
                                recordsAfterPagingAndSorting().map((item,index) =>
                                    (<TableRow key={index}>
                                        <TableCell>{item.societe}</TableCell>
                                        <TableCell>{item.titre_offre}</TableCell>
                                        <TableCell>{item.description_offre.substring(0,10)}...</TableCell>
                                        <TableCell>{item.responsabilite_offre.substring(0,10)}...</TableCell>
                                        <TableCell>{item.responsable_recru}</TableCell>
                                        <TableCell>{item.fonction_offre}</TableCell>
                                        <TableCell>{item.type_contrat}</TableCell>
                                        <TableCell>{item.secteur_activite}</TableCell>
                                        <TableCell>{item.niveau_etude}</TableCell>
                                        <TableCell>{item.experience}</TableCell>
                                        <TableCell>{item.ville_offre}</TableCell>
                                        <TableCell>{item.province_offre}</TableCell>
                                        <TableCell>
                                            <Controls.ActionButton
                                                color="primary"
                                                onClick={() => { openInPopup(item) }}
                                              >
                                                <EditOutlinedIcon fontSize="small" /> 
                                            </Controls.ActionButton>
                                            <Controls.ActionButton
                                                color="secondary"
                                                onClick={() => {
                                                    setConfirmDialog({
                                                        isOpen: true,
                                                        title: 'êtes-vous sûr de vouloir supprimer cet enrégistrement?',
                                                        subTitle: "Vous pouvez annuler cette opération",
                                                        onConfirm: () => { onDelete(item.id) }
                                                    })
                                                }}
                                              >
                                                <CloseIcon fontSize="small" />
                                            </Controls.ActionButton>
                                        </TableCell>
                                    </TableRow>)
                                )
                            }
                        </TableBody>
                    </TblContainer>
                    <TblPagination />
                    </Grid>
             </Paper>
             <Popup
                  title="Formulaire d'offre d'emploi"
                  openPopup={openPopup}
                  setOpenPopup={setOpenPopup}
             >
                 <OffreForm
                   recordForEdit={recordForEdit}
                   addOrEdit={addOrEdit}
                 />
                  
             </Popup>
             <Notification
                notify={notify}
                setNotify={setNotify}
            />
             <ConfirmDialog
                confirmDialog={confirmDialog}
                setConfirmDialog={setConfirmDialog}
             />
        </Box>
    )
}

export default TableOffreEmploi
